import React, { useEffect } from 'react'
import { Field } from 'redux-form'
import {
  Editor,
  Input,
  InputDatePicker,
  RadioWrapper,
  Checkbox
} from 'Shared/components/FormElements'
import { t } from 'i18next'
import { lengthLimit } from 'Shared/constants/Editor'
import moment from 'moment'
import { BigJustIconButton } from 'Shared/components/Button'

function ExperienceFormFields (props) {
  useEffect(() => {
    if (props.currentWork) {
      props.onCurrentWorkChange()
    }
  }, [props.currentWork])

  return (
    <div className='form-group'>
      <Field
        component={Input}
        label={t('forms.companyName.label')}
        placeholder={t('forms.companyName.placeholder')}
        name={`${props.experience}.organisationName`}
        simpleValue
      />
      <Field
        component={Input}
        label={t('forms.position.label')}
        name={`${props.experience}.position`}
        type='text'
        placeholder={t('forms.position.placeholder')}
      />
      <div className='form__group'>
        <div className='columns'>
          <div className='column col-6 col-xs-12'>
            <Field
              component={InputDatePicker}
              label={t('forms.startDate.label')}
              name={`${props.experience}.startedAt`}
              icon={'ion-md-clock'}
              maxDate={moment().utc()}
              floatRight
              scrollableYearDropdown
              showYearDropdown
              showMonthDropdown
              placeholderText={t('forms.startDate.placeholder')}
              format={(value, name) => value ? moment(value, moment.ISO_8601) : null}
            />
          </div>
          <div className='column col-6 col-xs-12'>
            <Field
              component={InputDatePicker}
              label={t('forms.endDate.label')}
              name={`${props.experience}.finishedAt`}
              icon={'ion-md-clock'}
              maxDate={moment().utc()}
              floatRight
              scrollableYearDropdown
              showYearDropdown
              showMonthDropdown
              placeholderText={props.currentWork ? t('common.present') : t('forms.endDate.placeholder')}
              format={(value, name) => value ? moment(value, moment.ISO_8601) : null}
              disabled={props.currentWork}
            />
          </div>
        </div>
        <RadioWrapper>
          <Field
            component={Checkbox}
            text={t('profile.experience.sections.workExperience.iCurrentlyWorkHere')}
            name={`${props.experience}.currentWork`}
            normalize={v => !!v}
          />
        </RadioWrapper>
      </div>
      <div className='form__group'>
        <Field
          component={Editor}
          label={t('common.description')}
          name={`${props.experience}.description`}
          limit={lengthLimit.description}
          placeholder={t('profile.experience.sections.workExperience.description')}
        />
        <Field
          component={Input}
          name={`${props.experience}.experienceId`}
          type='hidden'
        />
        <Field
          component={Input}
          name={`${props.experience}.candidateId`}
          type='hidden'
        />
        <Field
          component={Input}
          name={`${props.experience}.type`}
          type='hidden'
        />
      </div>
      <div className='form__wrapper--button-group'>
        <BigJustIconButton onClick={props.removeField}><i className='ion-md-trash' /></BigJustIconButton>
      </div>
    </div>
  )
}

export default ExperienceFormFields



