import React, { Fragment } from 'react'
import { Helmet } from 'react-helmet'
import { cleanseString, withPossessiveS } from 'Shared/helpers/string'
import Container from 'Shared/components/Container'
import withTranslations from 'Shared/hoc/withTranslations'
import { ProfilePreviewNavigationIds } from 'Candidate/constants/ProfilePreviewNavigation'
import ProfilePreviewHeader from 'Candidate/components/ProfilePreviewHeader'
import ProfilePreviewAboutConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewAboutConnector'
import ProfilePreviewGeneralInfoConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewGeneralInfoConnector'
import ProfilePreviewEducationConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewEducationConnector'
import ProfilePreviewExperienceConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewExperienceConnector'
import ProfilePreviewCertificatesConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewCertificatesConnector'
import ProfilePreviewVolunteeringConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewVolunteeringConnector'
import ProfilePreviewSkillsConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewSkillsConnector'
import ProfilePreviewLanguagesConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewLanguagesConnector'
import ProfilePreviewVideoCvConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewVideoCvConnector'
import ProfilePreviewCvDocumentsConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewCvDocumentsConnector'
import ProfilePreviewAdditionalDocumentsConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewAdditionalDocumentsConnector'
import ProfilePreviewInterestsConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewInterestsConnector'
import ProfilePreviewWebsitesConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewWebsitesConnector'
import ReassessmentNotificationConnector from 'Candidate/connectors/ReassessmentNotification/ReassessmentNotificationConnector'
import ProfileRatingConnector from 'Candidate/connectors/CandidateProfile/ProfileRatingConnector'
import ProfilePreviewNavigationConnector from 'Candidate/connectors/ProfilePreviewNavigation/ProfilePreviewNavigationConnector'
import { Element } from 'react-scroll'

import './CandidateProfileView.scss'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'

const CandidateProfileView = ({ t, firstName }) => (
  <Fragment>
    <Helmet>
      <title>{firstName && cleanseString(t('meta.candidate.title.profileView', { firstName: withPossessiveS(firstName) }))}</title>
      <meta property='og:title' content={firstName && cleanseString(t('meta.candidate.title.profileView', { firstName: withPossessiveS(firstName) }))} />
    </Helmet>
    <section className='candidate-profile-view__container parsing--hidden'>
      <Container>
        <ProfilePreviewHeader />
      </Container>
      <ProfilePreviewNavigationConnector />
      <Container>
        <ReassessmentNotificationConnector />
        <ProfileRatingConnector />
        <Element name={ProfilePreviewNavigationIds.about}>
          <ProfilePreviewSection isWrapped header={t('common.about')}>
            <ProfilePreviewAboutConnector />
            <ProfilePreviewGeneralInfoConnector />
          </ProfilePreviewSection>
        </Element>
        <Element name={ProfilePreviewNavigationIds.resume}>
          <ProfilePreviewCvDocumentsConnector />
        </Element>
        <Element name={ProfilePreviewNavigationIds.education}>
          <ProfilePreviewEducationConnector />
          <ProfilePreviewCertificatesConnector />
        </Element>
        <Element name={ProfilePreviewNavigationIds.experience}>
          <ProfilePreviewExperienceConnector />
          <ProfilePreviewVolunteeringConnector />
          <ProfilePreviewSkillsConnector />
        </Element>
        <Element name={ProfilePreviewNavigationIds.languages}>
          <ProfilePreviewLanguagesConnector />
        </Element>
        <Element name={ProfilePreviewNavigationIds.videoCv}>
          <ProfilePreviewVideoCvConnector />
          <ProfilePreviewAdditionalDocumentsConnector />
        </Element>
        <Element name={ProfilePreviewNavigationIds.interests}>
          <ProfilePreviewInterestsConnector />
        </Element>
        <Element name={ProfilePreviewNavigationIds.social}>
          <ProfilePreviewWebsitesConnector />
        </Element>
      </Container>
    </section>
  </Fragment>
)

export default withTranslations(CandidateProfileView)



