import { formValueSelector, reduxForm, change } from 'redux-form'
import { connect } from 'react-redux'
import validate from 'Candidate/connectors/Forms/EditProfileSections/IntroductionSection/IntroductionSectionFormValidator'
import IntroductionSectionForm from 'Candidate/forms/EditProfileSections/IntroductionSection/IntroductionSectionForm'
import { fetchCountriesIfNeeded } from 'Shared/ducks/resources'
import { countriesOptions } from 'Shared/selectors/resources'
import { userPhone, userFirstName, userLastName, userCountry, userCity } from 'Shared/selectors/user'
import { updateCandidate } from 'Shared/ducks/user/candidate'

export const formName = 'IntroductionSectionForm'

let Form = reduxForm({
  form: formName,
  validate,
  enableReinitialize: true,
  onSubmit: (values, dispatch) => dispatch(updateCandidate(values, true))
})(IntroductionSectionForm)

const mapStateToProps = state => ({
  countriesOptions: countriesOptions(state),
  selectedCountry: formValueSelector(formName)(state, 'countryOfResidence'),
  initialValues: {
    firstName: userFirstName(state),
    lastName: userLastName(state),
    countryOfResidence: userCountry(state),
    city: userCity(state),
    phoneNumber: userPhone(state)
  }
})

const mapDispatchToProps = dispatch => ({
  fetchCountries: () => dispatch(fetchCountriesIfNeeded()),
  resetCity: () => dispatch(change(formName, 'city', null))
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



