import startCase from 'lodash/startCase'

export default values => {
  const errors = {}
  const certificatesErrors = []

  const requiredCertificateFields = ['name', 'issuer', 'issuedAt']

  if (values.certificates) {
    values.certificates.forEach((certificate, index) => {
      const certificateErrors = {}

      requiredCertificateFields.forEach(field => {
        if (!certificate[field]) {
          certificateErrors[field] = {
            key: 'errors.requiredField',
            options: {
              field: startCase(field)
            }
          }
          certificatesErrors[index] = certificateErrors
        }
      })
    })
  }

  if (certificatesErrors.length) {
    errors.certificates = certificatesErrors
  }

  return errors
}



