import React from 'react'
import ProfilePreviewExperienceElementMobile from './ProfilePreviewExperienceElement.mobile'
import ProfilePreviewExperienceElementWeb from './ProfilePreviewExperienceElement'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewExperienceElement = props => props.isMobile ? <ProfilePreviewExperienceElementMobile {...props} /> : <ProfilePreviewExperienceElementWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewExperienceElement)



