import { validateUrlWithProtocol, validateProtocol } from 'Shared/helpers/string'

export default values => {
  const errors = {}

  const websitesErrors = []

  const socialLinksFields = ['linkedin', 'instagram', 'twitter']

  socialLinksFields.forEach(field => {
    if (values[field] && !validateProtocol(values[field]) && validateUrlWithProtocol(values[field])) {
      errors[field] = {
        key: 'errors.invalidLink',
        options: {}
      }
    }
  })

  values.websites.forEach((website, index) => {
    const websiteErrors = {}

    if (!website.type) {
      websiteErrors.type = {
        key: 'errors.requiredField',
        options: {
          field: 'Type'
        }
      }
      websitesErrors[index] = websiteErrors
    }

    if (!website.url || website.url === '') {
      websiteErrors.url = {
        key: 'errors.requiredField',
        options: {
          field: 'Url'
        }
      }
      websitesErrors[index] = websiteErrors
    } else if (website.url && validateProtocol(website.url)) {
      websiteErrors.url = {
        key: 'errors.invalidLinkNoHttp',
        options: {}
      }
      websitesErrors[index] = websiteErrors
    } else if (website.url && validateUrlWithProtocol(website.url)) {
      websiteErrors.url = {
        url: {key: 'errors.invalidLink', options: {}}
      }
      websitesErrors[index] = websiteErrors
    }
  })

  if (websitesErrors.length) {
    errors.websites = websitesErrors
  }

  return errors
}



