import React from 'react'
import ProfilePreviewInterestsMobile from './ProfilePreviewInterests.mobile'
import ProfilePreviewInterestsWeb from './ProfilePreviewInterests'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewInterests = props => props.isMobile ? <ProfilePreviewInterestsMobile {...props} /> : <ProfilePreviewInterestsWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewInterests)



