import React, { useState, useEffect } from 'react'
import { t } from 'i18next'
import Label from 'Shared/components/FormElements/Label'
import { downloadFile } from 'Shared/helpers/files'
import { BigJustIconButton } from 'Shared/components/Button'
import UploadedCvFileFormConnector from 'Candidate/connectors/UploadedFileFormConnector/UploadedCvFileFormConnector'
import Modal from 'Shared/components/Modal'
import { Trans } from 'react-i18next'
import DeleteModal from 'Shared/components/DeleteModal'
import { UPLOAD_DOCUMENT_TYPES } from 'Shared/constants/Files'
import './ProfilePreviewCvDocumentsElement.scss'
import { StaticRoutes } from 'Shared/constants/Routes'

function ProfilePreviewCvDocumentsElement (props) {
  const [editModeVisibility, setEditModeVisibility] = useState(false)
  const [deleteAttachedCVModalVisibility, setDeleteAttachedCVModalVisibility] = useState(false)
  const [deleteCVModalVisibility, setDeleteCVModalVisibility] = useState(false)

  useEffect(() => {
    props.fetchCandidateAttachedCvs()
  }, [])

  const openEditMode = () => setEditModeVisibility(true)
  const hideEditMode = () => setEditModeVisibility(false)
  const handleDownload = () => downloadFile(props.document.userId, props.document.originalName)

  const isCvFileAttached = () => props.document.documentType === UPLOAD_DOCUMENT_TYPES.CV && props.candidateAttachedCvs.indexOf(props.document.id) >= 0

  const openDeleteAttachedCVModal = () => setDeleteAttachedCVModalVisibility(true)
  const hideDeleteAttachedCVModal = () => setDeleteAttachedCVModalVisibility(false)

  const openDeleteCVModal = () => setDeleteCVModalVisibility(true)
  const hideDeleteCVModal = () => setDeleteCVModalVisibility(false)

  const handleDeleteCv = () => {
    props.deleteFile(props.document.id, props.document.title)
    hideDeleteCVModal()
  }

  const onDelete = () => isCvFileAttached() ? openDeleteAttachedCVModal() : openDeleteCVModal()

  return editModeVisibility ? (
    <div className='profile-preview-cv-documents__document-element-edit-mode'>
      <UploadedCvFileFormConnector defaultEditMode afterSubmit={hideEditMode} file={props.document} onDelete={onDelete} />
      {
        isCvFileAttached() ? (
          <Modal
            title={t('modals.cvLinkedToApplicationModal.title')}
            handleSubmit={hideDeleteAttachedCVModal}
            handleClose={hideDeleteAttachedCVModal}
            show={deleteAttachedCVModalVisibility}
            submitText={t('common.okGotIt')}
            showContact={false}
            showCancel={false}
          >
            <Trans i18nKey='modals.cvLinkedToApplicationModal.description'>
              You have attached this CV to a job application on Oliv. As the application has already
              been sent to the employer, you will not be able to delete it. You can still upload a new
              CV to your profile and attach it to future job applications. If you have any questions,
              please <a href={StaticRoutes.contact}>contact</a> us. We'll be happy to help.
            </Trans>
          </Modal>
        ) : (
          <DeleteModal
            title={t('remove.file.title', {name: props.document.title})}
            content={t('remove.file.description', {name: props.document.title})}
            handleDelete={handleDeleteCv}
            isModalVisible={deleteCVModalVisibility}
            hideDeleteModal={hideDeleteCVModal}
            submitText={isCvFileAttached() && t('remove.cvAttached.title')}
          />
        )
      }
    </div>
  ) : (
    <div className='profile-preview-cv-documents__document-element'>
      <div className='profile-preview-cv-documents__document-details'>
        <Label>{t(`documentTypes.${props.document.documentType}`)}{props.document.isDefaultCv && ` - ${t('common.default')}`}</Label>
        <div className='profile-preview-cv-documents__document-title'>{props.document.title}</div>
      </div>
      <div className='profile-preview-cv-documents__document-buttons'>
        <BigJustIconButton onClick={handleDownload}><i className='ion-md-download' /></BigJustIconButton>
        <BigJustIconButton onClick={openEditMode}><i className='icon-im-edit' /></BigJustIconButton>
      </div>
    </div>
  )
}

export default ProfilePreviewCvDocumentsElement



