import React from 'react'
import ProfilePreviewSectionWeb from './ProfilePreviewSection'
import ProfilePreviewSectionMobile from './ProfilePreviewSection.mobile'
import withDeviceWithDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewSection = props => props.isMobile ? <ProfilePreviewSectionMobile {...props} /> : <ProfilePreviewSectionWeb {...props} />

export default withDeviceWithDetector(ProfilePreviewSection)



