import React from 'react'
import ProfilePreviewAdditionalDocumentsElementMobile from './ProfilePreviewAdditionalDocumentsElement.mobile'
import ProfilePreviewAdditionalDocumentsElementWeb from './ProfilePreviewAdditionalDocumentsElement'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewAdditionalDocumentsElement = props => props.isMobile ? <ProfilePreviewAdditionalDocumentsElementMobile {...props} /> : <ProfilePreviewAdditionalDocumentsElementWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewAdditionalDocumentsElement)



