import React from 'react'
import { Field } from 'redux-form'
import { Input, Select } from 'Shared/components/FormElements'
import UniversityAlgoliaField from 'Candidate/components/UniversityAlgoliaField'
import { BigJustIconButton } from 'Shared/components/Button'
import { t } from 'i18next'

function UniversityFormFields (props) {
  const handleGradeSystemChange = () => {
    props.resetGradeFormat()
    props.resetGradeValue()
  }

  const handleGradeFormatChange = () => {
    props.resetGradeValue()
  }

  return (
    <div className='form-group'>
      <UniversityAlgoliaField
        onSelect={props.chooseUniversity}
        name={`${props.university}.universityId`}
        defaultRefinement={props.universityName}
      />
      <Field
        component={Select}
        options={props.degrees}
        optional={t('common.optional')}
        label={t('forms.degree.label')}
        placeholder={t('forms.degree.placeholder')}
        name={`${props.university}.degree`}
        withIcon
      />
      <Field
        component={Select}
        options={props.majors}
        label={t('forms.major.label')}
        placeholder={t('forms.major.placeholder')}
        name={`${props.university}.majorId`}
      />
      <Field
        component={Input}
        label={t('forms.specialisation.label')}
        placeholder={t('forms.specialisation.placeholder')}
        name={`${props.university}.specialisation`}
        optional={t('common.optional')}
        type='text'
      />
      <div className='columns form__wrapper--inline'>
        <div className='column col-8 col-xs-12'>
          <Field
            component={Select}
            options={props.graduationMonths}
            label={t('forms.graduationMonth.label')}
            placeholder={t('forms.graduationMonth.placeholder')}
            name={`${props.university}.graduationMonth`}
            withIcon
          />
        </div>
        <div className='column col-4 col-xs-12'>
          <Field
            component={Select}
            options={props.graduationYears}
            label={t('forms.graduationYear.label')}
            placeholder={t('forms.graduationYear.placeholder')}
            name={`${props.university}.graduationYear`}
            withIcon
          />
        </div>
      </div>
      <Field
        component={Select}
        options={props.gradeSystemOptions}
        onChangeCallback={handleGradeSystemChange}
        label={t('forms.gradingSystem.label')}
        placeholder={t('forms.gradingSystem.placeholder')}
        name={`${props.university}.gradeSystem`}
        withIcon
      />
      <div className='columns form__wrapper--inline'>
        <div className='column col-6 col-xs-12'>
          <Field
            component={Select}
            options={props.gradeFormatOptions}
            onChangeCallback={handleGradeFormatChange}
            label={t('forms.gradeFormat.label')}
            placeholder={t('forms.gradeFormat.placeholder')}
            name={`${props.university}.gradeFormat`}
            withIcon
          />
        </div>
        <div className='column col-6 col-xs-12'>
          <Field
            component={Select}
            options={props.gradeValueOptions}
            label={t('forms.gradeValue.label')}
            placeholder={t('forms.gradeValue.placeholder')}
            name={`${props.university}.gradeValue`}
            withIcon
          />
        </div>
        <Field
          component={Input}
          name={`${props.university}.id`}
          type='hidden'
        />
      </div>
      <div className='form__wrapper--button-group'>
        <BigJustIconButton onClick={props.removeField}><i className='ion-md-trash' /></BigJustIconButton>
      </div>
    </div>
  )
}

export default UniversityFormFields



