import React, { useEffect, useState } from 'react'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import AddCertificatesFormConnector from 'Candidate/connectors/Forms/EditProfileSections/CertificatesSection/AddCertificatesFormConnector'
import ProfilePreviewCertificatesListConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewCertificatesListConnector'
import Modal from 'Shared/components/Modal'
import './ProfilePreviewCertificates.scss'
import { t } from 'i18next'
import NotificationService from 'Shared/services/NotificationService'

function ProfilePreviewCertificates (props) {
  const [addModalVisibility, setAddModalVisibility] = useState(false)

  useEffect(() => {
    if (props.hasAddNewCertificateSucceeded) {
      hideAddModal()
      NotificationService.success(t('alerts.updateCertificatesSuccess'))
    }
  }, [props.hasAddNewCertificateSucceeded])

  const showAddModal = () => {
    setAddModalVisibility(true)
  }

  const hideAddModal = () => setAddModalVisibility(false)

  return (
    <ProfilePreviewSection isWrapped header={t('profile.education.sections.additionalCertifications.label')}>
      <ProfilePreviewCertificatesListConnector />
      <div>
        <AddNewSectionButton
          text={t('candidate.profile.certificates.addCertificate')}
          onClick={showAddModal}
        />
        <Modal
          title={t('modals.addCertificate.title')}
          show={addModalVisibility}
          handleClose={hideAddModal}
          handleCancel={hideAddModal}
          handleSubmit={props.submitAddCertificates}
          cancelText={t('common.cancel')}
          submitText={t('common.submit')}
          showContact={false}
          closeOnSubmit={false}
          showCancel
        >
          <AddCertificatesFormConnector />
        </Modal>
      </div>
    </ProfilePreviewSection>
  )
}

export default ProfilePreviewCertificates



