import React from 'react'
import './ProfilePreviewCvDocumentsList.scss'
import ProfilePreviewCvDocumentsElementConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewCvDocumentsElementConnector'
import * as R from 'ramda'

const renderCvDocumentElement = document => <ProfilePreviewCvDocumentsElementConnector id={document.id} key={document.id} />

const CvDocumentsList = (props) => R.map(
  renderCvDocumentElement
)(props.cvDocuments)

function ProfilePreviewCvDocumentsList (props) {
  return (
    <div>
      <CvDocumentsList cvDocuments={props.cvDocuments} />
    </div>
  )
}

export default ProfilePreviewCvDocumentsList



