import { connect } from 'react-redux'
import LanguageFormFields from 'Candidate/forms/EditProfileSections/LanguagesSection/LanguageFormFields'
import { getLanguagesOptions } from 'Shared/selectors/resources'
import { formValueSelector } from 'redux-form'

const mapStateToProps = (state, ownProps) => ({
  languageOptions: getLanguagesOptions(state, ownProps.index, formValueSelector(ownProps.formName)(state, 'languages'))
})

export default connect(mapStateToProps)(LanguageFormFields)



