import { connect } from 'react-redux'
import ProfilePreviewExperienceList from 'Candidate/components/ProfilePreviewSections/ProfilePreviewExperience/components/ProfilePreviewExperienceList'
import { getUserExperience } from 'Shared/selectors/experience'

const mapStateToProps = state => ({
  experienceList: getUserExperience(state)
})

export default connect(mapStateToProps)(ProfilePreviewExperienceList)



