import React, { useEffect, useState } from 'react'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import ProfilePreviewLanguagesListConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewLanguagesListConnector'
import { t } from 'i18next'
import './ProfilePreviewLanguages.scss'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import Modal from 'Shared/components/Modal'
import AddLanguagesFormConnector from 'Candidate/connectors/Forms/EditProfileSections/LanguagesSection/AddLanguagesFormConnector'
import NotificationService from 'Shared/services/NotificationService'

function ProfilePreviewLanguages (props) {
  const [addModalVisibility, setAddModalVisibility] = useState(false)

  useEffect(() => {
    props.fetchLanguagesIfNeeded()
  }, [])

  useEffect(() => {
    if (props.hasAddNewLanguageSucceeded) {
      hideAddModal()
      NotificationService.success(t('alerts.updateLanguagesSuccess'))
    }
  }, [props.hasAddNewLanguageSucceeded])

  const showAddModal = () => setAddModalVisibility(true)
  const hideAddModal = () => setAddModalVisibility(false)

  return (
    <ProfilePreviewSection
      isWrapped
      header={t('profile.additional.sections.languages.label')}
    >
      <ProfilePreviewLanguagesListConnector />
      <div className='profile-preview-language'>
        <AddNewSectionButton
          text={t('candidate.profile.language.addLanguage')}
          onClick={showAddModal}
        />
        <Modal
          title={t('modals.addLanguage.title')}
          show={addModalVisibility}
          handleClose={hideAddModal}
          handleCancel={hideAddModal}
          handleSubmit={props.submitAddLanguages}
          cancelText={t('common.cancel')}
          submitText={t('common.submit')}
          showContact={false}
          closeOnSubmit={false}
          className='profile-preview-language__modal'
          showCancel
        >
          <AddLanguagesFormConnector />
        </Modal>
      </div>
    </ProfilePreviewSection>
  )
}

export default ProfilePreviewLanguages



