import React, { useState, useEffect, Fragment } from 'react'
import { BigJustIconButton } from 'Shared/components/Button'
import Label from 'Shared/components/FormElements/Label'
import Modal from 'Shared/components/Modal'
import { t } from 'i18next'
import { showYearsOld } from 'Shared/helpers/string'
import GeneralInfoFormConnector from 'Candidate/connectors/Forms/EditProfileSections/GeneralInfoSection/GeneralInfoFormConnector'
import './ProfilePreviewGeneralInfo.scss'
import NotificationService from 'Shared/services/NotificationService'
import cx from 'classnames'
import { isNotNaN, isNotNilOrEmpty } from 'Shared/helpers/Ramda'

function ProfilePreviewGeneralInfo (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  useEffect(() => {
    props.fetchCountries()
    if (props.editGeneralInfoSucceeded) {
      hideEditModal()
      NotificationService.success(t('alerts.updateGeneralInfoSuccess'))
    }
  })

  const ageCellClass = cx({
    'profile-preview-general-info__cell-text': true,
    'profile-preview-general-info__cell-text--lowercase': isNotNilOrEmpty(props.userAge) && isNotNaN(props.userAge)
  })

  return (
    <div className='profile-preview-general-info__container'>
      <div className='profile-preview-general-info'>
        <div className='profile-preview-general-info__container'>
          <div className='columns'>
            <div className='column col-4'>
              <div className='profile-preview-general-info__cell'>
                <Label>{t('forms.visaStatus.label')}</Label>
                <div className='profile-preview-general-info__cell-text'>{props.userVisa}</div>
              </div>
            </div>
            <div className='column col-4'>
              <div className='profile-preview-general-info__cell'>
                <Label>{t('common.drivingLicence')}</Label>
                <div className='profile-preview-general-info__cell-text'>{props.userDrivingLicence}</div>
              </div>
            </div>
            <div className='column col-4'>
              <div className='profile-preview-general-info__cell'>
                <Label>{t('common.carCapacity')}</Label>
                <div className='profile-preview-general-info__cell-text'>{props.userCarCapacity}</div>
              </div>
            </div>
          </div>
          {
            !props.isSensitiveMarket && (
              <Fragment>
                <div className='profile-preview-general-info__row'>
                  <div className='columns'>
                    <div className='column col-4'>
                      <div className='profile-preview-general-info__cell'>
                        <Label>{t('forms.gender.label')}</Label>
                        <div className='profile-preview-general-info__cell-text'>{props.userGender}</div>
                      </div>
                    </div>
                    <div className='column col-4'>
                      <div className='profile-preview-general-info__cell'>
                        <Label>{t('common.age')}</Label>
                        <div className={ageCellClass}>{showYearsOld(props.userAge)}</div>
                      </div>
                    </div>
                    <div className='column col-4'>
                      <div className='profile-preview-general-info__cell'>
                        <Label>{t('forms.nationality.label')}</Label>
                        <div className='profile-preview-general-info__cell-text'>{props.userNationality}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </Fragment>
            )
          }
        </div>
        <BigJustIconButton className='profile-preview-about__edit-button' onClick={showEditModal}><i className='icon-im-edit' /></BigJustIconButton>
      </div>
      <Modal
        title={t('modals.editGeneralInfo.title')}
        show={editModalVisibility}
        handleSubmit={props.submitEditGeneralInfo}
        handleClose={hideEditModal}
        handleCancel={hideEditModal}
        cancelText={t('common.cancel')}
        submitText={t('common.submit')}
        showContact={false}
        closeOnSubmit={false}
        showCancel
      >
        <GeneralInfoFormConnector />
      </Modal>
    </div>
  )
}

export default ProfilePreviewGeneralInfo



