import { connect } from 'react-redux'
import ProfilePreviewEducationHighSchools from 'Candidate/components/ProfilePreviewSections/ProfilePreviewEducation/components/ProfilePreviewEducationHighSchools'
import { userHighSchools } from 'Shared/selectors/highSchoolLevelEducation'

const mapStateToProps = state => ({
  educationList: userHighSchools(state)
})

export default connect(mapStateToProps)(ProfilePreviewEducationHighSchools)



