import React from 'react'
import ProfilePreviewHeaderWeb from './ProfilePreviewHeader'
import ProfilePreviewHeaderMobile from './ProfilePreviewHeader.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewHeader = props => props.isMobile ? <ProfilePreviewHeaderMobile {...props} /> : <ProfilePreviewHeaderWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewHeader)



