import { connect } from 'react-redux'
import { formValueSelector } from 'redux-form'
import { isFieldInvalid } from 'Shared/selectors/reduxForm'
import ExperienceFormFieldArray from 'Candidate/forms/EditProfileSections/ExperienceSection/ExperienceFormFieldArray'
import { t } from 'i18next'
import { getUserId } from 'Shared/selectors/user'

const mapStateToProps = (state, ownProps) => ({
  userId: getUserId(state),
  getOrganisationName: experienceField => formValueSelector(ownProps.formName)(state, `${experienceField}.organisationName`) || t('profile.experience.sections.workExperience.addNew'),
  isFieldArrayInvalid: experienceField => isFieldInvalid(ownProps.formName, experienceField)(state)
})

export default connect(mapStateToProps)(ExperienceFormFieldArray)



