import React from 'react'
import ProfilePreviewIntroductionWeb from './ProfilePreviewIntroduction'
import ProfilePreviewIntroductionMobile from './ProfilePreviewIntroduction.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewIntroduction = props => props.isMobile ? <ProfilePreviewIntroductionMobile {...props} /> : <ProfilePreviewIntroductionWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewIntroduction)



