import React, { useState, useEffect } from 'react'
import { t } from 'i18next'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton/index'
import Collapse, { Panel } from 'rc-collapse'
import cx from 'classnames'
import UniversityFormFieldsConnector from 'Candidate/connectors/Forms/EditProfileSections/EducationSection/UniversityFormFieldsConnector'
import 'Shared/assets/scss/vendor/rc-collapse-form.scss'
import { hasEmptyFieldArray } from 'Shared/helpers/forms'

function EducationFormUniversitiesFieldArray (props) {
  const [accordionActiveKey, setAccordionActiveKey] = useState(null)

  const addNewField = () => {
    props.fields.push({
      id: null,
      universityId: null,
      degree: null,
      majorId: null,
      specialisation: null,
      graduationMonth: null,
      graduationYear: null,
      gradeSystem: null,
      gradeFormat: null,
      gradeValue: null
    })
  }

  useEffect(() => {
    hasEmptyFieldArray(props) && addNewField()
  }, [])

  useEffect(() => {
    if (props.fields.length > 0) {
      setAccordionActiveKey(props.fields.length.toString())
    }
  }, [props.fields.length])

  const removeField = index => () => {
    const { fields, onDelete } = props
    const itemId = fields.get(index).id
    return itemId ? onDelete(itemId) : fields.remove(index)
  }

  const EducationFormUniversitiesField = (university, index) => {
    const PanelClass = cx({
      'form-fields__collapse-panel-error': props.isFieldArrayInvalid(university)
    })

    return (
      <Panel className={PanelClass} key={index + 1} header={props.getUniversityName(props.getUniversityId(university))}>
        <UniversityFormFieldsConnector
          removeField={removeField(index)}
          formName={props.formName}
          university={university}
        />
      </Panel>
    )
  }

  const getFields = () => props.fields.map(EducationFormUniversitiesField)

  return (
    <div>
      <Collapse
        accordion
        className='form-fields__collapse'
        activeKey={accordionActiveKey}
        onChange={setAccordionActiveKey}
      >
        {getFields()}
      </Collapse>
      <AddNewSectionButton
        text={t('profile.education.sections.educationDetails.addNew')}
        onClick={addNewField}
      />
    </div>
  )
}

export default EducationFormUniversitiesFieldArray



