import React from 'react'
import ProfilePreviewPhotoWeb from './ProfilePreviewPhoto'
import ProfilePreviewPhotoMobile from './ProfilePreviewPhoto.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewPhoto = (props) => props.isMobile ? <ProfilePreviewPhotoMobile {...props} /> : <ProfilePreviewPhotoWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewPhoto)



