import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { linkTypes } from 'Shared/constants/Links'
import AddWebsitesForm from 'Candidate/forms/EditProfileSections/WebsitesSection/AddWebsitesForm'
import validate from './AddWebsitesFormValidator'
import {
  deleteCandidateLink,
  updateCandidateLinks
} from 'Shared/ducks/links'
import { getCandidateLinkType } from 'Shared/selectors/links'

export const formName = 'AddWebsitesForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmit: (values, dispatch) => dispatch(updateCandidateLinks(values))
})(AddWebsitesForm)

const mapStateToProps = state => {
  const candidateId = state.user.details.id
  const linkedin = getCandidateLinkType(candidateId, linkTypes.linkedin)(state)
  const instagram = getCandidateLinkType(candidateId, linkTypes.instagram)(state)
  const twitter = getCandidateLinkType(candidateId, linkTypes.twitter)(state)

  return ({
    initialValues: {
      websites: [],
      linkedin: linkedin.url,
      linkedinId: linkedin.id,
      instagram: instagram.url,
      instagramId: instagram.id,
      twitter: twitter.url,
      twitterId: twitter.id
    }
  })
}

const mapDispatchToProps = dispatch => ({
  deleteLink: (linkId) => dispatch(deleteCandidateLink(linkId))
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



