import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import ProfilePreviewInterests from 'Candidate/components/ProfilePreviewSections/ProfilePreviewInterests'
import { getCandidateInterestsList } from 'Shared/selectors/interests'
import { formName } from 'Candidate/connectors/Forms/Profile/Experience/InterestsFormConnector'

const mapStateToProps = state => ({
  interests: getCandidateInterestsList()(state),
  editInterestsSucceeded: hasSubmitSucceeded(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  submitEditInterests: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewInterests)



