import React from 'react'
import ProfilePreviewAdditionalDocumentsMobile from './ProfilePreviewAdditionalDocuments.mobile'
import ProfilePreviewAdditionalDocumentsWeb from './ProfilePreviewAdditionalDocuments'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewAdditionalDocuments = props => props.isMobile ? <ProfilePreviewAdditionalDocumentsMobile {...props} /> : <ProfilePreviewAdditionalDocumentsWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewAdditionalDocuments)



