import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import {
  FacebookShareButton,
  TwitterShareButton,
  LinkedinShareButton,
  WhatsappShareButton,
  EmailShareButton,
  FacebookIcon,
  TwitterIcon,
  LinkedinIcon,
  WhatsappIcon
} from 'react-share'
import Icon from 'Shared/components/Icon'
import { THEME_COLORS } from 'Shared/constants/UI'
import withTranslations from 'Shared/hoc/withTranslations'
import ShareButton from 'Shared/components/ShareDropdown/components/ShareButton'
import DropdownMenu from 'Shared/components/DropdownMenu'
import { isBrowser } from 'Shared/helpers/mobile'
import { t } from 'i18next'
import cx from 'classnames'

import './ShareDropdown.scss'
import NotificationService from 'Shared/services/NotificationService'

export const ShareMediaList = (props) => {
  const shareUrl = (medium) => {
    return `${window.location.origin}${props.shareUrl}?utm_source=${medium}&utm_medium=shared`
  }

  const copyNotification = () => NotificationService.success(t('alerts.copied'))

  return (
    <div className='share-dropdown__social-media-items'>
      <FacebookShareButton
        className='share-dropdown__social-media-item'
        url={shareUrl('Facebook')}
        quote={`${props.title} via #OlivHQ`}
      >
        <FacebookIcon
          round
          size={32}
        />
        <div className='share-icons__description'>{t('common.shareViaFacebook')}</div>
      </FacebookShareButton>
      <TwitterShareButton
        className='share-dropdown__social-media-item'
        url={shareUrl('Twitter')}
        title={`${props.title} via @OlivHQ`}
      >
        <TwitterIcon
          round
          size={32}
        />
        <div className='share-icons__description'>{t('common.shareViaTwitter')}</div>
      </TwitterShareButton>
      <LinkedinShareButton
        className='share-dropdown__social-media-item'
        url={shareUrl('LinkedIn')}
        windowWidth={750}
        windowHeight={600}
      >
        <LinkedinIcon
          round
          size={32}
        />
        <div className='share-icons__description'>{t('common.shareViaLinkedin')}</div>
      </LinkedinShareButton>
      <WhatsappShareButton
        className='share-dropdown__social-media-item'
        url={shareUrl('WhatsApp')}
        title={props.title}
      >
        <WhatsappIcon
          round
          size={32}
        />
        <div className='share-icons__description'>{t('common.shareViaWhatsapp')}</div>
      </WhatsappShareButton>
      {
        !isBrowser() && <EmailShareButton
          className='share-dropdown__social-media-item'
          url={shareUrl('Email')}
          subject={props.title}
          body={`Here's a job on Oliv I think you might like: ${shareUrl}`}
        >
          <Icon
            color={THEME_COLORS.WHITE}
            backgroundColor={THEME_COLORS.GEYSER}
            icon='icon-im-share-mail'
            className='share-icons'
          />
          <div className='share-icons__description'>{t('common.shareViaEmail')}</div>
        </EmailShareButton>
      }
      <CopyToClipboard text={shareUrl('CopyURL')} onCopy={copyNotification}>
        <div className='share-dropdown__social-media-item'>
          <Icon
            color={THEME_COLORS.WHITE}
            backgroundColor={THEME_COLORS.GEYSER}
            icon='icon-im-copy-link'
            className='share-icons share-icons--copy-link'
          />
          <div className='share-icons__description'>{t('common.copyLink')}</div>
        </div>
      </CopyToClipboard>
    </div>
  )
}

class ShareDropdown extends PureComponent {
  render () {
    const buttonClass = cx({
      [this.props.className]: !!this.props.className
    })

    return (
      <DropdownMenu
        className='share-dropdown'
        triggerButton={<ShareButton className={buttonClass} iconClass={this.props.iconClass} withText={this.props.withText} />}
        align={'right'}
        closeOnOutsideClick
        withActiveStatus
      >
        <ShareMediaList {...this.props} />
      </DropdownMenu>
    )
  }
}

ShareDropdown.defaultProps = {
  title: ''
}

ShareDropdown.propTypes = {
  shareUrl: PropTypes.string.isRequired,
  withText: PropTypes.bool,
  title: PropTypes.string,
  iconClass: PropTypes.string
}

export default withTranslations(ShareDropdown)



