const ProfilePreviewEducationHighSchoolElement = props => props.isMobile ? <ProfilePreviewEducationHighSchoolElementMobile {...props} /> : <ProfilePreviewEducationHighSchoolElementWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewEducationHighSchoolElement)



