import React from 'react'
import { Form, FieldArray } from 'redux-form'
import ExperienceFormFieldArrayConnector from 'Candidate/connectors/Forms/EditProfileSections/ExperienceSection/ExperienceFormFieldArrayConnector'

function AddExperienceForm (props) {
  return (
    <Form onSubmit={props.handleSubmit}>
      <div className='form__group'>
        <FieldArray name='experiences' formName={props.form} component={ExperienceFormFieldArrayConnector} />
      </div>
    </Form>
  )
}

export default AddExperienceForm



