import React, { Fragment } from 'react'
import { Form, Field } from 'redux-form'
import { t } from 'i18next'
import { Select, InputDatePicker, RadioWrapper, Radio } from 'Shared/components/FormElements'
import moment from 'moment'
import { isNotNilOrEmpty } from 'Shared/helpers/Ramda'
import { normalizeRadio } from 'Shared/helpers/forms'
import cx from 'classnames'

function GeneralInfoForm (props) {
  const visaStatusClass = cx({
    'column': true,
    'col-12': props.isSensitiveMarket,
    'col-6': !props.isSensitiveMarket
  })

  return (
    <Form onSubmit={props.handleSubmit}>
      {
        !props.isSensitiveMarket && (
          <Fragment>
            <div className='columns form__group--margin'>
              <div className='column col-6 col-xs-12'>
                <RadioWrapper
                  legend={t('forms.gender.label')}
                  hasRequiredError={props.hasGenderFieldError}
                >
                  <Field
                    className='step_basic__radio_button'
                    component={Radio}
                    radioButtonValue='male'
                    name='gender'
                    text={t('forms.gender.male')}
                  />
                  <Field
                    className='step_basic__radio_button'
                    component={Radio}
                    radioButtonValue='female'
                    name='gender'
                    text={t('forms.gender.female')}
                  />
                </RadioWrapper>
              </div>
              <div className='column col-6 col-xs-12'>
                <Field
                  component={InputDatePicker}
                  label={t('forms.dateOfBirth.label')}
                  name='dateOfBirth'
                  icon={'ion-md-clock'}
                  floatRight
                  minDate={moment().utc().subtract(72, 'years')}
                  maxDate={moment().utc().subtract(14, 'years')}
                  scrollableYearDropdown
                  showYearDropdown
                  showMonthDropdown
                  yearDropdownItemNumber={40}
                  placeholderText={t('forms.dateOfBirth.placeholder')}
                  format={value => value ? moment(value, moment.ISO_8601) : null}
                  disabled={isNotNilOrEmpty(props.initialValues.dateOfBirth) && !props.impersonation}
                />
              </div>
            </div>
          </Fragment>
        )
      }
      <div className='columns form__group--margin'>
        {
          !props.isSensitiveMarket && (
            <Fragment>
              <div className='column col-6 col-xs-12'>
                <Field
                  component={Select}
                  label={t('forms.nationality.label')}
                  options={props.nationalityOptions}
                  placeholder={t('forms.nationality.placeholder')}
                  name='nationality'
                  withIcon
                />
              </div>
            </Fragment>
          )
        }
        <div className={visaStatusClass}>
          <Field
            component={Select}
            label={t('forms.visaStatus.label')}
            placeholder={t('forms.visaStatus.placeholder')}
            options={props.visaStatusesOptions}
            name='visaStatus'
            withIcon
          />
        </div>
      </div>
      <div className='columns form__group--margin'>
        <div className='column col-6 col-xs-12'>
          <RadioWrapper
            legend={t('forms.drivingLicence.label')}
          >
            <Field
              className='step_basic__radio_button'
              component={Radio}
              text={t('common.yes')}
              normalize={normalizeRadio}
              name='driversLicence'
              radioButtonValue
            />
            <Field
              className='step_basic__radio_button'
              component={Radio}
              text={t('common.no')}
              name='driversLicence'
              normalize={normalizeRadio}
              radioButtonValue={false}
            />
          </RadioWrapper>
        </div>
        <div className='column col-6 col-xs-12'>
          <RadioWrapper
            legend={t('forms.ownCar.label')}
            disabled={!props.driversLicence}
          >
            <Field
              className='step_basic__radio_button'
              component={Radio}
              text={t('common.yes')}
              normalize={normalizeRadio}
              disabled={!props.driversLicence}
              name='ownsACar'
              radioButtonValue
            />
            <Field
              className='step_basic__radio_button'
              component={Radio}
              text={t('common.no')}
              normalize={normalizeRadio}
              disabled={!props.driversLicence}
              name='ownsACar'
              radioButtonValue={false}
            />
          </RadioWrapper>
        </div>
      </div>
    </Form>
  )
}

export default GeneralInfoForm



