import { connect } from 'react-redux'
import { formValueSelector } from 'redux-form'
import { isFieldInvalid } from 'Shared/selectors/reduxForm'
import LanguagesFormFieldArray from 'Candidate/forms/EditProfileSections/LanguagesSection/LanguagesFormFieldArray'
import { getUserId } from 'Shared/selectors/user'
import { getLanguageNameOrLabel } from 'Shared/selectors/resources'

const mapStateToProps = (state, ownProps) => ({
  userId: getUserId(state),
  getLanguageName: languageField => {
    const code = formValueSelector(ownProps.formName)(state, `${languageField}.languageCode`)
    return getLanguageNameOrLabel(state, code)
  },
  isFieldArrayInvalid: languageField => isFieldInvalid(ownProps.formName, languageField)(state)
})

export default connect(mapStateToProps)(LanguagesFormFieldArray)



