import React from 'react'
import ProfilePreviewSocialListMobile from './ProfilePreviewSocialList.mobile'
import ProfilePreviewSocialListWeb from './ProfilePreviewSocialList'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewSocialList = props => props.isMobile ? <ProfilePreviewSocialListMobile {...props} /> : <ProfilePreviewSocialListWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewSocialList)



