import { t } from 'i18next'
import { createSelector } from 'reselect'
import * as R from 'ramda'

export const getUniversities = state => state.universities.entries

export const getUniversityNameOrLabel = createSelector(
  getUniversities,
  (state, id) => id,
  (universities, id) => R.pathOr(
    t('profile.education.sections.educationDetails.addNew'),
    [id, 'name']
  )(universities)
)



