import { connect } from 'react-redux'
import ProfilePreviewCertificatesElement from 'Candidate/components/ProfilePreviewSections/ProfilePreviewCertificates/components/ProfilePreviewCertificatesElement'
import {
  getCertificateDetails,
  getCertificateName,
  getCertificateIssuer,
  getCertificateDate
} from 'Shared/selectors/certificates'
import { deleteCertificate } from 'Shared/ducks/certificates'
import { t } from 'i18next'

const mapStateToProps = (state, ownProps) => ({
  certificateDetails: getCertificateDetails(state, ownProps.id),
  title: getCertificateName(state, ownProps.id),
  issuer: getCertificateIssuer(state, ownProps.id),
  dates: `${t('forms.yearOfCompletion.label')}: ` + getCertificateDate(state, ownProps.id)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  deleteCertificate: () => dispatch(deleteCertificate(ownProps.id))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewCertificatesElement)



