import React from 'react'
import ProfilePreviewEducationUniversityElementMobile from './ProfilePreviewEducationUniversityElement.mobile'
import ProfilePreviewEducationUniversityElementWeb from './ProfilePreviewEducationUniversityElement'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewEducationUniversityElement = props => props.isMobile ? <ProfilePreviewEducationUniversityElementMobile {...props} /> : <ProfilePreviewEducationUniversityElementWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewEducationUniversityElement)



