import React from 'react'
import HighSchoolAlgoliaField from 'Candidate/components/HighSchoolAlgoliaField'
import { Input, Select } from 'Shared/components/FormElements'
import { Field } from 'redux-form'
import { BigJustIconButton } from 'Shared/components/Button'
import { t } from 'i18next'

function HighSchoolFormFields (props) {
  return (
    <div className='step_education_body__form'>
      <HighSchoolAlgoliaField
        onSelect={props.chooseHighSchool}
        defaultRefinement={props.highSchoolName}
        name={`${props.highSchool}.highSchoolId`}
      />
      <Field
        component={Select}
        options={props.gradingSystemOptions}
        label={t('forms.educationSystem.label')}
        placeholder={t('forms.educationSystem.placeholder')}
        name={`${props.highSchool}.educationSystem`}
        withIcon
      />
      <div className='columns form__wrapper--inline'>
        <div className='column col-8 col-xs-12'>
          <Field
            component={Select}
            options={props.graduationMonths}
            label={t('forms.graduationMonth.label')}
            placeholder={t('forms.graduationMonth.placeholder')}
            name={`${props.highSchool}.graduationMonth`}
            withIcon
          />
        </div>
        <div className='column col-4 col-xs-12'>
          <Field
            component={Select}
            options={props.graduationYears}
            label={t('forms.graduationYear.label')}
            placeholder={t('forms.graduationYear.placeholder')}
            name={`${props.highSchool}.graduationYear`}
            withIcon
          />
        </div>
        <Field
          component={Input}
          name={`${props.highSchool}.id`}
          type='hidden'
        />
      </div>
      <div className='form__wrapper--button-group'>
        <BigJustIconButton onClick={props.removeField}><i className='ion-md-trash' /></BigJustIconButton>
      </div>
    </div>
  )
}

export default HighSchoolFormFields



