import React, { useState, useEffect } from 'react'
import './ProfilePreviewWebsites.scss'
import ProfilePreviewWebsitesListConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewWebsitesListConnector'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import AddWebsitesFormConnector from 'Candidate/connectors/Forms/EditProfileSections/WebsitesSection/AddWebsitesFormConnector'
import { PreviewModalMobile } from 'Shared/components/Modal'
import { t } from 'i18next'
import NotificationService from 'Shared/services/NotificationService'
import ProfilePreviewSocialListConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewSocialListConnector'
import Button, { PrimaryButton } from 'Shared/components/Button'

function ProfilePreviewWebsites (props) {
  const [addModalVisibility, setAddModalVisibility] = useState(false)

  const showAddModal = () => setAddModalVisibility(true)
  const hideAddModal = () => setAddModalVisibility(false)

  const handleSubmit = () => props.submitWebsites()

  useEffect(() => {
    if (props.isWebsitesFormSucceeded) {
      hideAddModal()
      NotificationService.success(t('alerts.updateWebsitesSuccess'))
    }
  })

  return (
    <ProfilePreviewSection
      header={t('candidate.profile.socialLinks.social')}
    >
      <ProfilePreviewWebsitesListConnector />
      <ProfilePreviewSocialListConnector />
      <AddNewSectionButton
        text={t('candidate.profile.socialLinks.addSocialLinks')}
        onClick={showAddModal}
      />
      <PreviewModalMobile
        title={t('candidate.profile.socialLinks.addSocialLinks')}
        show={addModalVisibility}
        handleClose={hideAddModal}
        handleSubmit={handleSubmit}
        handleCancel={hideAddModal}
        cancelText={t('common.cancel')}
        submitText={t('common.submit')}
        showContact={false}
        closeOnSubmit={false}
      >
        <div className='profile-preview-websites-mobile__modal'>
          <AddWebsitesFormConnector />
          <div className='profile-preview-websites-mobile__modal-buttons'>
            <Button onClick={hideAddModal}>{t('common.cancel')}</Button>
            <PrimaryButton onClick={handleSubmit}>{t('common.submit')}</PrimaryButton>
          </div>
        </div>
      </PreviewModalMobile>
    </ProfilePreviewSection>
  )
}

export default ProfilePreviewWebsites



