import React from 'react'
import ProfilePreviewLanguagesMobile from './ProfilePreviewLanguages.mobile'
import ProfilePreviewLanguagesWeb from './ProfilePreviewLanguages'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewLanguages = props => props.isMobile ? <ProfilePreviewLanguagesMobile {...props} /> : <ProfilePreviewLanguagesWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewLanguages)



