import React from 'react'
import * as R from 'ramda'
import ProfilePreviewCertificatesElementConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewCertificatesElementConnector'
import './ProfilePreviewCertificatesList.scss'

const renderUniversityElement = certificate => <ProfilePreviewCertificatesElementConnector id={certificate.id} key={certificate.id} />

const CertificateList = (props) => R.map(
  renderUniversityElement
)(props.certificateList)

function ProfilePreviewCertificateList (props) {
  return (
    <div className='columns'>
      <CertificateList certificateList={props.certificateList} />
    </div>
  )
}

export default ProfilePreviewCertificateList



