import { connect } from 'react-redux'
import ProfilePreviewCvDocumentsElement from 'Candidate/components/ProfilePreviewSections/ProfilePreviewCvDocuments/components/ProfilePreviewCvDocumentsElement'
import { getDocumentById } from 'Shared/selectors/files'
import { deleteCandidateFileById, fetchCandidateAttachedCvs } from 'Shared/ducks/files'

const mapStateToProps = (state, ownProps) => ({
  candidateAttachedCvs: state.files.candidateAttachedCvs,
  document: getDocumentById(state, ownProps.id)
})

const mapDispatchToProps = dispatch => ({
  deleteFile: (fileId, name) => dispatch(deleteCandidateFileById(fileId, name)),
  fetchCandidateAttachedCvs: () => dispatch(fetchCandidateAttachedCvs())
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewCvDocumentsElement)



