import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import AboutSectionForm from 'Candidate/forms/EditProfileSections/AboutSection/AboutSectionForm'
import { updateCandidate } from 'Shared/ducks/user/candidate'
import validate from './AboutSectionFormValidator'
import { userPitch } from 'Shared/selectors/user'

export const formName = 'AboutSectionForm'

let Form = reduxForm({
  form: formName,
  validate,
  enableReinitialize: true,
  onSubmit: (values, dispatch) => dispatch(updateCandidate(values))
})(AboutSectionForm)

const mapStateToProps = state => ({
  initialValues: {
    pitch: userPitch(state)
  }
})

export default connect(mapStateToProps)(Form)



