import startCase from 'lodash/startCase'
import { getEditorContentLength } from 'Shared/helpers/editor'
import { lengthLimit } from 'Shared/constants/Editor'

export default values => {
  const errors = {}
  const volunteeringsErrors = []

  const requiredVolunteeringFields = ['organisationName', 'position', 'startedAt', 'type', 'description']

  if (values.volunteerings) {
    values.volunteerings.forEach((volunteering, index) => {
      const volunteeringErrors = {}

      requiredVolunteeringFields.forEach(field => {
        if (!volunteering[field]) {
          volunteeringErrors[field] = {
            key: 'errors.requiredField',
            options: {
              field: startCase(field)
            }
          }
          volunteeringsErrors[index] = volunteeringErrors
        }
      })

      if (!volunteering['finishedAt'] && !volunteering['currentWork']) {
        volunteeringErrors['finishedAt'] = {
          key: 'errors.requiredField',
          options: {
            field: startCase('finishedAt')
          }
        }
        volunteeringsErrors[index] = volunteeringErrors
      }

      if (volunteering['description'] && getEditorContentLength(volunteering['description']) < 30) {
        volunteeringErrors['description'] = {
          key: 'errors.descriptionTooShort',
          options: {
            field: startCase('description'),
            chars: startCase('30')
          }
        }
        volunteeringsErrors[index] = volunteeringErrors
      }

      if (volunteering['description'] && getEditorContentLength(volunteering['description']) > lengthLimit.description) {
        volunteeringErrors['description'] = {
          key: 'errors.charactersLength',
          options: {
            limit: lengthLimit.description
          }
        }
        volunteeringsErrors[index] = volunteeringErrors
      }
    })
  }

  if (volunteeringsErrors.length) {
    errors.volunteerings = volunteeringsErrors
  }

  return errors
}



