import React, { useEffect, useState } from 'react'
import { t } from 'i18next'
import * as R from 'ramda'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import NotificationService from 'Shared/services/NotificationService'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import InterestsFormConnector from 'Candidate/connectors/Forms/Profile/Experience/InterestsFormConnector'
import './ProfilePreviewInterests.scss'
import { PreviewModalMobile } from 'Shared/components/Modal'
import Button, { SmallJustIconButton, PrimaryButton } from 'Shared/components/Button'

const mapIndexed = R.addIndex(R.map)

const InterestElement = (interest, id) => <div className='profile-preview-interests__element' key={`${interest}-${id}`}>{interest}</div>

function ProfilePreviewInterestsMobile (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  useEffect(() => {
    if (props.editInterestsSucceeded) {
      hideEditModal()
      NotificationService.success(t('alerts.updateInterestsSuccess'))
    }
  })

  const interestsList = mapIndexed(
    InterestElement
  )(props.interests)

  return (
    <ProfilePreviewSection
      isWrapped
      header={t('profile.experience.sections.interests.label')}
    >
      {
        props.interests.length > 0 && (
          <div className='profile-preview-interests'>
            <div className='profile-preview-interests__interests-container'>
              {interestsList}
            </div>
            <SmallJustIconButton className='profile-preview-interests-mobile__edit-button' onClick={showEditModal}><i className='icon-im-edit' /></SmallJustIconButton>
          </div>
        )
      }
      <AddNewSectionButton
        text={t('candidate.profile.interests.addInterests')}
        onClick={showEditModal}
      />
      <PreviewModalMobile
        title={t('modals.editInterests.title')}
        show={editModalVisibility}
        handleSubmit={props.submitEditInterests}
        handleClose={hideEditModal}
      >
        <div className='profile-preview-interests-mobile__modal profile-preview-interests-mobile__modal-margin'>
          <InterestsFormConnector />
          <div className='profile-preview-interests-mobile__modal-buttons'>
            <Button onClick={hideEditModal}>{t('common.cancel')}</Button>
            <PrimaryButton onClick={props.submitEditInterests}>{t('common.submit')}</PrimaryButton>
          </div>
        </div>
      </PreviewModalMobile>
    </ProfilePreviewSection>
  )
}

export default ProfilePreviewInterestsMobile



