import React from 'react'
import ProfilePreviewCvDocumentsWeb from './ProfilePreviewCvDocuments'
import ProfilePreviewCvDocumentsMobile from './ProfilePreviewCvDocuments.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewCvDocuments = props => props.isMobile ? <ProfilePreviewCvDocumentsMobile {...props} /> : <ProfilePreviewCvDocumentsWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewCvDocuments)



