import React, { useState, useEffect, Fragment } from 'react'
import './ProfilePreviewNavigation.scss'
import Container from 'Shared/components/Container'
import { ProfilePreviewNavigationItems } from 'Candidate/constants/ProfilePreviewNavigation'
import * as R from 'ramda'
import { Link } from 'react-scroll'
import throttle from 'lodash/throttle'
import UserAvatar from 'Shared/components/UserAvatar/UserAvatar'
import cx from 'classnames'

function ProfilePreviewNavigation (props) {
  const [lastScrollTop, setLastScrollTop] = useState(0)
  const [activeLink, setActiveLink] = useState('')

  useEffect(() => {
    window.addEventListener('scroll', handleScroll)
  }, [])

  useEffect(() => {
    return () => {
      window.removeEventListener('scroll', handleScroll)
    }
  }, [])

  const setScrollOffset = () => setLastScrollTop(window.scrollY)

  const handleScroll = () => throttle(setScrollOffset, 100)()

  const handleOnClickSetActiveLink = id => () => {
    setActiveLink(id)
    return setTimeout(() => setActiveLink(id), 600)
  }

  const handleScrollSetActiveLink = id => setActiveLink(id)

  const LinkClassName = id => cx({
    'profile-preview-navigation__link': true,
    'profile-preview-navigation__link--active': id === activeLink
  })

  const NavigationItem = navItem => {
    return (
      <Link
        onClick={handleOnClickSetActiveLink(navItem.id)}
        activeClass='profile-preview-navigation__link'
        className={LinkClassName(navItem.id)}
        spy
        smooth
        offset={-140}
        onSetActive={handleScrollSetActiveLink}
        to={navItem.id}
        key={navItem.id}
      >
        {navItem.name}
      </Link>
    )
  }

  const renderItems = R.map(NavigationItem)

  const hasScrolled = lastScrollTop > 311

  const NavClass = cx({
    'profile-preview-navigation': true,
    'profile-preview-navigation--scrolled': hasScrolled
  })

  const getProfilePreviewWidth = () => {
    const container = document.getElementById('profile-preview-navigation__profile-preview')
    return container ? container.scrollWidth : 200
  }

  const profileClass = cx({
    'profile-preview-navigation__profile-preview': !hasScrolled,
    'profile-preview-navigation__profile-preview--scrolled': hasScrolled
  })

  const helperClass = cx({
    'profile-preview-navigation__scroll-helper': true,
    'profile-preview-navigation__scroll-helper--scrolled': hasScrolled
  })

  return (
    <Fragment>
      <div className={helperClass} />
      <div id='profile-preview-navigation' className={NavClass}>
        <Container>
          <div className='profile-preview-navigation__links-wrapper'>
            <div
              id='profile-preview-navigation__profile-preview'
              className={profileClass}
              style={{ left: -(getProfilePreviewWidth() + 91) }}
            >
              <UserAvatar details={props.details} />
              <div className='profile-preview-navigation__full-name'>{props.fullName}</div>
            </div>
            <div className='profile-preview-navigation__links'>
              {renderItems(props.navigationItems)}
            </div>
          </div>
        </Container>
      </div>
    </Fragment>
  )
}

ProfilePreviewNavigation.defaultProps = {
  navigationItems: ProfilePreviewNavigationItems()
}

export default ProfilePreviewNavigation



