import React, { useState } from 'react'
import { JustStarRating } from 'Shared/components/FormElements/RatingStars'
import { t } from 'i18next'
import { degree } from 'Shared/constants/Languages'
import { BigJustIconButton } from 'Shared/components/Button'
import Modal from 'Shared/components/Modal'
import DeleteModal from 'Shared/components/DeleteModal'
import Collapse, { Panel } from 'rc-collapse'
import LanguageFormConnector from 'Candidate/connectors/Forms/Profile/Additional/LanguageFormConnector'
import './ProfilePreviewLanguagesElement.scss'

function ProfilePreviewLanguagesElement (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)
  const [deleteModalVisibility, setDeleteModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  const showDeleteModal = () => setDeleteModalVisibility(true)
  const hideDeleteModal = () => setDeleteModalVisibility(false)

  return (
    <div className='profile-preview-languages__language-wrapper'>
      <div className='profile-preview-languages__language-element'>
        <h4 className='profile-preview-languages__language-name'>{props.languageName}</h4>
        <div className='columns'>
          <div className='column col-4'>
            <JustStarRating
              label={t('profile.additional.sections.languages.skill.speaking')}
              starCount={3}
              value={props.language.speaking}
              degreeDescription={degree()}
              quiet
              disabled
            />
          </div>
          <div className='column col-4'>
            <JustStarRating
              label={t('profile.additional.sections.languages.skill.writing')}
              starCount={3}
              value={props.language.writing}
              degreeDescription={degree()}
              quiet
              disabled
            />
          </div>
          <div className='column col-4'>
            <JustStarRating
              label={t('profile.additional.sections.languages.skill.reading')}
              starCount={3}
              value={props.language.reading}
              degreeDescription={degree()}
              quiet
              disabled
            />
          </div>
        </div>
      </div>
      <BigJustIconButton className='profile-preview-languages-element__edit-button' onClick={showEditModal}><i className='icon-im-edit' /></BigJustIconButton>
      <Modal
        className='profile-preview-languages-element__edit-modal'
        title={t('modals.editLanguage.title')}
        show={editModalVisibility}
        handleClose={hideEditModal}
        handleCancel={hideEditModal}
        cancelText={t('common.cancel')}
        showContact={false}
        closeOnSubmit={false}
        showSubmit={false}
        showCancel
      >
        <Collapse defaultActiveKey={'99'} activeKey={'99'} className='education-details__collapse'>
          <Panel key={'99'} header={props.languageName}>
            <LanguageFormConnector
              closeOnSubmit={hideEditModal}
              handleDelete={showDeleteModal}
              details={props.language}
            />
          </Panel>
        </Collapse>
        <DeleteModal
          title={t('remove.languageDetails.title')}
          content={t('remove.languageDetails.description')}
          handleDelete={props.deleteLanguage}
          isModalVisible={deleteModalVisibility}
          hideDeleteModal={hideDeleteModal}
        />
      </Modal>
    </div>
  )
}

export default ProfilePreviewLanguagesElement



