import React, { useState } from 'react'
import { Form, FieldArray, Field } from 'redux-form'
import WebsitesLinksArrayForm from 'Candidate/forms/Profile/WebsitesLinksArrayForm'
import { Input, InputWithIcon, InputWrapper } from 'Shared/components/FormElements'
import DeleteModal from 'Shared/components/DeleteModal'
import { t } from 'i18next'

function AddWebsitesForm (props) {
  const [linkRemoveId, setLinkRemoveId] = useState('')
  const [deleteLinkModalVisibility, setDeleteLinkModalVisibility] = useState(false)

  const openDeleteLinkModal = linkId => {
    setLinkRemoveId(linkId)
    setDeleteLinkModalVisibility(true)
  }

  const hideDeleteLinkModal = () => {
    setLinkRemoveId('')
    setDeleteLinkModalVisibility(false)
  }

  const handleDeleteLink = () => {
    props.deleteLink(linkRemoveId)
    hideDeleteLinkModal()
  }

  return (
    <Form onSubmit={props.handleSubmit}>
      <div className='form__group form__group--field-array'>
        <FieldArray name='websites' showLabel={false} showOptionalLabel={false} component={WebsitesLinksArrayForm} onDelete={openDeleteLinkModal} />
      </div>
      <div className='form__group'>
        <InputWrapper>
          <Field
            component={InputWithIcon}
            iconClassName='icon-im-linkedin'
            showOnError
            placeholder={t('forms.linkedIn.placeholder')}
            name='linkedin'
          />
          <Field
            component={Input}
            name='linkedinId'
            type='hidden'
          />
          <Field
            component={InputWithIcon}
            iconClassName='icon-im-instagram'
            placeholder={t('forms.instagram.placeholder')}
            showOnError
            name='instagram'
          />
          <Field
            component={Input}
            name='instagramId'
            type='hidden'
          />
          <Field
            component={InputWithIcon}
            iconClassName='icon-im-twitter'
            placeholder={t('forms.twitter.placeholder')}
            showOnError
            name='twitter'
          />
          <Field
            component={Input}
            name='twitterId'
            type='hidden'
          />
        </InputWrapper>
      </div>
      <DeleteModal
        title={t('remove.websites.title')}
        content={t('remove.websites.description')}
        handleDelete={handleDeleteLink}
        isModalVisible={deleteLinkModalVisibility}
        hideDeleteModal={hideDeleteLinkModal}
      />
    </Form>
  )
}

export default AddWebsitesForm



