import React, { useState, useEffect } from 'react'
import { t } from 'i18next'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import AddVolunteeringFormConnector from 'Candidate/connectors/Forms/EditProfileSections/VolunteeringSection/AddVolunteeringFormConnector'
import ProfilePreviewVolunteeringListConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewVolunteeringListConnector'
import { PreviewModalMobile } from 'Shared/components/Modal'
import './ProfilePreviewVolunteering.scss'
import NotificationService from 'Shared/services/NotificationService'
import Button, { PrimaryButton } from 'Shared/components/Button'

function ProfilePreviewVolunteeringMobile (props) {
  const [addModalVisibility, setAddModalVisibility] = useState(false)

  useEffect(() => {
    if (props.hasAddVolunteeringSucceeded) {
      hideAddModal()
      NotificationService.success(t('alerts.updateVolunteeringSuccess'))
    }
  }, [props.hasAddVolunteeringSucceeded])

  const showAddModal = () => setAddModalVisibility(true)
  const hideAddModal = () => setAddModalVisibility(false)

  return (
    <ProfilePreviewSection
      header={t('common.volunteering')}
    >
      <ProfilePreviewVolunteeringListConnector />
      <div className='profile-preview-volunteering'>
        <AddNewSectionButton
          text={t('candidate.profile.volunteering.addVolunteering')}
          onClick={showAddModal}
        />
        <PreviewModalMobile
          title={t('modals.addVolunteering.title')}
          show={addModalVisibility}
          handleClose={hideAddModal}
        >
          <AddVolunteeringFormConnector />
          <div className='profile-preview-volunteering-mobile__modal-buttons'>
            <Button onClick={hideAddModal}>{t('common.cancel')}</Button>
            <PrimaryButton onClick={props.submitAddVolunteeringForm}>{t('common.submit')}</PrimaryButton>
          </div>
        </PreviewModalMobile>
      </div>
    </ProfilePreviewSection>
  )
}

export default ProfilePreviewVolunteeringMobile



