import { connect } from 'react-redux'
import ProfilePreviewVolunteeringList from 'Candidate/components/ProfilePreviewSections/ProfilePreviewVolunteering/components/ProfilePreviewVolunteeringList'
import { getUserVolunteering } from 'Shared/selectors/experience'

const mapStateToProps = state => ({
  volunteeringList: getUserVolunteering(state)
})

export default connect(mapStateToProps)(ProfilePreviewVolunteeringList)



