import React, { useState } from 'react'
import EditorModal from './EditorModal'
import { ASPECT_RATIOS, CROP_SIZES } from 'Shared/constants/Files'
import { t } from 'i18next'
import UploadButton from './UploadButton'
import { dataURItoBlob } from 'Shared/helpers/files'
import { parseFileName } from 'Shared/helpers/string'
import {
  ACCEPTED_AVATAR_FORMATS,
  getAcceptedAvatarFormats,
  MAX_UPLOAD_FILE_SIZE
} from 'Shared/constants/S3'
import NotificationService from 'Shared/services/NotificationService'

function SingleImageUploadButton (props) {
  const [originalImage, setOriginalImage] = useState(null)
  const [editorVisibility, setEditorVisibility] = useState(false)
  const [file, setFile] = useState(null)

  const uploadImage = async croppedImage => {
    await props.uploadFile(dataURItoBlob(croppedImage), parseFileName(file.name))
  }

  const showEditorModal = () => setEditorVisibility(true)
  const hideEditorModal = () => setEditorVisibility(false)

  const handleEditorModalClose = () => {
    hideEditorModal()
    setOriginalImage(null)
  }

  const onDrop = (acceptedFiles, rejectedFiles) => {
    if (!props.requirement) {
      return props.handleException()
    }

    if (!props.multiple && !acceptedFiles.length && rejectedFiles.some(file => file.size > MAX_UPLOAD_FILE_SIZE)) {
      return NotificationService.error(t('errors.imageTooLarge', { limit: MAX_UPLOAD_FILE_SIZE / Math.pow(1024, 2) }))
    }

    if (!props.multiple && !acceptedFiles.length && rejectedFiles.some(file => !ACCEPTED_AVATAR_FORMATS.includes(file.type))) {
      return NotificationService.error(t('errors.fileUnsupported'))
    }
  }

  const onDropAccepted = acceptedFiles => {
    if (!props.requirement) {
      return props.handleException()
    }

    const reader = new window.FileReader()
    reader.onload = () => {
      setOriginalImage(reader.result)
      setFile(acceptedFiles[0])
    }

    reader.readAsDataURL(acceptedFiles[0])

    return showEditorModal()
  }

  return (
    <div className={props.className}>
      <UploadButton
        customButton={props.buttonComponent}
        customButtonChildren={props.customButtonChildren}
        onDrop={onDrop}
        onDropAccepted={onDropAccepted}
        acceptedFormats={getAcceptedAvatarFormats()}
        maxFileSize={MAX_UPLOAD_FILE_SIZE}
      />
      <EditorModal
        title={t('common.cropImage')}
        sizeConstraints={CROP_SIZES.AVATAR}
        cropAspectRatio={ASPECT_RATIOS.SQUARE}
        hideModal={handleEditorModalClose}
        handleSubmit={uploadImage}
        isModalVisible={editorVisibility}
        file={originalImage}
      />
    </div>
  )
}

SingleImageUploadButton.defaultProps = {
  handleException: () => {},
  requirement: true
}

export default SingleImageUploadButton



