import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import ProfilePreviewIntroduction from 'Candidate/components/ProfilePreviewHeader/components/ProfilePreviewIntroduction'
import { userFullName, userLocation, userMail, userPhone } from 'Shared/selectors/user'
import { userMajors } from 'Shared/selectors/education'
import { formName } from 'Candidate/connectors/Forms/EditProfileSections/IntroductionSection/IntroductionSectionFormConnector'

const mapStateToProps = state => ({
  editDetailsSucceeded: hasSubmitSucceeded(formName)(state),
  fullName: userFullName(state),
  major: userMajors(state),
  location: userLocation(state),
  email: userMail(state),
  phoneNumber: userPhone(state)
})

const mapDispatchToProps = dispatch => ({
  submitEditDetails: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewIntroduction)



