import ProfilePreviewWebsitesList from 'Candidate/components/ProfilePreviewSections/ProfilePreviewWebsites/components/ProfilePreviewWebsitesList'
import { getUserWebsites } from 'Shared/selectors/links'

const mapStateToProps = state => ({
  websites: getUserWebsites(state)
})

const mapDispatchToProps = dispatch => ({})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewWebsitesList)



