import React, { useState } from 'react'
import { t } from 'i18next'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import ProfilePreviewCvDocumentsListConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewCvDocumentsListConnector'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import Modal from 'Shared/components/Modal'
import { S3Uploader } from 'Shared/components/FormElements'
import { MAX_CV_FILE_COUNT, MAX_UPLOAD_FILE_SIZE } from 'Shared/constants/S3'
import { UPLOAD_DOCUMENT_TYPES } from 'Shared/constants/Files'
import FilesUploadProgress from 'Shared/connectors/Components/FilesUploadProgressConnector'
import './ProfilePreviewCvDocuments.scss'

function ProfilePreviewCvDocuments (props) {
  const [addModalVisibility, setAddModalVisibility] = useState(false)

  const showAddModal = () => setAddModalVisibility(true)
  const hideAddModal = () => setAddModalVisibility(false)

  const handleUpload = (file, policy, signature) => {
    props.uploadCvFile(file, policy, signature)
    hideAddModal()
  }

  return (
    <ProfilePreviewSection
      isWrapped
      header={t('candidate.profile.cvFiles.resume')}
    >
      <ProfilePreviewCvDocumentsListConnector />
      <FilesUploadProgress formLayout={false} fileType={UPLOAD_DOCUMENT_TYPES.CV} />
      <AddNewSectionButton
        text={t('candidate.profile.cvFiles.addCvFile')}
        onClick={showAddModal}
      />
      <Modal
        title={t('modals.uploadFile.title')}
        show={addModalVisibility}
        handleClose={hideAddModal}
        handleSubmit={hideAddModal}
        handleCancel={hideAddModal}
        cancelText={t('common.cancel')}
        submitText={t('common.submit')}
        showContact={false}
        closeOnSubmit={false}
      >
        <div className='profile-preview-cv-documents__modal-margin'>
          <S3Uploader
            loading={props.isFileLoading}
            generateSignature={props.generateSignature}
            uploadFile={handleUpload}
            maxSize={MAX_UPLOAD_FILE_SIZE}
            maxFileCount={MAX_CV_FILE_COUNT}
            files={props.cvDocuments}
            allFiles={props.allFiles}
          />
        </div>
      </Modal>
    </ProfilePreviewSection>
  )
}

export default ProfilePreviewCvDocuments



