import React, { useState, useEffect, Fragment } from 'react'
import { PreviewModalMobile } from 'Shared/components/Modal'
import Button, { SmallJustIconButton, PrimaryButton } from 'Shared/components/Button'
import Label from 'Shared/components/FormElements/Label'
import { t } from 'i18next'
import GeneralInfoFormConnector from 'Candidate/connectors/Forms/EditProfileSections/GeneralInfoSection/GeneralInfoFormConnector'
import './ProfilePreviewGeneralInfo.scss'
import NotificationService from 'Shared/services/NotificationService'
import { showYearsOld } from 'Shared/helpers/string'
import cx from 'classnames'
import { isNotNaN, isNotNilOrEmpty } from 'Shared/helpers/Ramda'

const GeneralInfoCell = props => {
  const textClass = cx({
    'profile-preview-general-info-mobile__cell-text': true,
    'profile-preview-general-info__cell-text--lowercase': props.lowercase
  })

  return (
    <div className='column col-6'>
      <div className='profile-preview-general-info-mobile__cell'>
        <Label>{props.label}</Label>
        <div className={textClass}>{props.text}</div>
      </div>
    </div>
  )
}

function ProfilePreviewGeneralInfoMobile (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  useEffect(() => {
    props.fetchCountries()
    if (props.editGeneralInfoSucceeded) {
      hideEditModal()
      NotificationService.success(t('alerts.updateGeneralInfoSuccess'))
    }
  })

  const showDataBasedOnSensitiveMarket = () => props.isSensitiveMarket ? (
    <Fragment>
      <div className='columns'>
        <GeneralInfoCell label={t('forms.visaStatus.label')} text={props.userVisa} />
        <GeneralInfoCell label={t('common.drivingLicence')} text={props.userDrivingLicence} />
      </div>
      <div className='columns'>
        <GeneralInfoCell label={t('common.carCapacity')} text={props.userCarCapacity} />
      </div>
    </Fragment>
  ) : (
    <Fragment>
      <div className='columns'>
        <GeneralInfoCell label={t('forms.visaStatus.label')} text={props.userVisa} />
        <GeneralInfoCell label={t('forms.nationality.label')} text={props.userNationality} />
      </div>
      <div className='columns'>
        <GeneralInfoCell label={t('common.drivingLicence')} text={props.userDrivingLicence} />
        <GeneralInfoCell label={t('forms.gender.label')} text={props.userGender} />
      </div>
      <div className='columns'>
        <GeneralInfoCell label={t('common.carCapacity')} text={props.userCarCapacity} />
        <GeneralInfoCell label={t('common.age')} lowercase={isNotNilOrEmpty(props.userAge) && isNotNaN(props.userAge)} text={showYearsOld(props.userAge)} />
      </div>
    </Fragment>
  )

  return (
    <div className='profile-preview-general-info-mobile__container'>
      <div className='profile-preview-general-info-mobile'>
        <div className='profile-preview-general-info-mobile__columns'>
          {showDataBasedOnSensitiveMarket()}
        </div>
        <SmallJustIconButton className='profile-preview-about__edit-button' onClick={showEditModal}><i className='icon-im-edit' /></SmallJustIconButton>
      </div>
      <PreviewModalMobile
        title={t('modals.editGeneralInfo.title')}
        show={editModalVisibility}
        handleClose={hideEditModal}
      >
        <div className='profile-preview-general-info-mobile__modal'>
          <GeneralInfoFormConnector />
        </div>
        <div className='profile-preview-introduction-mobile__modal-buttons'>
          <Button onClick={hideEditModal}>{t('common.cancel')}</Button>
          <PrimaryButton onClick={props.submitEditGeneralInfo}>{t('common.submit')}</PrimaryButton>
        </div>
      </PreviewModalMobile>
    </div>
  )
}

export default ProfilePreviewGeneralInfoMobile



