import React, { useEffect, useState } from 'react'
import { PreviewModalMobile } from 'Shared/components/Modal'
import { t } from 'i18next'
import { lengthLimit } from 'Shared/constants/ReadMore'
import ReadMore from 'Shared/components/ReadMore/ReadMore'
import Button, { SmallJustIconButton, PrimaryButton } from 'Shared/components/Button'
import AboutSectionFormConnector from 'Candidate/connectors/Forms/EditProfileSections/AboutSection/AboutSectionFormConnector.js'
import './ProfilePreviewAbout.scss'
import NotificationService from 'Shared/services/NotificationService'

function ProfilePreviewAbout (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  useEffect(() => {
    if (props.editPitchSucceeded) {
      hideEditModal()
      NotificationService.success(t('alerts.updateAboutMeSuccess'))
    }
  })

  return (
    <div className='profile-preview-about__container'>
      <div className='profile-preview-about'>
        <ReadMore text={props.pitch} minLength={lengthLimit.profileAboutMe} />
        <SmallJustIconButton className='profile-preview-about-mobile__edit-button' onClick={showEditModal}><i className='icon-im-edit' /></SmallJustIconButton>
      </div>
      <PreviewModalMobile
        title={t('modals.editPitch.title')}
        show={editModalVisibility}
        handleClose={hideEditModal}
      >
        <div className='profile-preview-about__modal-margin'>
          <AboutSectionFormConnector />
        </div>
        <div className='profile-preview-introduction-mobile__modal-buttons'>
          <Button onClick={hideEditModal}>{t('common.cancel')}</Button>
          <PrimaryButton onClick={props.submitEditPitch}>{t('common.submit')}</PrimaryButton>
        </div>
      </PreviewModalMobile>
    </div>
  )
}

export default ProfilePreviewAbout



