import React, { useState, useEffect } from 'react'
import Label from 'Shared/components/FormElements/Label/Label'
import withTranslations from 'Shared/hoc/withTranslations'
import { BigJustIconButton } from 'Shared/components/Button'
import Modal from 'Shared/components/Modal'
import IntroductionSectionFormConnector from 'Candidate/connectors/Forms/EditProfileSections/IntroductionSection/IntroductionSectionFormConnector'
import { t } from 'i18next'
import './ProfilePreviewIntroduction.scss'

function ProfilePreviewIntroduction (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  useEffect(() => {
    if (props.editDetailsSucceeded) {
      hideEditModal()
    }
  })

  return (
    <div className='profile-preview-introduction'>
      <div className='profile-preview-introduction__heading'>{props.fullName}</div>
      <div className='profile-preview-introduction__text profile-preview-introduction__text--primary'>{props.major}</div>
      <div className='profile-preview-introduction__text profile-preview-introduction__text--margin profile-preview-introduction__text--capitalize'>{props.location}</div>
      <div className='profile-preview-introduction__contact'>
        <div className='profile-preview-introduction__contact-item'>
          <Label>{props.t('common.contactNumber')}</Label>
          <div className='profile-preview-introduction__text profile-preview-introduction__text--light-weight'>{props.phoneNumber}</div>
        </div>
        <div className='profile-preview-introduction__contact-item'>
          <Label>{props.t('common.emailAddress')}</Label>
          <div className='profile-preview-introduction__text profile-preview-introduction__text--light-weight'>{props.email}</div>
        </div>
        <BigJustIconButton onClick={showEditModal}><i className='icon-im-edit' /></BigJustIconButton>
        <Modal
          title={t('modals.editDetails.title')}
          show={editModalVisibility}
          handleSubmit={props.submitEditDetails}
          handleClose={hideEditModal}
          handleCancel={hideEditModal}
          cancelText={t('common.cancel')}
          submitText={t('common.submit')}
          showContact={false}
          closeOnSubmit={false}
          showCancel
        >
          <div className='profile-preview-introduction__modal-margin'>
            <IntroductionSectionFormConnector />
          </div>
        </Modal>
      </div>
    </div>
  )
}

export default withTranslations(ProfilePreviewIntroduction)



