import React from 'react'
import { Form, Field } from 'redux-form'
import { Input, Select } from 'Shared/components/FormElements'
import { websiteTypeOptions } from 'Shared/constants/Links'
import { BigJustIconButton } from 'Shared/components/Button'
import { t } from 'i18next'

function EditSingleWebsiteForm (props) {
  const updateWebsite = async () => {
    await props.submit()
    return props.afterSubmit()
  }

  return (
    <Form onSubmit={props.handleSubmit}>
      <div className='form__links-group'>
        <div className='form__links--fields'>
          <Field
            component={Select}
            options={websiteTypeOptions()}
            placeholder={t('profile.additional.sections.websites.selectType')}
            name='type'
            withIcon
          />
          <Field
            component={Input}
            iconClassName='icon-im-linkedin'
            name='url'
            className='form__links-group--input'
            placeholder={t('profile.additional.sections.websites.placeholder')}
            disableGroup
          />
          <Field
            component={Input}
            name='link'
            type='hidden'
          />
          <Field
            component={Input}
            name='category'
            type='hidden'
          />
        </div>
        <div className='file-input__button-group'>
          <BigJustIconButton className='file-input__button--controls' justIcon type='button' onClick={updateWebsite}><i className='ion-md-checkmark' /></BigJustIconButton>
          <BigJustIconButton className='file-input__button--controls' justIcon type='button' onClick={props.onDelete}><i className='ion-md-trash' /></BigJustIconButton>
        </div>
      </div>
    </Form>
  )
}

export default EditSingleWebsiteForm



