import React from 'react'
import { Form, FieldArray } from 'redux-form'
import CertificatesFormFieldArrayConnector from 'Candidate/connectors/Forms/EditProfileSections/CertificatesSection/CertificatesFormFieldArrayConnector'

function AddCertificatesForm (props) {
  return (
    <Form onSubmit={props.handleSubmit}>
      <div className='form__group'>
        <FieldArray name='certificates' formName={props.form} component={CertificatesFormFieldArrayConnector} />
      </div>
    </Form>
  )
}

export default AddCertificatesForm



