import { connect } from 'react-redux'
import ReassessmentNotification from 'Candidate/components/ReassessmentNotification/ReassessmentNotification'
import { canCandidateAskForReassessment, isCandidateWaitingForReassessment } from 'Shared/selectors/user'
import { requestForReassessment } from 'Shared/ducks/user/candidate'

const mapStateToProps = state => ({
  canCandidateAskForReassessment: canCandidateAskForReassessment()(state),
  isCandidateWaitingForReassessment: isCandidateWaitingForReassessment()(state),
  isUserFetched: state.user.userFetched
})

const mapDispatchToProps = dispatch => ({
  askForReassessment: () => dispatch(requestForReassessment())
})

export default connect(mapStateToProps, mapDispatchToProps)(ReassessmentNotification)



