import React, { useEffect, useState } from 'react'
import { PreviewModalMobile } from 'Shared/components/Modal'
import Label from 'Shared/components/FormElements/Label/Label'
import Button, { SmallJustIconButton, PrimaryButton } from 'Shared/components/Button'
import IntroductionSectionFormConnector from 'Candidate/connectors/Forms/EditProfileSections/IntroductionSection/IntroductionSectionFormConnector'
import { t } from 'i18next'
import './ProfilePreviewIntroduction.scss'

function ProfilePreviewIntroductionMobile (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  useEffect(() => {
    if (props.editDetailsSucceeded) {
      hideEditModal()
    }
  })

  return (
    <div className='profile-preview-introduction-mobile'>
      <div className='profile-preview-introduction-mobile__heading'>{props.fullName}</div>
      <div className='profile-preview-introduction-mobile__text profile-preview-introduction-mobile__text--primary profile-preview-introduction-mobile__text--margin'>{props.major}</div>
      <div className='profile-preview-introduction-mobile__text profile-preview-introduction__text--capitalize profile-preview-introduction-mobile__text--margin'>{props.location}</div>
      <div className='profile-preview-introduction-mobile__contact'>
        <div className='profile-preview-introduction-mobile__contact-item'>
          <div>
            <Label>{t('common.contactNumber')}</Label>
            <div className='profile-preview-introduction-mobile__text profile-preview-introduction-mobile__text--light-weight'>{props.phoneNumber}</div>
          </div>
        </div>
        <div className='profile-preview-introduction-mobile__contact-item'>
          <div>
            <Label>{t('common.emailAddress')}</Label>
            <div className='profile-preview-introduction-mobile__text profile-preview-introduction-mobile__text--light-weight'>{props.email}</div>
          </div>
          <SmallJustIconButton onClick={showEditModal}><i className='icon-im-edit' /></SmallJustIconButton>
        </div>
        <PreviewModalMobile
          title={t('modals.editDetails.title')}
          show={editModalVisibility}
          handleClose={hideEditModal}
        >
          <div className='profile-preview-introduction-mobile__modal'>
            <IntroductionSectionFormConnector />
          </div>
          <div className='profile-preview-introduction-mobile__modal-buttons'>
            <Button onClick={hideEditModal}>{t('common.cancel')}</Button>
            <PrimaryButton onClick={props.submitEditDetails}>{t('common.submit')}</PrimaryButton>
          </div>
        </PreviewModalMobile>
      </div>
    </div>
  )
}

export default ProfilePreviewIntroductionMobile



