import React from 'react'
import { Form, FieldArray } from 'redux-form'
import VolunteeringFormFieldArrayConnector from 'Candidate/connectors/Forms/EditProfileSections/VolunteeringSection/VolunteeringFormFieldArrayConnector'

function AddVolunteeringForm (props) {
  return (
    <Form onSubmit={props.handleSubmit}>
      <div className='form__group'>
        <FieldArray name='volunteerings' formName={props.form} component={VolunteeringFormFieldArrayConnector} />
      </div>
    </Form>
  )
}

export default AddVolunteeringForm



