import { connect } from 'react-redux'
import ProfilePreviewEducationHighSchoolElement from 'Candidate/components/ProfilePreviewSections/ProfilePreviewEducation/components/ProfilePreviewEducationHighSchoolElement'
import { deleteHighSchoolEducationDetailsById } from 'Shared/ducks/highSchoolLevelEducation'
import { getHighSchoolById, getHighSchoolName, getEducationSystem, getEducationGraduationDate, hasOneHighSchoolEducation } from 'Shared/selectors/highSchoolLevelEducation'
import { t } from 'i18next'

const mapStateToProps = (state, ownProps) => ({
  title: getHighSchoolName(state, ownProps.id),
  description: `${t('forms.educationSystem.label')}: ` + getEducationSystem(state, ownProps.id),
  graduationDate: `${t('common.graduationDate')}: ` + getEducationGraduationDate(state, ownProps.id),
  educationDetails: getHighSchoolById(state, ownProps.id),
  hasOneHighSchoolEducation: hasOneHighSchoolEducation(state)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  deleteEducationDetailsById: () => dispatch(deleteHighSchoolEducationDetailsById(ownProps.id))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewEducationHighSchoolElement)



