import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import { getCandidateLinkType, hasAnySocialLink } from 'Shared/selectors/links'
import ProfilePreviewSocialList from 'Candidate/components/ProfilePreviewSections/ProfilePreviewWebsites/components/ProfilePreviewSocialList'
import { linkTypes } from 'Shared/constants/Links'
import { formName } from 'Candidate/connectors/Forms/EditProfileSections/WebsitesSection/AddSocialLinksFormConnector'

const mapStateToProps = state => ({
  linkedIn: getCandidateLinkType(state.user.details.id, linkTypes.linkedin)(state),
  instagram: getCandidateLinkType(state.user.details.id, linkTypes.instagram)(state),
  twitter: getCandidateLinkType(state.user.details.id, linkTypes.twitter)(state),
  hasAnySocialLink: hasAnySocialLink(state),
  isSocialLinksFormSucceeded: hasSubmitSucceeded(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  submitSocialLinks: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewSocialList)



