import React from 'react'
import UserAvatar from 'Shared/components/UserAvatar/UserAvatar'
import SingleImageUploadButton from 'Shared/components/SingleImageUploader/SingleImageUploadButton'
import { SmallJustIconButton } from 'Shared/components/Button'
import './ProfilePreviewPhoto.scss'

const EditButton = props => <SmallJustIconButton {...props} className='profile-preview-photo-mobile__edit-button' />

function ProfilePreviewPhotoMobile (props) {
  return (
    <div className='profile-preview-photo-mobile__container'>
      <UserAvatar details={props.details} className='profile-preview-photo-mobile__image' />
      <SingleImageUploadButton
        uploadFile={props.uploadPhoto}
        buttonComponent={EditButton}
        customButtonChildren={<i className='icon-im-edit' />}
        className='profile-preview-photo-mobile__edit-button-container'
      />
    </div>
  )
}

export default ProfilePreviewPhotoMobile



