import { connect } from 'react-redux'
import { hasSubmitSucceeded, submit } from 'redux-form'
import ProfilePreviewLanguages from 'Candidate/components/ProfilePreviewSections/ProfilePreviewLanguages'
import { fetchLanguagesIfNeeded } from 'Shared/ducks/resources'
import { formName } from 'Candidate/connectors/Forms/EditProfileSections/LanguagesSection/AddLanguagesFormConnector'

const mapStateToProps = state => ({
  hasAddNewLanguageSucceeded: hasSubmitSucceeded(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  submitAddLanguages: () => dispatch(submit(formName)),
  fetchLanguagesIfNeeded: () => dispatch(fetchLanguagesIfNeeded())
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewLanguages)



