import CandidateProfileViewWeb from './CandidateProfileView'
import CandidateProfileViewMobile from './CandidateProfileView.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

import React from 'react'

const CandidateProfileView = (props) => props.isMobile ? <CandidateProfileViewMobile {...props} /> : <CandidateProfileViewWeb {...props} />

export default withDeviceWidthDetector(CandidateProfileView)



