import { connect } from 'react-redux'
import ProfilePreviewAvailability from 'Candidate/components/ProfilePreviewHeader/components/ProfilePreviewAvailability'
import { userAvailabilityRange, isAvailable } from 'Shared/selectors/user'

const mapStateToProps = state => ({
  userAvailability: userAvailabilityRange(state),
  isAvailable: isAvailable(state)
})

const mapDispatchToProps = dispatch => ({})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewAvailability)



