import { connect } from 'react-redux'
import ProfilePreviewVideoCv from 'Candidate/components/ProfilePreviewSections/ProfilePreviewVideoCv'
import { userVideoCvUrl } from 'Shared/selectors/user'
import { uploadUserVideoCv } from 'Shared/ducks/user/candidate'
import { deleteVideo } from 'Shared/ducks/files'

const mapStateToProps = state => ({
  videoCvUrl: userVideoCvUrl(state),
  isVideoUploading: state.files.videoCvUploadProgress
})

const mapDispatchToProps = dispatch => ({
  uploadVideoCv: (file, filename) => dispatch(uploadUserVideoCv(file, filename)),
  deleteVideoCv: () => dispatch(deleteVideo())
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewVideoCv)



