import { connect } from 'react-redux'
import ProfilePreviewPhoto from 'Candidate/components/ProfilePreviewHeader/components/ProfilePreviewPhoto'
import { getUserDetails } from 'Shared/selectors/user'
import { uploadUserAvatar } from 'Shared/ducks/user'
import { updateUserAttributes } from 'Chat/ducks/classes/users'

const mapStateToProps = (state) => ({
  details: getUserDetails(state)
})

const mapDispatchToProps = dispatch => ({
  uploadPhoto: async (file, filename) => {
    await dispatch(uploadUserAvatar(file, filename))
    dispatch(updateUserAttributes())
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewPhoto)



