import React from 'react'
import ProfilePreviewVolunteeringMobile from './ProfilePreviewVolunteering.mobile'
import ProfilePreviewVolunteeringWeb from './ProfilePreviewVolunteering'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewVolunteering = props => props.isMobile ? <ProfilePreviewVolunteeringMobile {...props} /> : <ProfilePreviewVolunteeringWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewVolunteering)



