import { connect } from 'react-redux'
import ProfilePreviewExperienceElement from 'Candidate/components/ProfilePreviewSections/ProfilePreviewExperience/components/ProfilePreviewExperienceElement'
import {
  getExperienceDetails,
  getExperiencePosition,
  getExperienceOrganisationName,
  getExperienceDates,
  getExperienceDescription,
  hasOneWorkExperience
} from 'Shared/selectors/experience'
import { deleteExperience } from 'Shared/ducks/experiences'

const mapStateToProps = (state, ownProps) => ({
  experienceDetails: getExperienceDetails(state, ownProps.id),
  title: getExperiencePosition(state, ownProps.id),
  organisationName: getExperienceOrganisationName(state, ownProps.id),
  dates: getExperienceDates(state, ownProps.id),
  description: getExperienceDescription(state, ownProps.id),
  hasOneWorkExperience: hasOneWorkExperience(state)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  deleteExperience: () => dispatch(deleteExperience(ownProps.id))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewExperienceElement)



