import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import ProfilePreviewGeneralInfo from 'Candidate/components/ProfilePreviewSections/ProfilePreviewGeneralInfo'
import {
  userAge,
  userGender,
  userNationality,
  userVisa,
  userDrivingLicence,
  userCarCapacity
} from 'Shared/selectors/user'
import { fetchCountriesIfNeeded } from 'Shared/ducks/resources'
import { formName } from 'Candidate/connectors/Forms/EditProfileSections/GeneralInfoSection/GeneralInfoFormConnector'
import { isSensitiveMarket } from 'Shared/selectors/geolocation'

const mapStateToProps = state => ({
  editGeneralInfoSucceeded: hasSubmitSucceeded(formName)(state),
  userAge: userAge(state),
  userGender: userGender(state),
  userNationality: userNationality(state),
  userVisa: userVisa(state),
  userDrivingLicence: userDrivingLicence(state),
  userCarCapacity: userCarCapacity(state),
  isSensitiveMarket: isSensitiveMarket(state)
})

const mapDispatchToProps = dispatch => ({
  fetchCountries: () => dispatch(fetchCountriesIfNeeded()),
  submitEditGeneralInfo: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewGeneralInfo)



