import React from 'react'
import ProfilePreviewGeneralInfoWeb from './ProfilePreviewGeneralInfo'
import ProfilePreviewGeneralInfoMobile from './ProfilePreviewGeneralInfo.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewGeneralInfo = props => props.isMobile ? <ProfilePreviewGeneralInfoMobile {...props} /> : <ProfilePreviewGeneralInfoWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewGeneralInfo)



