import React, { useState } from 'react'
import { downloadFile } from 'Shared/helpers/files'
import Label from 'Shared/components/FormElements/Label'
import { SmallJustIconButton } from 'Shared/components/Button'
import DeleteModal from 'Shared/components/DeleteModal'
import UploadedFileFormConnector from 'Candidate/connectors/UploadedFileFormConnector/UploadedFileFormConnector'
import './ProfilePreviewAdditionalDocumentsElement.scss'
import { t } from 'i18next'

function ProfilePreviewAdditionalDocumentsElementMobile (props) {
  const [editModeVisibility, setEditModeVisibility] = useState(false)
  const [deleteModalVisibility, setDeleteModalVisibility] = useState(false)

  const openEditMode = () => setEditModeVisibility(true)
  const hideEditMode = () => setEditModeVisibility(false)

  const handleDownload = () => downloadFile(props.document.userId, props.document.originalName)

  const openDeleteModal = () => setDeleteModalVisibility(true)
  const hideDeleteModal = () => setDeleteModalVisibility(false)

  const handleDelete = () => {
    props.deleteFile(props.document.id, props.document.title)
    hideDeleteModal()
  }

  return editModeVisibility ? (
    <div className='profile-preview-documents-mobile__document-element-edit-mode'>
      <UploadedFileFormConnector defaultEditMode afterSubmit={hideEditMode} file={props.document} onDelete={openDeleteModal} />
      <DeleteModal
        title={t('remove.file.title', {name: props.document.title})}
        content={t('remove.file.description', {name: props.document.title})}
        handleDelete={handleDelete}
        isModalVisible={deleteModalVisibility}
        hideDeleteModal={hideDeleteModal}
      />
    </div>
  ) : (
    <div className='profile-preview-documents-mobile__document-element'>
      <div className='profile-preview-documents-mobile__document-details'>
        <Label>{t(`documentTypes.${props.document.documentType}`)}</Label>
        <div className='profile-preview-documents-mobile__document-title'>{props.document.title}</div>
      </div>
      <div className='profile-preview-documents-mobile__document-buttons'>
        <SmallJustIconButton onClick={handleDownload}><i className='ion-md-download' /></SmallJustIconButton>
        <SmallJustIconButton onClick={openEditMode}><i className='icon-im-edit' /></SmallJustIconButton>
      </div>
    </div>
  )
}

export default ProfilePreviewAdditionalDocumentsElementMobile



