import { t } from 'i18next'
import { createSelector } from 'reselect'
import * as R from 'ramda'

export const getHighSchools = state => state.highSchools.entries

export const getHighSchoolNameOrLabel = createSelector(
  getHighSchools,
  (state, id) => id,
  (highSchools, id) => R.pathOr(
    t('profile.education.sections.highSchoolEducationDetails.addNew'),
    [id, 'name']
  )(highSchools)
)



