import React from 'react'
import ProfilePreviewCvDocumentsElementMobile from './ProfilePreviewCvDocumentsElement.mobile'
import ProfilePreviewCvDocumentsElementWeb from './ProfilePreviewCvDocumentsElement'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewCvDocumentsElement = props => props.isMobile ? <ProfilePreviewCvDocumentsElementMobile {...props} /> : <ProfilePreviewCvDocumentsElementWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewCvDocumentsElement)



