import React from 'react'
import * as R from 'ramda'
import ProfilePreviewEducationHighSchoolElementConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewEducationHighSchoolElementConnector'
import './ProfilePreviewEducationHighSchools.scss'

const renderHighSchoolElement = education => <ProfilePreviewEducationHighSchoolElementConnector id={education.id} key={education.id} />

const HighSchoolList = (props) => R.map(
  renderHighSchoolElement
)(props.educationList)

function ProfilePreviewEducationHighSchools (props) {
  return (
    <div className='columns'>
      <HighSchoolList educationList={props.educationList} />
    </div>
  )
}

export default ProfilePreviewEducationHighSchools



