import React from 'react'
import ProfilePreviewExperienceMobile from './ProfilePreviewExperience.mobile'
import ProfilePreviewExperienceWeb from './ProfilePreviewExperience'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewExperience = props => props.isMobile ? <ProfilePreviewExperienceMobile {...props} /> : <ProfilePreviewExperienceWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewExperience)



