import { educationLevels } from 'Shared/constants/Candidates'
import startCase from 'lodash/startCase'
import trimEnd from 'lodash/trimEnd'

export default values => {
  const errors = {}
  const universitiesErrors = []
  const highSchoolsErrors = []

  if (values.educationLevel && values.educationLevel === educationLevels.university) {
    if (!values.universities || (values.universities && values.universities.length === 0)) {
      errors['educationLevel'] = {
        key: 'errors.emptyEducationForUniversity'
      }
    }
  }

  if (values.educationLevel && values.educationLevel === educationLevels.highSchool) {
    if (!values.highSchools || (values.highSchools && values.highSchools.length === 0)) {
      errors['educationLevel'] = {
        key: 'errors.emptyEducationForHighSchool'
      }
    }
  }

  if (values.educationLevel === educationLevels.none) {
    errors['educationLevel'] = {
      key: 'errors.requiredField',
      options: {
        field: 'education'
      }
    }
  }

  const universityRequiredFields = ['degree', 'gradeFormat', 'gradeSystem', 'gradeValue', 'graduationMonth', 'graduationYear']
  const universityRequiredFieldsId = ['majorId', 'universityId']

  if (values.universities) {
    values.universities.forEach((university, index) => {
      const universityErrors = {}

      universityRequiredFields.forEach(field => {
        if (!university[field]) {
          universityErrors[field] = {
            key: 'errors.requiredField',
            options: {
              field: startCase(field)
            }
          }
          universitiesErrors[index] = universityErrors
        }
      })

      universityRequiredFieldsId.forEach(field => {
        if (!university[field]) {
          universityErrors[field] = {
            key: 'errors.requiredField',
            options: {
              field: trimEnd(field, 'Id')
            }
          }
          universitiesErrors[index] = universityErrors
        }
      })
    })
  }

  const highSchoolRequiredFields = ['educationSystem', 'graduationMonth', 'graduationYear']
  const highSchoolRequiredFieldsId = ['highSchoolId']

  if (values.highSchools) {
    values.highSchools.forEach((highSchool, index) => {
      const highSchoolErrors = {}

      highSchoolRequiredFields.forEach(field => {
        if (!highSchool[field]) {
          highSchoolErrors[field] = {
            key: 'errors.requiredField',
            options: {
              field: startCase(field)
            }
          }
          highSchoolsErrors[index] = highSchoolsErrors
        }
      })

      highSchoolRequiredFieldsId.forEach(field => {
        if (!highSchool[field]) {
          highSchoolErrors[field] = {
            key: 'errors.requiredField',
            options: {
              field: trimEnd(field, 'Id')
            }
          }
          highSchoolsErrors[index] = highSchoolErrors
        }
      })
    })
  }

  if (highSchoolsErrors.length) {
    errors.highSchools = highSchoolsErrors
  }

  if (universitiesErrors.length) {
    errors.universities = universitiesErrors
  }

  return errors
}



