import React, { useEffect, useState } from 'react'
import { t } from 'i18next'
import * as R from 'ramda'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import SkillsFormConnector from 'Candidate/connectors/Forms/Profile/Experience/SkillsFormConnector'
import Button, { SmallJustIconButton, PrimaryButton } from 'Shared/components/Button'
import { PreviewModalMobile } from 'Shared/components/Modal'
import './ProfilePreviewSkills.scss'
import NotificationService from 'Shared/services/NotificationService'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'

const mapIndexed = R.addIndex(R.map)

const SkillElement = (skill, id) => <div className='profile-preview-skills__skill-element' key={`${skill}-${id}`}>{skill}</div>

function ProfilePreviewSkillsMobile (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  useEffect(() => {
    if (props.editSkillsSucceeded) {
      hideEditModal()
      NotificationService.success(t('alerts.updateSkillsSuccess'))
    }
  })

  const skillsList = mapIndexed(
    SkillElement
  )(props.skills)

  return (
    <ProfilePreviewSection header={t('profile.experience.sections.skills.label')}>
      {
        props.skills.length > 0 && (
          <div className='profile-preview-skills'>
            <div className='profile-preview-skills__skills-container'>
              {skillsList}
            </div>
            <SmallJustIconButton className='profile-preview-skills-mobile__edit-button' onClick={showEditModal}><i className='icon-im-edit' /></SmallJustIconButton>
          </div>
        )
      }
      <AddNewSectionButton
        text={t('candidate.profile.skills.addSkills')}
        onClick={showEditModal}
      />
      <PreviewModalMobile
        title={t('modals.editSkills.title')}
        show={editModalVisibility}
        handleSubmit={props.submitEditSkills}
        handleClose={hideEditModal}
      >
        <div className='profile-preview-skills-mobile__modal profile-preview-skills-mobile__modal-margin'>
          <SkillsFormConnector />
        </div>
        <div className='profile-preview-skills-mobile__modal-buttons'>
          <Button onClick={hideEditModal}>{t('common.cancel')}</Button>
          <PrimaryButton onClick={props.submitEditSkills}>{t('common.submit')}</PrimaryButton>
        </div>
      </PreviewModalMobile>
    </ProfilePreviewSection>
  )
}

export default ProfilePreviewSkillsMobile



