import { connect } from 'react-redux'
import CertificatesFormFields from 'Candidate/forms/EditProfileSections/CertificatesSection/CertificatesFormFields'
import { getGeneratePastYearsListOptions } from 'Shared/helpers/date'

const mapStateToProps = state => ({
  years: getGeneratePastYearsListOptions()
})

export default connect(mapStateToProps)(CertificatesFormFields)



