import { validateUrlWithProtocol, validateProtocol } from 'Shared/helpers/string'

export default values => {
  const errors = {}

  if (!values.url) {
    errors.url = {
      key: 'errors.requiredField',
      options: {
        field: 'Url'
      }
    }
  }

  if (values.url && validateProtocol(values.url)) {
    errors.url = {
      key: 'errors.invalidLinkNoHttp',
      options: {}
    }
  } else if (values.url && validateUrlWithProtocol(values.url)) {
    errors.url = {
      key: 'errors.invalidLink',
      options: {}
    }
  }

  return errors
}



