import React from 'react'
import ProfilePreviewVolunteeringElementMobile from './ProfilePreviewVolunteeringElement.mobile'
import ProfilePreviewVolunteeringElementWeb from './ProfilePreviewVolunteeringElement'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewVolunteeringElement = props => props.isMobile ? <ProfilePreviewVolunteeringElementMobile {...props} /> : <ProfilePreviewVolunteeringElementWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewVolunteeringElement)



