import React, { useState } from 'react'
import { isNotNilOrEmpty } from 'Shared/helpers/Ramda'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import Button, { SmallJustIconButton } from 'Shared/components/Button'
import { t } from 'i18next'
import './ProfilePreviewEducationUniversityElement.scss'
import Collapse, { Panel } from 'rc-collapse'
import { EducationDetailsFormConnector } from 'Candidate/connectors/Forms/Profile/Education'
import { PreviewModalMobile } from 'Shared/components/Modal'
import DeleteModal from 'Shared/components/DeleteModal'

function ProfilePreviewEducationUniversityElement (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)
  const [deleteModalVisibility, setDeleteModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  const showDeleteModal = () => setDeleteModalVisibility(true)
  const hideDeleteModal = () => setDeleteModalVisibility(false)

  return (
    <div className='column col-12 profile-preview-education-element-mobile'>
      <div className='profile-preview-education-element_wrapper'>
        <div className='profile-preview-education-element__details'>
          <div>
            <div className='profile-preview-education-element__logo'>
              {
                isNotNilOrEmpty(props.logoUrl) ? (
                  <img className='profile-preview-education-element__logo-img' src={props.logoUrl} />
                ) : (
                  <div className='profile-preview-education-element__logo-icon icon-im-universities' />
                )
              }
            </div>
          </div>
          <div>
            <div className='profile-preview-education-element-mobile__header'>{props.title}</div>
            <div className='profile-preview-education-element-mobile__text'>{props.description}</div>
            <div className='profile-preview-education-element-mobile__text profile-preview-education-element-mobile__text--grey'>{props.graduationDate}</div>
            {props.gradingInfo && <div className='profile-preview-education-element-mobile__text profile-preview-education-element-mobile__text--grey'>{props.gradingInfo}</div>}
            {props.normalizedGrade && <div className='profile-preview-education-element-mobile__text profile-preview-education-element-mobile__text--grey'>
              {props.normalizedGrade}&nbsp;
              <Tooltip content={t('profile.education.normalizedGrade.description')}>
                <i className='ion-md-information-circle' />
              </Tooltip>
            </div>}
          </div>
        </div>
        <SmallJustIconButton className='profile-preview-education-element-mobile__text' onClick={showEditModal}><i className='icon-im-edit' /></SmallJustIconButton>
      </div>
      <PreviewModalMobile
        title={t('modals.editEducation.title')}
        show={editModalVisibility}
        handleClose={hideEditModal}
      >
        <Collapse defaultActiveKey={'99'} activeKey={'99'} className='education-details__collapse'>
          <Panel key={'99'} header={props.title}>
            <EducationDetailsFormConnector
              key={props.id}
              details={props.educationDetails}
              closeOnSubmit={hideEditModal}
              handleDelete={showDeleteModal}
              disableDelete={props.hasOneUniversityEducation}
            />
          </Panel>
        </Collapse>
        <DeleteModal
          title={t('remove.educationDetails.title')}
          content={t('remove.educationDetails.description')}
          handleDelete={props.deleteEducationDetailsById}
          isModalVisible={deleteModalVisibility}
          hideDeleteModal={hideDeleteModal}
        />
        <div className='profile-preview-education-mobile__modal-buttons'>
          <Button onClick={hideEditModal}>{t('common.cancel')}</Button>
        </div>
      </PreviewModalMobile>
    </div>
  )
}

export default ProfilePreviewEducationUniversityElement



