import * as R from 'ramda'
import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import { mapToOptionsByTranslations } from 'Shared/helpers/forms'

const getMajors = state => state.majors.entries

export const getMajorOptions = createSelector(
  getMajors,
  R.pipe(
    transformToArray,
    mapToOptionsByTranslations
  )
)



