import { reduxForm } from 'redux-form'
import AddExperienceForm from 'Candidate/forms/EditProfileSections/ExperienceSection/AddExperienceForm'
import validate from './AddExperienceFormValidator'
import { addExperience } from 'Shared/ducks/experiences'
import { cleanTrailingSpacesFromHtml } from 'Shared/helpers/string'

export const formName = 'AddExperienceForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmit: (values, dispatch) => Promise.all(
    values.experiences.map(experienceValues => {
      const cleanDescription = cleanTrailingSpacesFromHtml(experienceValues.description)
      return dispatch(addExperience({ ...experienceValues, description: cleanDescription }))
    })
  )
})(AddExperienceForm)

export default Form



