import { connect } from 'react-redux'
import ProfilePreviewMoreActions from 'Candidate/components/ProfilePreviewHeader/components/ProfilePreviewMoreActions'
import { getUserId, userFirstName, userLastName, userProfileVisibility } from 'Shared/selectors/user'
import { updateCandidate } from 'Shared/ducks/user/candidate'
import { mapVisibilityValues, reverseMapVisibilityValues } from 'Shared/helpers/profile'

const mapStateToProps = state => ({
  id: getUserId(state),
  firstName: userFirstName(state),
  lastName: userLastName(state),
  userProfileVisibility: reverseMapVisibilityValues(userProfileVisibility(state))
})

const mapDispatchToProps = dispatch => ({
  changeVisibility: visibility => dispatch(updateCandidate({ visibility: mapVisibilityValues(visibility) }, true))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewMoreActions)



