import { connect } from 'react-redux'
import VolunteeringFormFields from 'Candidate/forms/EditProfileSections/VolunteeringSection/VolunteeringFormFields'
import { change, formValueSelector } from 'redux-form'
import { activityOptions } from 'Shared/constants/Experience'

const mapStateToProps = (state, ownProps) => ({
  activityTypes: activityOptions(),
  currentWork: formValueSelector(ownProps.formName)(state, `${ownProps.experience}.currentWork`)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  onCurrentWorkChange: () => dispatch(change(ownProps.formName, `${ownProps.experience}.finishedAt`, null))
})

export default connect(mapStateToProps, mapDispatchToProps)(VolunteeringFormFields)



