import React, { useState } from 'react'
import { ProfileVisibilitySection, ShareProfileSection } from 'Candidate/components/ProfilePreviewHeader/components/ProfilePreviewMoreActions/ProfilePreviewMoreActions'
import { PreviewModalMobile } from 'Shared/components/Modal'
import { generatePublicProfileRoute } from 'Shared/helpers/routes'
import { t } from 'i18next'
import './ProfilePreviewMoreActions.scss'

function ProfileNavigationMoreActionsMobile (props) {
  const [modalVisibility, setModalVisibility] = useState(false)
  const profileUrl = generatePublicProfileRoute(props.firstName, props.lastName, props.id)

  const closeModal = () => setModalVisibility(false)
  const openModal = () => setModalVisibility(true)

  return (
    <div>
      <i onClick={openModal} className='icon-im-more profile-preview-more-actions-mobile__btn' />
      <PreviewModalMobile
        title={t('candidate.profile.reachMoreEmployers')}
        show={modalVisibility}
        handleClose={closeModal}
      >
        <ProfileVisibilitySection profileUrl={profileUrl} userProfileVisibility={props.userProfileVisibility} changeVisibility={props.changeVisibility} />
        <div className='profile-preview-more-actions__divider' />
        <ShareProfileSection profileUrl={profileUrl} />
      </PreviewModalMobile>
    </div>
  )
}

export default ProfileNavigationMoreActionsMobile



