import React, { useState, useEffect } from 'react'
import { t } from 'i18next'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton/index'
import Collapse, { Panel } from 'rc-collapse'
import cx from 'classnames'
import VolunteeringFormFieldsConnector from 'Candidate/connectors/Forms/EditProfileSections/VolunteeringSection/VolunteeringFormFieldsConnector'
import 'Shared/assets/scss/vendor/rc-collapse-form.scss'

function VolunteeringFormUniversitiesFieldArray (props) {
  const [accordionActiveKey, setAccordionActiveKey] = useState(null)

  const addNewField = () => {
    props.fields.push({
      candidateId: props.userId,
      type: null,
      organisationName: null,
      description: null,
      currentWork: null,
      finishedAt: null,
      startedAt: null,
      position: null
    })
  }

  useEffect(() => {
    addNewField()
  }, [])

  useEffect(() => {
    if (props.fields.length > 0) {
      setAccordionActiveKey(props.fields.length.toString())
    }
  }, [props.fields.length])

  const removeField = index => () => {
    const { fields, onDelete } = props
    const itemId = fields.get(index).id
    return itemId ? onDelete(itemId) : fields.remove(index)
  }

  const VolunteeringFormField = (volunteering, index) => {
    const PanelClass = cx({
      'form-fields__collapse-panel-error': props.isFieldArrayInvalid(volunteering)
    })

    return (
      <Panel className={PanelClass} key={index + 1} header={props.getOrganisationName(volunteering)}>
        <VolunteeringFormFieldsConnector
          removeField={removeField(index)}
          formName={props.formName}
          volunteering={volunteering}
        />
      </Panel>
    )
  }

  const getFields = () => props.fields.map(VolunteeringFormField)

  return (
    <div>
      <Collapse
        accordion
        className='form-fields__collapse'
        activeKey={accordionActiveKey}
        onChange={setAccordionActiveKey}
      >
        {getFields()}
      </Collapse>
      <AddNewSectionButton
        text={t('profile.experience.sections.volunteering.addNew')}
        onClick={addNewField}
      />
    </div>
  )
}

export default VolunteeringFormUniversitiesFieldArray



