import React from 'react'
import ProfilePreviewWebsitesMobile from './ProfilePreviewWebsites.mobile'
import ProfilePreviewWebsitesWeb from './ProfilePreviewWebsites'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewWebsites = props => props.isMobile ? <ProfilePreviewWebsitesMobile {...props} /> : <ProfilePreviewWebsitesWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewWebsites)



