import React, { Component } from 'react'
import { PreviewModalMobile } from 'Shared/components/Modal'
import Button, { PrimaryButton } from 'Shared/components/Button'
import ProfileAvailabilityConnector from 'Candidate/connectors/Availability/ProfileAvailabilityConnector'
import AvailabilityFormConnector from 'Candidate/connectors/Forms/Availability/Availability'
import withTranslations from 'Shared/hoc/withTranslations'
import NotificationService from 'Shared/services/NotificationService'
import cx from 'classnames'
import ProfilePreviewMoreActionsConnector from 'Candidate/connectors/ProfilePreviewHeader/ProfilePreviewMoreActionsConnector'
import './CandidateProfileViewNavbar.scss'

class CandidateProfileViewNavbar extends Component {
  state = {
    isAvailabilityPreviewOpen: false,
    isAvailabilityChangeOpen: false
  }

  componentDidUpdate (prevProps) {
    if (this.props.hasSubmitSucceeded && prevProps.hasSubmitSucceeded !== this.props.hasSubmitSucceeded) {
      NotificationService.success(this.props.t('alerts.availabilityUpdated'))
      this.closeAvailabilityChange()
    }
  }

  openAvailabilityPreview = () => this.setState({ isAvailabilityPreviewOpen: true })
  closeAvailabilityPreview = () => this.setState({ isAvailabilityPreviewOpen: false })

  openAvailabilityChange = () => {
    this.closeAvailabilityPreview()
    this.setState({ isAvailabilityChangeOpen: true })
  }
  closeAvailabilityChange = () => this.setState({ isAvailabilityChangeOpen: false })

  availabilityModal = () => {
    const { t } = this.props
    return <PreviewModalMobile
      title={t('profile.preferences.sections.availability.label')}
      show={this.state.isAvailabilityPreviewOpen}
      handleClose={this.closeAvailabilityPreview}
    >
      <ProfileAvailabilityConnector />
      <Button
        onClick={this.openAvailabilityChange}
        className='profile-view-navbar__modal-btn'
      >
        {t('common.changeDates')}
      </Button>
    </PreviewModalMobile>
  }

  changeAvailabilityModal = () => {
    const { t } = this.props
    return <PreviewModalMobile
      title={t('profile.preferences.sections.availability.label')}
      show={this.state.isAvailabilityChangeOpen}
      handleClose={this.closeAvailabilityChange}
    >
      <div className='profile-view-navbar__modal-text'>
        {t('profile.preferences.sections.availability.text')}
      </div>
      <AvailabilityFormConnector />
      <PrimaryButton
        disabled={this.props.isFormSubmitting}
        onClick={this.props.submitForm}
        className='profile-view-navbar__modal-submit-btn'
      >
        {t('common.saveChanges')}
      </PrimaryButton>
    </PreviewModalMobile>
  }

  render () {
    const calendarIconClass = cx({
      'icon-im-calendar profile-view-navbar__availability-btn': true,
      'profile-view-navbar__availability-btn--notification': !this.props.isStillAvailable
    })

    return (
      <div className='profile-view-navbar__container'>
        <i onClick={this.openAvailabilityPreview} className={calendarIconClass} />
        <ProfilePreviewMoreActionsConnector />
        {this.state.isAvailabilityPreviewOpen && this.availabilityModal()}
        {this.state.isAvailabilityChangeOpen && this.changeAvailabilityModal()}
      </div>
    )
  }
}

export default withTranslations(CandidateProfileViewNavbar)



