import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import ProfilePreviewAbout from 'Candidate/components/ProfilePreviewSections/ProfilePreviewAbout'
import { userFirstName, userPitch } from 'Shared/selectors/user'
import { formName } from 'Candidate/connectors/Forms/EditProfileSections/AboutSection/AboutSectionFormConnector.js'

const mapStateToProps = state => ({
  editPitchSucceeded: hasSubmitSucceeded(formName)(state),
  firstName: userFirstName(state),
  pitch: userPitch(state)
})

const mapDispatchToProps = dispatch => ({
  submitEditPitch: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewAbout)



