import React from 'react'
import { Field } from 'redux-form'
import { t } from 'i18next'
import { Input, Select } from 'Shared/components/FormElements'
import { BigJustIconButton } from 'Shared/components/Button'

function CertificatesFormFields (props) {
  return (
    <div className='form-group'>
      <Field
        name={`${props.certificate}.name`}
        component={Input}
        label={t('forms.courseCertificateName.label')}
        placeholder={t('forms.courseCertificateName.placeholder')}
      />
      <Field
        name={`${props.certificate}.issuer`}
        component={Input}
        label={t('forms.organisation.label')}
        placeholder={t('forms.organisation.placeholder')}
      />
      <Field
        name={`${props.certificate}.issuedAt`}
        component={Select}
        label={t('forms.yearOfCompletion.label')}
        options={props.years}
        withIcon
      />
      <Field
        component={Input}
        name={`${props.certificate}.id`}
        type='hidden'
      />
      <div className='form__wrapper--button-group'>
        <BigJustIconButton onClick={props.removeField}><i className='ion-md-trash' /></BigJustIconButton>
      </div>
    </div>
  )
}

export default CertificatesFormFields



