import { reduxForm } from 'redux-form'
import AddCertificatesForm from 'Candidate/forms/EditProfileSections/LanguagesSection/AddLanguageForm'
import validate from './AddLanguagesFormValidator'
import { addCandidateLanguage } from 'Shared/ducks/languages'

export const formName = 'AddLanguageFormForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmit: (values, dispatch) => Promise.all(
    values.languages.map(languageValues => dispatch(addCandidateLanguage(languageValues, false)))
  )
})(AddCertificatesForm)

export default Form



