import React from 'react'
import cx from 'classnames'
import './ProfilePreviewSection.scss'

function ProfilePreviewSection (props) {
  const containerClass = cx({
    'profile-preview-section': true,
    'profile-preview-section--wrapped': props.isWrapped
  })

  return (
    <div className={containerClass}>
      <div className='profile-preview-section__header'>{props.header}</div>
      <div>
        {props.children}
      </div>
    </div>
  )
}

export default ProfilePreviewSection



