import React from 'react'
import ProfilePreviewCertificatesMobile from './ProfilePreviewCertificates.mobile'
import ProfilePreviewCertificatesWeb from './ProfilePreviewCertificates'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewCertificates = props => props.isMobile ? <ProfilePreviewCertificatesMobile {...props} /> : <ProfilePreviewCertificatesWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewCertificates)



