import React from 'react'
import ProfilePreviewEducationMobile from './ProfilePreviewEducation.mobile'
import ProfilePreviewEducationWeb from './ProfilePreviewEducation'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewEducation = props => props.isMobile ? <ProfilePreviewEducationMobile {...props} /> : <ProfilePreviewEducationWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewEducation)



