import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Trans } from 'react-i18next'
import Notification from 'Shared/components/Notification/index'
import Modal from 'Shared/components/Modal'
import withTranslations from 'Shared/hoc/withTranslations/index'

import './ReassessmentNotification.scss'

class ReassessmentNotification extends Component {
  state = {
    showAskForReassessmentModal: false
  }

  static propTypes = {
    canCandidateAskForReassessment: PropTypes.bool.isRequired,
    isCandidateWaitingForReassessment: PropTypes.bool.isRequired
  }

  static defaultProps = {
    isCandidateWaitingForReassessment: false
  }

  openAskForReassessmentModal = () => this.setState({ showAskForReassessmentModal: true })
  closeAskForReassessmentModal = () => this.setState({ showAskForReassessmentModal: false })

  getNotificationText = () => (
    <Trans i18nKey='reassessment.request' className='reassessment-notification__ask-message'>
      Made significant improvements on your profile? <span className='ask-for-reassessment__btn' onClick={this.openAskForReassessmentModal}>Ask for profile review</span> for a higher rating.
    </Trans>
  )

  render () {
    const { t, canCandidateAskForReassessment, isCandidateWaitingForReassessment, isUserFetched } = this.props

    switch (true) {
      case !isUserFetched:
        return null
      case canCandidateAskForReassessment:
        return (
          <div>
            <Notification
              messageText={this.getNotificationText()}
              iconClassName='icon-im-info-outline reassessment-notification__ask-icon'
              success
              className='reassessment-notification'
            />
            <Modal
              title={t('modals.reassessment.title')}
              handleSubmit={this.props.askForReassessment}
              handleClose={this.closeAskForReassessmentModal}
              show={this.state.showAskForReassessmentModal}
              handleCancel={this.closeAskForReassessmentModal}
              cancelText={t('common.cancel')}
              submitText={t('reassessment.askForReassessment')}
              showContact={false}
              showCancel
            >
              <p>{t('modals.reassessment.description')}</p>
            </Modal>
          </div>
        )
      case isCandidateWaitingForReassessment:
        return (
          <Notification
            messageText={t('reassessment.waiting')}
            iconClassName='icon-im-info-outline reassessment-notification__waiting-icon'
            warning
            className='reassessment-notification'
          />
        )
      default:
        return null
    }
  }
}

export default withTranslations(ReassessmentNotification)



