import { connect } from 'react-redux'
import { formValueSelector } from 'redux-form'
import { isFieldInvalid } from 'Shared/selectors/reduxForm'
import VolunteeringFormFieldArray from 'Candidate/forms/EditProfileSections/VolunteeringSection/VolunteeringFormFieldArray'
import { t } from 'i18next'
import { getUserId } from 'Shared/selectors/user'

const mapStateToProps = (state, ownProps) => ({
  userId: getUserId(state),
  getOrganisationName: volunteeringField => formValueSelector(ownProps.formName)(state, `${volunteeringField}.organisationName`) || t('profile.experience.sections.volunteering.addNew'),
  isFieldArrayInvalid: volunteeringField => isFieldInvalid(ownProps.formName, volunteeringField)(state)
})

export default connect(mapStateToProps)(VolunteeringFormFieldArray)



