import React, { useState } from 'react'
import { t } from 'i18next'
import { BigJustIconButton } from 'Shared/components/Button'
import Modal from 'Shared/components/Modal'
import DeleteModal from 'Shared/components/DeleteModal'
import Collapse, { Panel } from 'rc-collapse'
import { VolunteeringDetailsFormConnector } from 'Candidate/connectors/Forms/Profile/Experience'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import './ProfilePreviewVolunteeringElement.scss'

function ProfilePreviewVolunteeringElement (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)
  const [deleteModalVisibility, setDeleteModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  const showDeleteModal = () => setDeleteModalVisibility(true)
  const hideDeleteModal = () => setDeleteModalVisibility(false)

  return (
    <div className='profile-preview-volunteering-element__container'>
      <div className='profile-preview-volunteering-element'>
        <div>
          <div className='profile-preview-volunteering-element__header'>{props.title}</div>
          <div className='profile-preview-volunteering-element__text'>{props.organisationName}</div>
          <div className='profile-preview-volunteering-element__text'>{props.dates}</div>
          <div className='profile-preview-volunteering-element__text'>{parseHtmlWithoutSpan(props.description)}</div>
        </div>
        <BigJustIconButton className='profile-preview-volunteering-element__edit-button' onClick={showEditModal}><i className='icon-im-edit' /></BigJustIconButton>
      </div>
      <Modal
        title={t('modals.editVolunteering.title')}
        show={editModalVisibility}
        handleClose={hideEditModal}
        handleCancel={hideEditModal}
        cancelText={t('common.cancel')}
        showContact={false}
        closeOnSubmit={false}
        showSubmit={false}
        showCancel
      >
        <Collapse defaultActiveKey={'99'} activeKey={'99'} className='education-details__collapse'>
          <Panel key={'99'} header={props.organisationName}>
            <VolunteeringDetailsFormConnector
              closeOnSubmit={hideEditModal}
              handleDelete={showDeleteModal}
              details={{ ...props.volunteeringDetails, ...props.organisationName }}
              disableDelete={props.hasOneVolunteering}
            />
          </Panel>
        </Collapse>
        <DeleteModal
          title={t('remove.volunteeringDetails.title')}
          content={t('remove.volunteeringDetails.description')}
          handleDelete={props.deleteVolunteering}
          isModalVisible={deleteModalVisibility}
          hideDeleteModal={hideDeleteModal}
        />
      </Modal>
    </div>
  )
}

export default ProfilePreviewVolunteeringElement



