import { connect } from 'react-redux'
import ProfilePreviewCertificateList from 'Candidate/components/ProfilePreviewSections/ProfilePreviewCertificates/components/ProfilePreviewCertificatesList'
import { getUserCertificates } from 'Shared/selectors/certificates'

const mapStateToProps = state => ({
  certificateList: getUserCertificates(state)
})

export default connect(mapStateToProps)(ProfilePreviewCertificateList)



