import { connect } from 'react-redux'
import { hasSubmitSucceeded, submit } from 'redux-form'
import ProfilePreviewCertificates from 'Candidate/components/ProfilePreviewSections/ProfilePreviewCertificates'
import { formName } from 'Candidate/connectors/Forms/EditProfileSections/CertificatesSection/AddCertificatesFormConnector'

const mapStateToProps = state => ({
  hasAddNewCertificateSucceeded: hasSubmitSucceeded(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  submitAddCertificates: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewCertificates)



