import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import LanguageService from 'Shared/services/LanguageService'
import { ISOcodes } from 'Shared/constants/i18n'
import { getUserTranslationsOrDefault } from 'Shared/helpers/translations'
import * as R from 'ramda'

const getInterestsById = state => state.interests.candidate
const getPublicCandidateInterestsById = state => state.interests.publicCandidate

const getInterestsList = interests => transformToArray(interests).map(interest => getUserTranslationsOrDefault(interest, ISOcodes[LanguageService.userLanguage]).name)

export const getCandidateInterestsList = () =>
  createSelector(
    getInterestsById,
    (getInterestsById) => getInterestsList(getInterestsById)
  )

export const getPublicCandidateInterestsList = () =>
  createSelector(
    getPublicCandidateInterestsById,
    (getPublicCandidateInterestsById) => getInterestsList(getPublicCandidateInterestsById)
  )

export const hasCandidateAnyInterests = createSelector(
  getInterestsById,
  R.pipe(
    transformToArray,
    R.isEmpty,
    R.not
  )
)



