import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import ProfilePreviewVolunteering from 'Candidate/components/ProfilePreviewSections/ProfilePreviewVolunteering'
import { formName } from 'Candidate/connectors/Forms/EditProfileSections/VolunteeringSection/AddVolunteeringFormConnector'

const mapStateToProps = state => ({
  hasAddVolunteeringSucceeded: hasSubmitSucceeded(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  submitAddVolunteeringForm: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewVolunteering)



