import { connect } from 'react-redux'
import { change, formValueSelector } from 'redux-form'
import UniversityFormFields from 'Candidate/forms/EditProfileSections/EducationSection/UniversityFormFields'
import { getUniversityNameById, getGradeSystemOptions, getGradeValuesByFormatAndSystem, getGradeFormatBySystem } from 'Shared/selectors/education'
import { degreeOptions } from 'Shared/selectors/resources'
import { getMajorOptions } from 'Shared/selectors/majors'
import withAlgolia from 'Algolia/hoc/withAlgolia'
import INDEX_NAME from 'Shared/constants/Algolia'
import { getGraduateMonthsAsOptions, getEducationYearsAsOptions } from 'Shared/helpers/date'

const mapStateToProps = (state, ownProps) => {
  const selectedGradeSystem = formValueSelector(ownProps.formName)(state, `${ownProps.university}.gradeSystem`)
  const selectedGradeFormat = formValueSelector(ownProps.formName)(state, `${ownProps.university}.gradeFormat`)
  const selectedUniversityId = formValueSelector(ownProps.formName)(state, `${ownProps.university}.id`)

  return ({
    indexName: INDEX_NAME.UNIVERSITIES,
    shouldUpdateUrl: false,
    universityName: getUniversityNameById(state, selectedUniversityId),
    degrees: degreeOptions(state),
    majors: getMajorOptions(state),
    graduationMonths: getGraduateMonthsAsOptions(),
    graduationYears: getEducationYearsAsOptions(),
    gradeSystemOptions: getGradeSystemOptions(state),
    gradeValueOptions: getGradeValuesByFormatAndSystem(state, selectedGradeFormat, selectedGradeSystem),
    gradeFormatOptions: getGradeFormatBySystem(state, selectedGradeSystem)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  resetGradeFormat: () => dispatch(change(ownProps.formName, `${ownProps.university}.gradeFormat`, null)),
  resetGradeValue: () => dispatch(change(ownProps.formName, `${ownProps.university}.gradeValue`, null)),
  chooseUniversity: value => dispatch(change(ownProps.formName, `${ownProps.university}.universityId`, value))
})

export default connect(mapStateToProps, mapDispatchToProps)(withAlgolia(UniversityFormFields))



