import { connect } from 'react-redux'
import { change } from 'redux-form'
import HighSchoolFormFields from 'Candidate/forms/EditProfileSections/EducationSection/HighSchoolFormFields'
import withAlgolia from 'Algolia/hoc/withAlgolia'
import INDEX_NAME from 'Shared/constants/Algolia'
import { getGraduateMonthsAsOptions, getEducationYearsAsOptions } from 'Shared/helpers/date'
import { highSchoolGradingSystemOptions } from 'Shared/selectors/resources'

const mapStateToProps = (state) => ({
  indexName: INDEX_NAME.HIGH_SCHOOLS,
  shouldUpdateUrl: false,
  graduationMonths: getGraduateMonthsAsOptions(),
  graduationYears: getEducationYearsAsOptions(),
  gradingSystemOptions: highSchoolGradingSystemOptions(state)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  chooseHighSchool: value => dispatch(change(ownProps.formName, `${ownProps.highSchool}.highSchoolId`, value))
})

export default connect(mapStateToProps, mapDispatchToProps)(withAlgolia(HighSchoolFormFields))



