import React, { useState } from 'react'
import { Form, Field } from 'redux-form'
import { Input, InputWithIcon, InputWrapper } from 'Shared/components/FormElements'
import { t } from 'i18next'
import { BigJustIconButton } from 'Shared/components/Button'
import DeleteModal from 'Shared/components/DeleteModal'
import './WebsitesSection.scss'

function LinkFormField ({ icon, placeholder, name, onDelete, linkId }) {
  const [deleteModalVisibility, setDeleteModalVisibility] = useState(false)
  const showDeleteModal = () => setDeleteModalVisibility(true)
  const hideDeleteModal = () => setDeleteModalVisibility(false)

  const handleDelete = () => {
    onDelete(linkId)
    hideDeleteModal()
  }

  return (
    <div className='social-links-form__field'>
      <Field
        component={InputWithIcon}
        iconClassName={icon}
        showOnError
        placeholder={placeholder}
        name={name}
      />
      <Field
        component={Input}
        name={`${name}Id`}
        type='hidden'
      />
      <BigJustIconButton disabled={!linkId} onClick={showDeleteModal}><i className='ion-md-trash' /></BigJustIconButton>
      <DeleteModal
        title={t('remove.socialLinks.title')}
        content={t('remove.socialLinks.description')}
        handleDelete={handleDelete}
        isModalVisible={deleteModalVisibility}
        hideDeleteModal={hideDeleteModal}
      />
    </div>
  )
}

function AddSocialLinksForm (props) {
  return (
    <Form onSubmit={props.handleSubmit}>
      <div className='form__group'>
        <InputWrapper>
          <LinkFormField
            icon='icon-im-linkedin'
            placeholder={t('forms.linkedIn.placeholder')}
            name='linkedin'
            onDelete={props.deleteLink}
            linkId={props.linkId('linkedinId')}
          />
          <LinkFormField
            icon='icon-im-instagram'
            placeholder={t('forms.instagram.placeholder')}
            name='instagram'
            onDelete={props.deleteLink}
            linkId={props.linkId('instagramId')}
          />
          <LinkFormField
            icon='icon-im-twitter'
            placeholder={t('forms.twitter.placeholder')}
            name='twitter'
            onDelete={props.deleteLink}
            linkId={props.linkId('twitterId')}
          />
        </InputWrapper>
      </div>
    </Form>
  )
}

export default AddSocialLinksForm



