import React from 'react'
import './ProfilePreviewAdditionalDocumentsList.scss'
import ProfilePreviewAdditionalDocumentsElementConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewAdditionalDocumentsElementConnector'
import * as R from 'ramda'

const renderDocumentElement = document => <ProfilePreviewAdditionalDocumentsElementConnector id={document.id} key={document.id} />

const DocumentsList = (props) => R.map(
  renderDocumentElement
)(props.documents)

function ProfilePreviewAdditionalDocumentsList (props) {
  return (
    <div>
      <DocumentsList documents={props.documents} />
    </div>
  )
}

export default ProfilePreviewAdditionalDocumentsList



