import React from 'react'
import * as R from 'ramda'
import ProfilePreviewExperienceElementConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewExperienceElementConnector'
import './ProfilePreviewExperienceList.scss'

const renderUniversityElement = experience => <ProfilePreviewExperienceElementConnector id={experience.id} key={experience.id} />

const ExperienceList = (props) => R.map(
  renderUniversityElement
)(props.experienceList)

function ProfilePreviewExperienceList (props) {
  return (
    <div>
      <ExperienceList experienceList={props.experienceList} />
    </div>
  )
}

export default ProfilePreviewExperienceList



