import { connect } from 'react-redux'
import ProfilePreviewNavigation from 'Candidate/components/ProfilePreviewNavigation'
import { getUserDetails, userFullName } from 'Shared/selectors/user'

const mapStateToProps = state => ({
  fullName: userFullName(state),
  details: getUserDetails(state)
})

const mapDispatchToProps = dispatch => ({})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewNavigation)



