import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileView from 'Candidate/views/CandidateProfileView'

const mapStateToProps = state => ({
  firstName: state.user.details.firstName
})

export default withRouter(connect(mapStateToProps)(CandidateProfileView))



