import React from 'react'
import PropTypes from 'prop-types'
import pure from 'recompose/pure'
import cx from 'classnames'
import Rating from 'react-rating'
import Label from '../Label'
import withTranslations from 'Shared/hoc/withTranslations'
import { getLayoutDirection } from 'Shared/helpers/layout'
import './RatingStars.scss'

const StarRating = ({t, input, label, starCount, degreeDescription, className, onHover, fullStar, emptyStar, quiet,
  withContainer, meta: { touched, error, submitting }}) => {
  const fieldClass = cx({
    'form__star-rating': true,
    'form__group--has-error': !submitting && touched && error,
    [className]: !!className
  })

  const containerClass = cx({
    'star-rating__star-container': withContainer
  })

  const getEmptyStar = withContainer
    ? <span className='star-rating__star-item'>
      <span className='ion-md-star-outline' />
    </span>
    : <span className='ion-md-star-outline' />

  const getFullStar = withContainer
    ? <span className='star-rating__star-item'>
      <span className='ion-md-star' />
    </span>
    : <span className='ion-md-star' />

  return (
    <div className={fieldClass}>
      <Label>
        <span>{label && touched && error ? t(error.key, error.options) : label}</span>
        {degreeDescription && input.value > 0 &&
        <span className='form__star-rating-degree'>{` - ${degreeDescription[input.value]}`}</span>}
      </Label>
      <div className={containerClass}>
        <Rating
          {...input}
          name={label}
          stop={starCount}
          initialRating={input.value}
          onChange={input.onChange}
          fullSymbol={fullStar || getFullStar}
          emptySymbol={emptyStar || getEmptyStar}
          quiet={quiet}
          direction={getLayoutDirection}
        />
      </div>
    </div>
  )
}

StarRating.defaultProps = {
  meta: {}
}

StarRating.propTypes = {
  input: PropTypes.shape({
    onChange: PropTypes.func.isRequired,
    value: PropTypes.number.isRequired
  }),
  onHover: PropTypes.func,
  meta: PropTypes.object,
  label: PropTypes.string.isRequired,
  starCount: PropTypes.number.isRequired,
  degreeDescription: PropTypes.object,
  className: PropTypes.string,
  quiet: PropTypes.bool
}

export default withTranslations(StarRating)

export const JustStarRating = pure(({ label, starCount, value, degreeDescription, className, quiet,
  withContainer, disabled, fullStar, emptyStar }) => {
  const starRatingClass = cx({
    'form__star-rating': true,
    'form__star-rating--just-star': true,
    [className]: !!className
  })

  const containerClass = cx({
    'star-rating__star-container': withContainer,
    'star-rating__star-container--no-border': !withContainer,
    'star-rating__star-container--disabled': disabled
  })

  return (
    <div className={starRatingClass}>
      <Label>
        <span>{label}</span>
        {degreeDescription && value > 0 &&
        <span className='form__star-rating-degree'>{` - ${degreeDescription[value]}`}</span>}
      </Label>
      <div className={containerClass}>
        <Rating
          name={label}
          stop={starCount}
          initialRating={value}
          fullSymbol={fullStar || <span className='star-rating__star-item'><span className='ion-md-star' /></span>}
          emptySymbol={emptyStar || <span className='star-rating__star-item'><span className='ion-md-star-outline' /></span>}
          quiet={quiet}
          readonly={disabled}
          direction={getLayoutDirection}
        />
      </div>
    </div>
  )
})

JustStarRating.propTypes = {
  label: PropTypes.string.isRequired,
  starCount: PropTypes.number.isRequired,
  value: PropTypes.number,
  degreeDescription: PropTypes.object,
  className: PropTypes.string,
  onHover: PropTypes.func
}

export const JustStarRatingMobile = pure(({ label, starCount, value, degreeDescription, className, quiet, withContainer, disabled, fullStar, emptyStar }) => {
  const starRatingClass = cx({
    'star-rating-mobile': true,
    'form__star-rating': true,
    'form__star-rating--just-star': true,
    [className]: !!className
  })

  const containerClass = cx({
    'star-rating-mobile__star-container': withContainer,
    'star-rating__star-container--disabled': disabled
  })

  const getEmptyStar = withContainer
    ? <span className='star-rating__star-item'>
      <span className='ion-md-star-outline' />
    </span>
    : <span className='ion-md-star-outline' />

  const getFullStar = withContainer
    ? <span className='star-rating__star-item'>
      <span className='ion-md-star' />
    </span>
    : <span className='ion-md-star' />

  return (
    <div className={starRatingClass}>
      <Label className='star-rating-mobile__label'>
        <span>{label}</span>
        {degreeDescription && value > 0 &&
        <span className='form__star-rating-degree'>{` - ${degreeDescription[value]}`}</span>}
      </Label>
      <div className={containerClass}>
        <Rating
          name={label}
          stop={starCount}
          initialRating={value}
          fullSymbol={fullStar || getFullStar}
          emptySymbol={emptyStar || getEmptyStar}
          quiet={quiet}
          readonly={disabled}
          direction={getLayoutDirection}
        />
      </div>
    </div>
  )
})



