import React from 'react'
import ProfilePreviewPhotoConnector from 'Candidate/connectors/ProfilePreviewHeader/ProfilePreviewPhotoConnector'
import ProfilePreviewIntroduction from 'Candidate/connectors/ProfilePreviewHeader/ProfilePreviewIntroductionConnector'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'

function ProfilePreviewHeaderMobile (props) {
  return (
    <ProfilePreviewSection>
      <div className='profile-preview-header-mobile'>
        <ProfilePreviewPhotoConnector />
        <ProfilePreviewIntroduction />
      </div>
    </ProfilePreviewSection>
  )
}

export default ProfilePreviewHeaderMobile



