import React from 'react'
import ProfilePreviewMoreActionsWeb from './ProfilePreviewMoreActions'
import ProfilePreviewMoreActionsMobile from './ProfileNavigationMoreActions.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewMoreActions = (props) => props.isMobile ? <ProfilePreviewMoreActionsMobile {...props} /> : <ProfilePreviewMoreActionsWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewMoreActions)



