import { validateProtocol, validateUrlWithProtocol } from 'Shared/helpers/string'

export default values => {
  const errors = {}

  const socialLinksFields = ['linkedin', 'instagram', 'twitter']

  socialLinksFields.forEach(field => {
    if (values[field] && !validateProtocol(values[field]) && validateUrlWithProtocol(values[field])) {
      errors[field] = {
        key: 'errors.invalidLink',
        options: {}
      }
    }
  })

  return errors
}



