import { reduxForm, formValueSelector } from 'redux-form'
import { connect } from 'react-redux'
import AddEducationForm from 'Candidate/forms/EditProfileSections/EducationSection/AddEducationForm'
import validate from './AddEducationFormValidator'
import { isFieldInvalid, getFieldErrorMessage } from 'Shared/selectors/reduxForm'
import { fetchMajors } from 'Shared/ducks/majors'
import { fetchEducationResources, fetchHighSchoolEducationGradingSystemIfNeeded } from 'Shared/ducks/resources'
import { fetchUniversitiesIfNeeded } from 'Shared/ducks/universities'
import { fetchHighSchoolsIfNeeded } from 'Shared/ducks/highSchools'
import { getUserEducationLevel } from 'Shared/selectors/user'
import { educationLevels } from 'Shared/constants/Candidates'
import { updateCandidate } from 'Shared/ducks/user/candidate'
import { addEducationDetails } from 'Shared/ducks/education'
import { addHighSchoolEducationDetails } from 'Shared/ducks/highSchoolLevelEducation'
import { toTimestamp } from 'Shared/helpers/date'

export const formName = 'AddEducationForm'

let Form = reduxForm({
  form: formName,
  enableReinitialize: true,
  validate,
  onSubmit: (values, dispatch) => {
    if (values.educationLevel) {
      dispatch(updateCandidate({ educationLevel: values.educationLevel }))
    }

    if (values.educationLevel === educationLevels.university && values.universities) {
      Promise.all(
        values.universities.map(universityFields => dispatch(addEducationDetails(universityFields)))
      )
    }

    if (values.educationLevel === educationLevels.highSchool && values.highSchools) {
      Promise.all(
        values.highSchools.map(highSchoolFields => dispatch(addHighSchoolEducationDetails({
          ...highSchoolFields,
          graduatedAt: toTimestamp(highSchoolFields.graduationYear, highSchoolFields.graduationMonth)
        })))
      )
    }
  }
})(AddEducationForm)

const mapStateToProps = state => ({
  initialValues: {
    educationLevel: getUserEducationLevel(state)
  },
  currentEducationLevel: getUserEducationLevel(state),
  selectedEducationLevel: formValueSelector(formName)(state, 'educationLevel'),
  hasEducationLevelError: isFieldInvalid(formName, 'educationLevel')(state),
  errorMessage: getFieldErrorMessage(formName, 'educationLevel')(state)
})

const mapDispatchToProps = dispatch => ({
  fetchHighSchoolEducationGradingSystem: () => dispatch(fetchHighSchoolEducationGradingSystemIfNeeded()),
  fetchEducationResources: () => dispatch(fetchEducationResources()),
  fetchMajors: () => dispatch(fetchMajors()),
  fetchUniversities: () => dispatch(fetchUniversitiesIfNeeded()),
  fetchHighSchools: () => dispatch(fetchHighSchoolsIfNeeded())
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



