import React from 'react'
import ProfilePreviewLanguagesElementMobile from './ProfilePreviewLanguagesElement.mobile'
import ProfilePreviewLanguagesElementWeb from './ProfilePreviewLanguagesElement'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewLanguagesElement = props => props.isMobile ? <ProfilePreviewLanguagesElementMobile {...props} /> : <ProfilePreviewLanguagesElementWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewLanguagesElement)



