import { reduxForm } from 'redux-form'
import AddCertificatesForm from 'Candidate/forms/EditProfileSections/CertificatesSection/AddCertificatesForm'
import validate from './AddCertificatesFormValidator'
import { addCertificate } from 'Shared/ducks/certificates'

export const formName = 'AddCertificatesForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmit: (values, dispatch) => Promise.all(
    values.certificates.map(certificateValues => dispatch(addCertificate(certificateValues, false)))
  )
})(AddCertificatesForm)

export default Form



