import React, { useEffect, useState } from 'react'
import { t } from 'i18next'
import './ProfilePreviewSocialList.scss'
import { BigJustIconButton } from 'Shared/components/Button'
import Modal from 'Shared/components/Modal'
import NotificationService from 'Shared/services/NotificationService'
import AddSocialLinksFormConnector from 'Candidate/connectors/Forms/EditProfileSections/WebsitesSection/AddSocialLinksFormConnector'
import cx from 'classnames'
import { getInstagramNickname, getLinkedinNickname, getTwitterNickname } from 'Shared/helpers/socialLinks'

const SocialLinkElement = (props) => (
  <div className='profile-preview-social-list__element'>
    <a target='_blank' className='profile-preview-social-list__element-link' href={props.url}>
      <i className={`profile-links__icon ${props.icon}`} />
      <span className='profile-preview-social-list__element-name'>{props.name}</span>
    </a>
  </div>
)

function ProfilePreviewSocialList (props) {
  const [addModalVisibility, setAddModalVisibility] = useState(false)

  const showAddModal = () => setAddModalVisibility(true)
  const hideAddModal = () => setAddModalVisibility(false)

  useEffect(() => {
    if (props.isSocialLinksFormSucceeded) {
      hideAddModal()
      NotificationService.success(t('alerts.updateSocialLinksSuccess'))
    }
  })

  const handleSubmit = () => props.submitSocialLinks()

  const linksClass = cx({
    'profile-preview-social-list__links-wrapper': props.hasAnySocialLink
  })

  return (
    <div>
      <div className={linksClass}>
        <div className='profile-preview-social-list__links'>
          { props.linkedIn.id && <SocialLinkElement url={props.linkedIn.url} name={getLinkedinNickname(props.linkedIn.url)} icon='ion-logo-linkedin' /> }
          { props.instagram.id && <SocialLinkElement url={props.instagram.url} name={getInstagramNickname(props.instagram.url)} icon='icon-im-instagram' /> }
          { props.twitter.id && <SocialLinkElement url={props.twitter.url} name={getTwitterNickname(props.twitter.url)} icon='icon-im-twitter' /> }
        </div>
        { props.hasAnySocialLink && <BigJustIconButton onClick={showAddModal}><i className='icon-im-edit' /></BigJustIconButton> }
      </div>
      <Modal
        title={t('modals.editSocialLinks.title')}
        show={addModalVisibility}
        handleClose={hideAddModal}
        handleSubmit={handleSubmit}
        handleCancel={hideAddModal}
        cancelText={t('common.cancel')}
        submitText={t('common.submit')}
        showContact={false}
        closeOnSubmit={false}
      >
        <div className='profile-preview-cv-documents__modal-margin'>
          <AddSocialLinksFormConnector />
        </div>
      </Modal>
    </div>
  )
}

export default ProfilePreviewSocialList



