import React from 'react'
import { Field } from 'redux-form'
import { t } from 'i18next'
import { Input, RatingStars, Select } from 'Shared/components/FormElements'
import { BigJustIconButton } from 'Shared/components/Button'
import { degree } from 'Shared/constants/Languages'

const RatingField = ({ label, name }) => (
  <div className='column col-4 col-xs-12'>
    <Field
      component={RatingStars}
      label={label}
      name={name}
      starCount={3}
      quiet
      degreeDescription={degree()}
      withContainer
    />
  </div>
)

function LanguageFormFields (props) {
  return (
    <div className='form-group'>
      <Field
        component={Select}
        label={t('forms.language.label')}
        options={props.languageOptions}
        placeholder={t('forms.language.placeholder')}
        name={`${props.language}.languageCode`}
        withIcon
      />
      <div className='form__group'>
        <div className='columns'>
          <RatingField label='spoken' name={`${props.language}.speaking`} />
          <RatingField label='written' name={`${props.language}.writing`} />
          <RatingField label='read' name={`${props.language}.reading`} />
        </div>
      </div>
      <Field
        component={Input}
        name={`${props.language}.candidateId`}
        type='hidden'
      />
      <Field
        component={Input}
        name={`${props.language}.id`}
        type='hidden'
      />
      <div className='form__wrapper--button-group'>
        <BigJustIconButton onClick={props.removeField}><i className='ion-md-trash' /></BigJustIconButton>
      </div>
    </div>
  )
}

export default LanguageFormFields



