import React, { useEffect } from 'react'
import { Form, Field, FieldArray } from 'redux-form'
import { Radio, RadioWrapper } from 'Shared/components/FormElements'
import { educationLevels } from 'Shared/constants/Candidates'
import UniversitiesFormFieldArrayConnector from 'Candidate/connectors/Forms/EditProfileSections/EducationSection/UniversitiesFormFieldArrayConnector'
import HighSchoolsFormFieldArrayConnector from 'Candidate/connectors/Forms/EditProfileSections/EducationSection/HighSchoolsFormFieldArrayConnector'
import { t } from 'i18next'

function AddEducationForm (props) {
  useEffect(() => {
    props.fetchMajors()
    props.fetchEducationResources()
    props.fetchUniversities()
    props.fetchHighSchoolEducationGradingSystem()
    props.fetchHighSchools()
  }, [])

  return (
    <Form onSubmit={props.handleSubmit}>
      {
        props.currentEducationLevel === educationLevels.highSchool && (
          <div className='form__group'>
            <RadioWrapper
              legend={t('candidate.completeProfile.education.educationLevelLabel')}
              hasError={props.hasEducationLevelError}
              error={props.errorMessage}
              name='educationLevel'
            >
              <Field
                component={Radio}
                text={t('common.highSchool')}
                name='educationLevel'
                radioButtonValue={educationLevels.highSchool}
              />
              <Field
                component={Radio}
                text={t('common.university')}
                brackets={t('candidate.completeProfile.education.studentOrGraduate')}
                name='educationLevel'
                radioButtonValue={educationLevels.university}
              />
            </RadioWrapper>
          </div>
        )
      }
      <div className='form__group'>
        {
          props.selectedEducationLevel === educationLevels.highSchool
            ? <FieldArray name='highSchools' formName={props.form} component={HighSchoolsFormFieldArrayConnector} />
            : <FieldArray name='universities' formName={props.form} component={UniversitiesFormFieldArrayConnector} />
        }
      </div>
    </Form>
  )
}

export default AddEducationForm



