import { connect } from 'react-redux'
import ProfilePreviewCvDocuments from 'Candidate/components/ProfilePreviewSections/ProfilePreviewCvDocuments'
import { getCandidateCvFiles, getAllCandidateFiles } from 'Shared/selectors/files'
import { uploadFileToS3, generateS3Signature } from 'Shared/ducks/files'
import { UPLOAD_DOCUMENT_TYPES } from 'Shared/constants/Files'

const mapStateToProps = state => ({
  cvDocuments: getCandidateCvFiles(state),
  allFiles: getAllCandidateFiles()(state),
  isFileLoading: state.files.isLoading
})

const mapDispatchToProps = dispatch => ({
  generateSignature: () => dispatch(generateS3Signature()),
  uploadCvFile: (file, policy, signature) => dispatch(uploadFileToS3(file, policy, signature, UPLOAD_DOCUMENT_TYPES.CV))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewCvDocuments)



