import React, { Component } from 'react'
import { Field } from 'redux-form'
import { Input, InputWrapper, Select } from 'Shared/components/FormElements'
import Button from 'Shared/components/Button'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import withTranslations from 'Shared/hoc/withTranslations'
import { websiteTypeOptions } from 'Shared/constants/Links'

class WebsitesLinksArrayForm extends Component {
  _addNewLink = () => this.props.fields.push({type: null, url: null, id: null})
  _removeLink = index => () => {
    const { fields, onDelete } = this.props
    const linkId = fields.get(index).id

    return linkId ? onDelete(linkId) : fields.remove(index)
  }

  render () {
    const { t, fields, showOptionalLabel, showLabel } = this.props
    const optionalLabelProps = showOptionalLabel ? { optional: t('common.optional') } : {}
    const labelProps = showLabel ? { label: t('common.websites') } : {}

    return (
      <div>
        <InputWrapper {...optionalLabelProps} {...labelProps}>
          {fields.map((website, index) => {
            return (
              <div className='form__links-group' key={index}>
                <div className='form__links--fields'>
                  <Field
                    component={Select}
                    options={websiteTypeOptions()}
                    placeholder={t('profile.additional.sections.websites.selectType')}
                    name={`${website}.type`}
                    withIcon
                  />
                  <Field
                    component={Input}
                    iconClassName='icon-im-linkedin'
                    name={`${website}.url`}
                    className='form__links-group--input'
                    placeholder={t('profile.additional.sections.websites.placeholder')}
                    disableGroup
                  />
                  <Field
                    component={Input}
                    name={`${website}.id`}
                    type='hidden'
                  />
                  <Field
                    component={Input}
                    name={`${website}.category`}
                    type='hidden'
                  />
                </div>
                <Button onClick={this._removeLink(index)} justIcon className='form__links-group--delete-btn'><i className='ion-md-trash' /></Button>
              </div>
            )
          })}
        </InputWrapper>
        <AddNewSectionButton text={t('candidate.profile.websites.addWebsites')} onClick={this._addNewLink} />
      </div>
    )
  }
}

WebsitesLinksArrayForm.defaultProps = {
  showOptionalLabel: true,
  showLabel: true
}

export default withTranslations(WebsitesLinksArrayForm)



