import { reduxForm, formValueSelector } from 'redux-form'
import { isFieldInvalid } from 'Shared/selectors/reduxForm'
import { connect } from 'react-redux'
import GeneralInfoForm from 'Candidate/forms/EditProfileSections/GeneralInfoSection/GeneralInfoForm'
import validate from './GeneralInfoFormValidator'
import { updateCandidate } from 'Shared/ducks/user/candidate'
import { nationalityOptions } from 'Shared/selectors/resources'
import { visaStatusesOptions } from 'Shared/constants/VisaStatuses'
import { isSensitiveMarket } from 'Shared/selectors/geolocation'
import {
  userDateOfBirth,
  userOwnsACar,
  userDriversLicence,
  userGender,
  userNationalityCode,
  userVisaStatus
} from 'Shared/selectors/user'

export const formName = 'GeneralInfoForm'

let Form = reduxForm({
  form: formName,
  validate,
  enableReinitialize: true,
  onSubmit: (values, dispatch) => dispatch(updateCandidate(values))
})(GeneralInfoForm)

const mapStateToProps = state => ({
  impersonation: state.user.impersonation,
  hasGenderFieldError: isFieldInvalid(formName, 'gender')(state),
  driversLicence: formValueSelector(formName)(state, 'driversLicence'),
  nationalityOptions: nationalityOptions(state),
  visaStatusesOptions: visaStatusesOptions(),
  isSensitiveMarket: isSensitiveMarket(state),
  initialValues: {
    dateOfBirth: userDateOfBirth(state),
    gender: userGender(state),
    nationality: userNationalityCode(state),
    visaStatus: userVisaStatus(state),
    driversLicence: userDriversLicence(state),
    ownsACar: userOwnsACar(state)
  }
})

export default connect(mapStateToProps)(Form)



