import { connect } from 'react-redux'
import { formValueSelector } from 'redux-form'
import { isFieldInvalid } from 'Shared/selectors/reduxForm'
import CertificatesFormFieldArray from 'Candidate/forms/EditProfileSections/CertificatesSection/CertificatesFormFieldArray'
import { t } from 'i18next'

const mapStateToProps = (state, ownProps) => ({
  getCertificateName: certificateField => formValueSelector(ownProps.formName)(state, `${certificateField}.name`) || t('profile.education.sections.additionalCertifications.addNew'),
  isFieldArrayInvalid: certificateField => isFieldInvalid(ownProps.formName, certificateField)(state)
})

export default connect(mapStateToProps)(CertificatesFormFieldArray)



