import { connect } from 'react-redux'
import ProfilePreviewAdditionalDocumentsElement from 'Candidate/components/ProfilePreviewSections/ProfilePreviewAdditionalDocuments/components/ProfilePreviewAdditionalDocumentsElement'
import { getDocumentById } from 'Shared/selectors/files'
import { deleteCandidateFileById } from 'Shared/ducks/files'

const mapStateToProps = (state, ownProps) => ({
  document: getDocumentById(state, ownProps.id)
})

const mapDispatchToProps = dispatch => ({
  deleteFile: (fileId, name) => dispatch(deleteCandidateFileById(fileId, name))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewAdditionalDocumentsElement)



