import { connect } from 'react-redux'
import ProfilePreviewLanguagesElement from 'Candidate/components/ProfilePreviewSections/ProfilePreviewLanguages/components/ProfilePreviewLanguagesElement'
import { getLanguageByCode } from 'Shared/selectors/languages'
import { deleteCandidateLanguage } from 'Shared/ducks/languages'

const mapStateToProps = (state, ownProps) => ({
  languageName: getLanguageByCode(state, ownProps.language.languageCode)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  deleteLanguage: () => dispatch(deleteCandidateLanguage(ownProps.language.id))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewLanguagesElement)



