import { connect } from 'react-redux'
import ProfilePreviewVolunteeringElement from 'Candidate/components/ProfilePreviewSections/ProfilePreviewVolunteering/components/ProfilePreviewVolunteeringElement'
import {
  getExperienceDetails,
  getExperiencePosition,
  getExperienceOrganisationName,
  getExperienceDates,
  getExperienceDescription,
  hasOneVolunteering
} from 'Shared/selectors/experience'
import { deleteExperience } from 'Shared/ducks/experiences'

const mapStateToProps = (state, ownProps) => ({
  volunteeringDetails: getExperienceDetails(state, ownProps.id),
  title: getExperiencePosition(state, ownProps.id),
  organisationName: getExperienceOrganisationName(state, ownProps.id),
  dates: getExperienceDates(state, ownProps.id),
  description: getExperienceDescription(state, ownProps.id),
  hasOneVolunteering: hasOneVolunteering(state)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  deleteVolunteering: () => dispatch(deleteExperience(ownProps.id))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewVolunteeringElement)



