import { connect } from 'react-redux'
import ProfilePreviewCvDocumentsList from 'Candidate/components/ProfilePreviewSections/ProfilePreviewCvDocuments/components/ProfilePreviewCvDocumentsList'
import { getCandidateCvFiles } from 'Shared/selectors/files'

const mapStateToProps = state => ({
  cvDocuments: getCandidateCvFiles(state)
})

const mapDispatchToProps = dispatch => ({})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewCvDocumentsList)



