import React from 'react'
import ProfilePreviewWebsitesElementMobile from './ProfilePreviewWebsitesElement.mobile'
import ProfilePreviewWebsitesElementWeb from './ProfilePreviewWebsitesElement'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewWebsitesElement = props => props.isMobile ? <ProfilePreviewWebsitesElementMobile {...props} /> : <ProfilePreviewWebsitesElementWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewWebsitesElement)



