import React, { useEffect, useState } from 'react'
import { t } from 'i18next'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton/index'
import Collapse, { Panel } from 'rc-collapse'
import HighSchoolFormFieldsConnector from 'Candidate/connectors/Forms/EditProfileSections/EducationSection/HighSchoolFormFieldsConnector'
import 'Shared/assets/scss/vendor/rc-collapse-form.scss'
import cx from 'classnames'
import { hasEmptyFieldArray } from 'Shared/helpers/forms'

function EducationFormHighSchoolsFieldArray (props) {
  const [accordionActiveKey, setAccordionActiveKey] = useState(null)

  const addNewField = () => props.fields.push({
    id: null,
    highSchoolId: null,
    educationSystem: null,
    specialisation: null,
    graduationMonth: null,
    graduationYear: null
  })

  useEffect(() => {
    hasEmptyFieldArray(props) && addNewField()
  }, [])

  useEffect(() => {
    if (props.fields.length > 0) {
      setAccordionActiveKey(props.fields.length.toString())
    }
  }, [props.fields.length])

  const removeField = index => () => {
    const { fields, onDelete } = props
    const itemId = fields.get(index).id
    return itemId ? onDelete(itemId) : fields.remove(index)
  }

  const EducationFormHighSchoolsField = (highSchool, index) => {
    const PanelClass = cx({
      'form-fields__collapse-panel-error': props.isFieldArrayInvalid(highSchool)
    })

    return (
      <Panel className={PanelClass} key={index + 1} header={props.getHighSchoolName(props.getHighSchoolId(highSchool))}>
        <HighSchoolFormFieldsConnector
          removeField={removeField(index)}
          formName={props.formName}
          highSchool={highSchool}
        />
      </Panel>
    )
  }

  const getFields = () => props.fields.map(EducationFormHighSchoolsField)

  return (
    <div>
      <Collapse
        accordion
        className='form-fields__collapse'
        activeKey={accordionActiveKey}
        onChange={setAccordionActiveKey}
      >
        {getFields()}
      </Collapse>
      <AddNewSectionButton
        text={t('profile.education.sections.highSchoolEducationDetails.addNew')}
        onClick={addNewField}
      />
    </div>
  )
}

export default EducationFormHighSchoolsFieldArray



