import { connect } from 'react-redux'
import ProfilePreviewEducationUniversities from 'Candidate/components/ProfilePreviewSections/ProfilePreviewEducation/components/ProfilePreviewEducationUniversities'
import { userEducation } from 'Shared/selectors/education'

const mapStateToProps = state => ({
  educationList: userEducation(state)
})

export default connect(mapStateToProps)(ProfilePreviewEducationUniversities)



