import React, { useState } from 'react'
import { t } from 'i18next'
import { BigJustIconButton } from 'Shared/components/Button'
import Modal from 'Shared/components/Modal'
import DeleteModal from 'Shared/components/DeleteModal'
import Collapse, { Panel } from 'rc-collapse'
import { ExperienceDetailsFormConnector } from 'Candidate/connectors/Forms/Profile/Experience'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import './ProfilePreviewExperienceElement.scss'

function ProfilePreviewExperienceElement (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)
  const [deleteModalVisibility, setDeleteModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  const showDeleteModal = () => setDeleteModalVisibility(true)
  const hideDeleteModal = () => setDeleteModalVisibility(false)

  return (
    <div className='profile-preview-experience-element__container'>
      <div className='profile-preview-experience-element'>
        <div>
          <div className='profile-preview-experience-element__header'>{props.title}</div>
          <div className='profile-preview-experience-element__text'>{props.organisationName}</div>
          <div className='profile-preview-experience-element__text'>{props.dates}</div>
          <div className='profile-preview-experience-element__text'>{parseHtmlWithoutSpan(props.description)}</div>
        </div>
        <BigJustIconButton className='profile-preview-experience-element__edit-button' onClick={showEditModal}><i className='icon-im-edit' /></BigJustIconButton>
      </div>
      <Modal
        title={t('modals.editExperience.title')}
        show={editModalVisibility}
        handleClose={hideEditModal}
        handleCancel={hideEditModal}
        cancelText={t('common.cancel')}
        showContact={false}
        closeOnSubmit={false}
        showSubmit={false}
        showCancel
      >
        <Collapse defaultActiveKey={'99'} activeKey={'99'} className='education-details__collapse'>
          <Panel key={'99'} header={props.organisationName}>
            <ExperienceDetailsFormConnector
              closeOnSubmit={hideEditModal}
              handleDelete={showDeleteModal}
              details={{ ...props.experienceDetails, ...props.organisationName }}
              disableDelete={props.hasOneWorkExperience}
            />
          </Panel>
        </Collapse>
        <DeleteModal
          title={t('remove.experienceDetails.title')}
          content={t('remove.experienceDetails.description')}
          handleDelete={props.deleteExperience}
          isModalVisible={deleteModalVisibility}
          hideDeleteModal={hideDeleteModal}
        />
      </Modal>
    </div>
  )
}

export default ProfilePreviewExperienceElement



