import { connect } from 'react-redux'
import ProfilePreviewLanguagesList from 'Candidate/components/ProfilePreviewSections/ProfilePreviewLanguages/components/ProfilePreviewLanguagesList'
import { getUserLanguages } from 'Shared/selectors/languages'

const mapStateToProps = state => ({
  userLanguages: getUserLanguages(state)
})

export default connect(mapStateToProps)(ProfilePreviewLanguagesList)



