import React from 'react'
import * as R from 'ramda'
import ProfilePreviewLanguageElementConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewLanguagesElementConnector'
import './ProfilePreviewLanguagesList.scss'

const renderLanguageElement = language => <ProfilePreviewLanguageElementConnector language={language} key={language.languageCode} />

const LanguageList = (props) => R.map(
  renderLanguageElement
)(props.userLanguages)

function ProfilePreviewLanguageList (props) {
  return (
    <div>
      <LanguageList userLanguages={props.userLanguages} />
    </div>
  )
}

export default ProfilePreviewLanguageList



