import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { ConfirmPopup } from 'Shared/components/Popup'
import Label from 'Shared/components/FormElements/Label/Label'
import SwitchButton from 'Shared/components/SwitchButton/SwitchButton'
import { ShareMediaList } from 'Shared/components/ShareDropdown'
import Button from 'Shared/components/Button'
import { generatePublicProfileRoute } from 'Shared/helpers/routes'
import { t } from 'i18next'
import './ProfilePreviewMoreActions.scss'

export function ProfileVisibilitySection (props) {
  const [visibility, setVisibility] = useState(props.userProfileVisibility)

  const handleOnChange = async () => {
    await props.changeVisibility(!visibility)
    setVisibility(!visibility)
  }

  return (
    <div>
      <Label>{t('candidate.profile.visibility.label')}</Label>
      <div className='profile-preview-more-actions__section-description'>
        {t('candidate.profile.visibility.description')}
      </div>
      <div className='profile-preview-more-actions__toggle-container'>
        {t('common.private')} <SwitchButton isChecked={visibility} onChange={handleOnChange} /> {t('common.public')}
      </div>
      <div className='profile-preview-more-actions__section-link'>
        <Link target='_blank' to={props.profileUrl}>{t('candidate.profile.visibility.preview')}</Link>
      </div>
    </div>
  )
}

export function ShareProfileSection (props) {
  return (
    <div className='profile-preview-more-actions__section-socials'>
      <Label>{t('candidate.profile.share.label')}</Label>
      <div>
        <ShareMediaList shareUrl={props.profileUrl} title={t('candidate.shareProfile')} />
      </div>
    </div>
  )
}

function ProfilePreviewMoreActions (props) {
  const profileUrl = generatePublicProfileRoute(props.firstName, props.lastName, props.id)

  return (
    <ConfirmPopup
      position='bottom'
      align='right'
      triggerButton={<Button className='profile-preview-more-actions__btn' withIcon><i className='icon-im-more' /><span>{t('candidate.profile.reachMoreEmployers')}</span></Button>}
      className='profile-preview-more-actions__popup'
    >
      <ProfileVisibilitySection profileUrl={profileUrl} userProfileVisibility={props.userProfileVisibility} changeVisibility={props.changeVisibility} />
      <div className='profile-preview-more-actions__divider' />
      <ShareProfileSection profileUrl={profileUrl} />
    </ConfirmPopup>
  )
}

export default ProfilePreviewMoreActions



