import { isNil, isEmpty } from 'ramda'
import startCase from 'lodash/startCase'

export default values => {
  const errors = {}

  const nonNullableFields = [
    'firstName',
    'lastName',
    'countryOfResidence',
    'phoneNumber',
    'city',
    'dateOfBirth'
  ]

  nonNullableFields.forEach(field => {
    if (isNil(values[field]) || isEmpty(values[field])) {
      errors[field] = {
        key: 'errors.requiredField',
        options: {
          field: startCase(field)
        }
      }
    }
  })

  return errors
}



