import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import EditSingleWebsiteForm from 'Candidate/forms/EditProfileSections/WebsitesSection/EditSingleWebsiteForm'
import validate from './EditSingleWebsiteFormValidator'
import { updateCandidateWebsites } from 'Shared/ducks/links'

export const formName = 'EditSingleWebsiteForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmit: (values, dispatch) => {
    const preparePayload = { websites: [{ url: values.url, type: values.type, id: values.id }] }
    return dispatch(updateCandidateWebsites(preparePayload))
  }
})(EditSingleWebsiteForm)

const mapStateToProps = (state, ownProps) => {
  const dynamicFormName = `${formName}-${ownProps.website.id}`

  return ({
    form: dynamicFormName,
    initialValues: {
      url: ownProps.website.url,
      type: ownProps.website.type,
      id: ownProps.website.id
    }
  })
}

export default connect(mapStateToProps)(Form)



