import React from 'react'
import cx from 'classnames'
import './ProfilePreviewSection.scss'

function ProfilePreviewSectionMobile (props) {
  const containerClass = cx({
    'profile-preview-section-mobile': true,
    'profile-preview-section-mobile--wrapped': props.isWrapped
  })

  return (
    <div className={containerClass}>
      { props.header && <div className='profile-preview-section-mobile__header'>{props.header}</div> }
      <div>
        {props.children}
      </div>
    </div>
  )
}

export default ProfilePreviewSectionMobile



