import { createSelector } from 'reselect'
import { transformToArray } from 'Shared/helpers/redux'
import filter from 'lodash/filter'
import find from 'lodash/find'
import { linkNullObject, linkCategories } from 'Shared/constants/Links'
import { getUserId } from 'Shared/selectors/user'
import * as R from 'ramda'

export const getLinkById = state => state.links.entries

export const getCandidateLinkType = (id, type) =>
  createSelector(
    getLinkById,
    getLinkById => find(transformToArray(getLinkById), { candidateId: id, type: type }) || linkNullObject
  )

export const getCandidateGeneralLinks = id =>
  createSelector(
    getLinkById,
    getLinkById => filter(transformToArray(getLinkById), { candidateId: id, category: linkCategories.general })
  )

export const getCandidateLinksList = candidateId =>
  createSelector(
    getLinkById,
    getLinkById => filter(transformToArray(getLinkById), { candidateId })
  )

export const getUserWebsites = createSelector(
  getLinkById,
  getUserId,
  (links, userId) => R.pipe(
    transformToArray,
    R.filter(R.allPass([
      R.propEq('candidateId', userId),
      R.propEq('category', linkCategories.general)
    ]))
  )(links)
)

export const getLinkDetailsById = createSelector(
  getLinkById,
  (state, id) => id,
  (links, id) => R.propOr({}, id)(links)
)

export const hasAnySocialLink = createSelector(
  getLinkById,
  R.pipe(
    transformToArray,
    R.filter(R.propEq('category', linkCategories.social)),
    R.isEmpty,
    R.not
  )
)

export const getCandidateWebsites = createSelector(
  getLinkById,
  (state, id) => id,
  (links, userId) => R.pipe(
    transformToArray,
    R.filter(R.allPass([
      R.propEq('candidateId', userId),
      R.propEq('category', linkCategories.general)
    ]))
  )(links)
)

export const hasCandidateAnySocialLink = createSelector(
  getLinkById,
  (state, id) => id,
  (links, userId) => R.pipe(
    transformToArray,
    R.filter(R.allPass([
      R.propEq('candidateId', userId),
      R.propEq('category', linkCategories.social)
    ])),
    R.isEmpty,
    R.not
  )(links)
)

export const hasCandidateAnyWebsites = createSelector(
  getLinkById,
  (state, id) => id,
  (links, userId) => R.pipe(
    transformToArray,
    R.filter(
      R.propEq('candidateId', userId)
    ),
    R.isEmpty,
    R.not
  )(links)
)



