import { connect } from 'react-redux'
import { hasSubmitSucceeded } from 'redux-form'
import ProfilePreviewWebsitesElement from 'Candidate/components/ProfilePreviewSections/ProfilePreviewWebsites/components/ProfilePreviewWebsitesElement'
import { getLinkDetailsById } from 'Shared/selectors/links'
import { deleteCandidateLink } from 'Shared/ducks/links'
import { formName } from 'Candidate/connectors/Forms/EditProfileSections/WebsitesSection/EditSingleWebsiteFormConnector'

const mapStateToProps = (state, ownProps) => {
  const getDynamicFormName = `${formName}-${ownProps.id}`

  return ({
    website: getLinkDetailsById(state, ownProps.id),
    hasUpdateSucceeded: hasSubmitSucceeded(getDynamicFormName)(state)
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  deleteWebsite: () => dispatch(deleteCandidateLink(ownProps.id))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewWebsitesElement)



