import React from 'react'
import * as R from 'ramda'
import ProfilePreviewVolunteeringElementConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewVolunteeringElementConnector'
import './ProfilePreviewVolunteeringList.scss'

const renderVolunteeringElement = volunteering => <ProfilePreviewVolunteeringElementConnector id={volunteering.id} key={volunteering.id} />

const VolunteeringList = (props) => R.map(
  renderVolunteeringElement
)(props.volunteeringList)

function ProfilePreviewVolunteeringList (props) {
  return (
    <div>
      <VolunteeringList volunteeringList={props.volunteeringList} />
    </div>
  )
}

export default ProfilePreviewVolunteeringList



