import React, { useState, useEffect } from 'react'
import { t } from 'i18next'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import AddExperienceFormConnector from 'Candidate/connectors/Forms/EditProfileSections/ExperienceSection/AddExperienceFormConnector'
import ProfilePreviewExperienceListConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewExperienceListConnector'
import Modal from 'Shared/components/Modal'
import './ProfilePreviewExperience.scss'
import NotificationService from 'Shared/services/NotificationService'

function ProfilePreviewExperience (props) {
  const [addModalVisibility, setAddModalVisibility] = useState(false)

  useEffect(() => {
    if (props.hasAddExperienceSucceeded) {
      hideAddModal()
      NotificationService.success(t('alerts.updateExperienceSuccess'))
    }
  }, [props.hasAddExperienceSucceeded])

  const showAddModal = () => setAddModalVisibility(true)
  const hideAddModal = () => setAddModalVisibility(false)

  return (
    <ProfilePreviewSection
      header={t('profile.experience.title')}
    >
      <ProfilePreviewExperienceListConnector />
      <div className='profile-preview-experience'>
        <AddNewSectionButton
          text={t('candidate.profile.experience.addExperience')}
          onClick={showAddModal}
        />
        <Modal
          title={t('modals.addExperience.title')}
          show={addModalVisibility}
          handleClose={hideAddModal}
          handleCancel={hideAddModal}
          handleSubmit={props.submitAddExperienceForm}
          cancelText={t('common.cancel')}
          submitText={t('common.submit')}
          showContact={false}
          closeOnSubmit={false}
          showCancel
        >
          <AddExperienceFormConnector />
        </Modal>
      </div>
    </ProfilePreviewSection>
  )
}

export default ProfilePreviewExperience



