import startCase from 'lodash/startCase'
import replace from 'lodash/replace'
import { makeRequiredFieldError } from 'Shared/helpers/forms'

export default values => {
  const errors = {}
  const languagesErrors = []

  const requiredCertificateFields = ['writing', 'speaking', 'reading']

  if (values.languages) {
    values.languages.forEach((language, index) => {
      const languageErrors = {}

      requiredCertificateFields.forEach(field => {
        if (!language[field]) {
          languageErrors[field] = makeRequiredFieldError(startCase(field))
          languagesErrors[index] = languageErrors
        }
      })

      if (!language['languageCode']) {
        languageErrors['languageCode'] = makeRequiredFieldError(replace('languageCode', 'Code', ''))
      }
    })
  }

  if (languagesErrors.length) {
    errors.languages = languagesErrors
  }

  return errors
}



