import ProfileRatingWeb from './ProfileRating'
import ProfileRatingMobile from './ProfileRating.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

import React from 'react'

const ProfileRating = (props) => props.isMobile ? <ProfileRatingMobile {...props} /> : <ProfileRatingWeb {...props} />

export default withDeviceWidthDetector(ProfileRating)



