const mapStateToProps = state => ({
  documents: getCandidateNonCvFiles(state)
})

const mapDispatchToProps = dispatch => ({})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewAdditionalDocumentsList)



