import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import ProfilePreviewWebsites from 'Candidate/components/ProfilePreviewSections/ProfilePreviewWebsites'
import { formName } from 'Candidate/connectors/Forms/EditProfileSections/WebsitesSection/AddWebsitesFormConnector'

const mapStateToProps = state => ({
  isWebsitesFormSucceeded: hasSubmitSucceeded(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  submitWebsites: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewWebsites)



