import React, { Component } from 'react'
import { Field, Form } from 'redux-form'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations'
import { MultiSelect } from 'Shared/components/FormElements'

class InterestsForm extends Component {
  static propTypes = {
    interests: PropTypes.arrayOf(PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string
    })).isRequired
  }

  async componentDidMount () {
    await this.props.fetchInterests()
  }

  render () {
    const { t, handleSubmit, submitForm } = this.props
    return (
      <Form className='form__group' onSubmit={handleSubmit(submitForm)}>
        <Field
          component={MultiSelect}
          options={this.props.interests}
          placeholder={t('forms.interests.placeholder')}
          name='interests'
          withIcon
        />
      </Form>
    )
  }
}

export default withTranslations(InterestsForm)



