import React from 'react'
import ProfilePreviewPhotoConnector from 'Candidate/connectors/ProfilePreviewHeader/ProfilePreviewPhotoConnector'
import ProfilePreviewIntroduction from 'Candidate/connectors/ProfilePreviewHeader/ProfilePreviewIntroductionConnector'
import ProfilePreviewMoreActionsConnector from 'Candidate/connectors/ProfilePreviewHeader/ProfilePreviewMoreActionsConnector'
import ProfilePreviewAvailabilityConnector from 'Candidate/connectors/ProfilePreviewHeader/ProfilePreviewAvailabilityConnector'
import './ProfilePreviewHeader.scss'

function ProfilePreviewHeader () {
  return (
    <div className='profile-preview-header'>
      <div className='profile-preview-header__introduction-wrapper'>
        <ProfilePreviewPhotoConnector />
        <div className='profile-preview-header__introduction'>
          <ProfilePreviewIntroduction />
        </div>
      </div>
      <div className='profile-preview-header__aside'>
        <ProfilePreviewMoreActionsConnector />
        <ProfilePreviewAvailabilityConnector />
      </div>
    </div>
  )
}

export default ProfilePreviewHeader



