import { connect } from 'react-redux'
import { formValueSelector } from 'redux-form'
import HighSchoolsFormFieldArray from 'Candidate/forms/EditProfileSections/EducationSection/HighSchoolsFormFieldArray'
import { getHighSchoolNameOrLabel } from 'Shared/selectors/highSchools'
import { isFieldInvalid } from 'Shared/selectors/reduxForm'

const mapStateToProps = (state, ownProps) => ({
  getHighSchoolName: id => getHighSchoolNameOrLabel(state, id),
  getHighSchoolId: highSchoolField => formValueSelector(ownProps.formName)(state, `${highSchoolField}.highSchoolId`),
  isFieldArrayInvalid: highSchoolField => isFieldInvalid(ownProps.formName, highSchoolField)(state)
})

export default connect(mapStateToProps)(HighSchoolsFormFieldArray)



