import React, { useState } from 'react'
import { t } from 'i18next'
import { BigJustIconButton } from 'Shared/components/Button'
import Modal from 'Shared/components/Modal'
import DeleteModal from 'Shared/components/DeleteModal'
import Collapse, { Panel } from 'rc-collapse'
import { CertificateDetailsFormConnector } from 'Candidate/connectors/Forms/Profile/Education'
import './ProfilePreviewCertificatesElement.scss'

function ProfilePreviewExperienceElement (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)
  const [deleteModalVisibility, setDeleteModalVisibility] = useState(false)

  const showEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  const showDeleteModal = () => setDeleteModalVisibility(true)
  const hideDeleteModal = () => setDeleteModalVisibility(false)

  return (
    <div className='column col-6 profile-preview-certificates-element__container'>
      <div className='profile-preview-certificates-element'>
        <div>
          <div className='profile-preview-certificates-element__header'>{props.title}</div>
          <div className='profile-preview-certificates-element__text'>{props.issuer}</div>
          <div className='profile-preview-certificates-element__text'>{props.dates}</div>
        </div>
        <BigJustIconButton className='profile-preview-certificates-element__edit-button' onClick={showEditModal}><i className='icon-im-edit' /></BigJustIconButton>
      </div>
      <Modal
        title={t('modals.editCertificate.title')}
        show={editModalVisibility}
        handleClose={hideEditModal}
        handleCancel={hideEditModal}
        cancelText={t('common.cancel')}
        showContact={false}
        closeOnSubmit={false}
        showSubmit={false}
        showCancel
      >
        <Collapse defaultActiveKey={'99'} activeKey={'99'} className='education-details__collapse'>
          <Panel key={'99'} header={props.title}>
            <CertificateDetailsFormConnector
              closeOnSubmit={hideEditModal}
              handleDelete={showDeleteModal}
              details={props.certificateDetails}
            />
          </Panel>
        </Collapse>
        <DeleteModal
          title={t('remove.certificates.title')}
          content={t('remove.certificates.description')}
          handleDelete={props.deleteCertificate}
          isModalVisible={deleteModalVisibility}
          hideDeleteModal={hideDeleteModal}
        />
      </Modal>
    </div>
  )
}

export default ProfilePreviewExperienceElement



