import { connect } from 'react-redux'
import { hasSubmitSucceeded, submit } from 'redux-form'
import { deleteHighSchoolEducationDetailsById, fetchCandidateHighSchoolEducation } from 'Shared/ducks/highSchoolLevelEducation'
import { deleteEducationDetailsById } from 'Shared/ducks/education'
import { getEducationHighSchoolDetails } from 'Shared/selectors/highSchoolLevelEducation'
import ProfilePreviewEducation from 'Candidate/components/ProfilePreviewSections/ProfilePreviewEducation'
import { getUserId, isHighSchoolLevelEducation } from 'Shared/selectors/user'
import { fetchEducationResources } from 'Shared/ducks/resources'
import { formName } from 'Candidate/connectors/Forms/EditProfileSections/EducationSection/AddEducationFormConnector'

const mapStateToProps = state => ({
  isHighSchoolLevelEducation: isHighSchoolLevelEducation(state),
  educationHighSchoolList: getEducationHighSchoolDetails(state),
  userId: getUserId(state),
  hasAddEducationSucceeded: hasSubmitSucceeded(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  fetchEducationResources: () => dispatch(fetchEducationResources()),
  fetchCandidateHighSchoolEducation: candidateId => dispatch(fetchCandidateHighSchoolEducation(candidateId)),
  deleteEducationDetailsById: id => dispatch(deleteEducationDetailsById(id)),
  deleteHighSchoolEducationDetailsById: id => dispatch(deleteHighSchoolEducationDetailsById(id)),
  submitAddEducation: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewEducation)



