import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import ProfilePreviewExperience from 'Candidate/components/ProfilePreviewSections/ProfilePreviewExperience'
import { formName } from 'Candidate/connectors/Forms/EditProfileSections/ExperienceSection/AddExperienceFormConnector'

const mapStateToProps = state => ({
  hasAddExperienceSucceeded: hasSubmitSucceeded(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  submitAddExperienceForm: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewExperience)



