import React from 'react'
import { t } from 'i18next'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import { VideoUploader } from 'Shared/components/FormElements'

function ProfilePreviewVideoCv (props) {
  return (
    <ProfilePreviewSection
      header={t('profile.media.sections.videoCV.label')}
    >
      <VideoUploader
        uploadVideo={props.uploadVideoCv}
        deleteVideo={props.deleteVideoCv}
        videoCvUrl={props.videoCvUrl}
        uploading={props.isVideoUploading}
        size='big'
      />
    </ProfilePreviewSection>
  )
}

export default ProfilePreviewVideoCv



