import startCase from 'lodash/startCase'
import { getEditorContentLength } from 'Shared/helpers/editor'
import { lengthLimit } from 'Shared/constants/Editor'

export default values => {
  const errors = {}
  const experiencesErrors = []

  const requiredExperienceFields = ['organisationName', 'position', 'startedAt', 'description']

  if (values.experiences) {
    values.experiences.forEach((experience, index) => {
      const experienceErrors = {}

      requiredExperienceFields.forEach(field => {
        if (!experience[field]) {
          experienceErrors[field] = {
            key: 'errors.requiredField',
            options: {
              field: startCase(field)
            }
          }
          experiencesErrors[index] = experienceErrors
        }
      })

      if (!experience['finishedAt'] && !experience['currentWork']) {
        experienceErrors['finishedAt'] = {
          key: 'errors.requiredField',
          options: {
            field: startCase('finishedAt')
          }
        }
        experiencesErrors[index] = experienceErrors
      }

      if (experience['description'] && getEditorContentLength(experience['description']) < 30) {
        experienceErrors['description'] = {
          key: 'errors.descriptionTooShort',
          options: {
            field: startCase('description'),
            chars: startCase('30')
          }
        }
        experiencesErrors[index] = experienceErrors
      }

      if (experience['description'] && getEditorContentLength(experience['description']) > lengthLimit.description) {
        experienceErrors['description'] = {
          key: 'errors.charactersLength',
          options: {
            limit: lengthLimit.description
          }
        }
        experiencesErrors[index] = experienceErrors
      }
    })
  }

  if (experiencesErrors.length) {
    errors.experiences = experiencesErrors
  }

  return errors
}



