import { reduxForm } from 'redux-form'
import AddVolunteeringForm from 'Candidate/forms/EditProfileSections/VolunteeringSection/AddVolunteeringForm'
import validate from './AddVolunteeringFormValidator'
import { addExperience } from 'Shared/ducks/experiences'
import { cleanTrailingSpacesFromHtml } from 'Shared/helpers/string'

export const formName = 'AddVolunteeringForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmit: (values, dispatch) => Promise.all(
    values.volunteerings.map(volunteeringValues => {
      const cleanDescription = cleanTrailingSpacesFromHtml(volunteeringValues.description)
      return dispatch(addExperience({ ...volunteeringValues, description: cleanDescription }))
    })
  )
})(AddVolunteeringForm)

export default Form



