import React from 'react'
import Collapse, { Panel } from 'rc-collapse'
import { assessment } from 'Shared/constants/Rating'
import withTranslations from 'Shared/hoc/withTranslations'
import Label from 'Shared/components/FormElements/Label'
import { candidatesImprovementLinks } from 'Shared/constants/Links'
import { isNil, cond, T, pipe, prop, always, propEq } from 'ramda'
import profileRecommendationsImage from 'Shared/assets/images/vectors/profile-recommendations.svg'

import badRatingStar from 'Shared/assets/images/vectors/rating-stars-1.svg'
import averageRatingStar from 'Shared/assets/images/vectors/rating-stars-2.svg'
import goodRatingStar from 'Shared/assets/images/vectors/rating-stars-3.svg'
import './ProfileRating.scss'
import { isNotNil } from 'Shared/helpers/Ramda'

const ProfileRating = ({ t, rating, improvements, isAutoApproved, recommendations }) => {
  const starRatingImage = {
    [assessment.bad]: badRatingStar,
    [assessment.average]: averageRatingStar,
    [assessment.good]: goodRatingStar
  }

  const profileRatingHeader = () => (
    <div className='profile-rating__header-wrapper'>
      <img className='profile-rating__stars' src={starRatingImage[rating]} alt={`Rating ${rating}`} />
      <div>
        <h3 className='profile-rating__header'>{t(`rating.assessment.${rating}.label`)}</h3>
        <div className='profile-rating__header-description'>{t(`rating.assessment.${rating}.description`)}</div>
      </div>
    </div>
  )

  const profileRecommendationHeader = () => (
    <div className='profile-rating__header-wrapper'>
      <img className='profile-rating__stars' src={profileRecommendationsImage} alt={'Profile recommendations'} />
      <div>
        <h3 className='profile-rating__header'>{t(`rating.autoApproved.header`)}</h3>
        <div className='profile-rating__header-description'>{t(`rating.autoApproved.description`)}</div>
      </div>
    </div>
  )

  const AssessmentCriteriaElement = ({ title, subtitle, description }) => (
    <div className='column col-6'>
      <div className='profile-rating__assessment-criteria-element'>
        <Label>{title}</Label>
        <div className='profile-rating__assessment-criteria-subtitle'>{subtitle}</div>
        <div className='profile-rating__assessment-criteria-description'>{description}</div>
      </div>
    </div>
  )

  const assessmentCriteriaList = list => {
    return list.map((improvement, index) => (
      <AssessmentCriteriaElement
        key={`assessment-criteria-${index}`}
        title={t(`rating.improvements.${improvement.name}.label`)}
        subtitle={improvement.alert ? t(`rating.${improvement.alert}`) : ''}
        description={<div>
          {t(`rating.improvements.${improvement.name}.description.${improvement.value}`)}
          {
            !isNil(candidatesImprovementLinks[improvement.name]) &&
            <a href={candidatesImprovementLinks[improvement.name]} target='_blank'> {t(`rating.improvements.link.${improvement.value}`)}</a>
          }
        </div>}
      />
    ))
  }

  const profileRatingWithImprovements = () => (
    <div className='profile-rating__wrapper'>
      <Collapse>
        <Panel key={'99'} header={profileRatingHeader()}>
          <div className='profile-rating__content-wrapper'>
            <div className='columns'>
              {assessmentCriteriaList(improvements)}
            </div>
          </div>
        </Panel>
      </Collapse>
    </div>
  )

  const profileRecommendations = () => (
    <div className='profile-rating__wrapper'>
      <Collapse>
        <Panel key={'99'} header={profileRecommendationHeader()}>
          <div className='profile-rating__content-wrapper'>
            <div className='columns'>
              {assessmentCriteriaList(recommendations)}
            </div>
          </div>
        </Panel>
      </Collapse>
    </div>
  )

  const profileRatingWithoutImprovements = () => (
    <div className='profile-rating__wrapper--no-improvements'>
      {profileRatingHeader()}
    </div>
  )

  const getFeedbackComponent = () => improvements.length > 0 ? profileRatingWithImprovements() : profileRatingWithoutImprovements()

  const showComponentOrNull = cond([
    [propEq('isAutoApproved', true), profileRecommendations],
    [pipe(prop('rating'), isNotNil), getFeedbackComponent],
    [T, always(null)]
  ])

  return showComponentOrNull({ isAutoApproved, rating })
}

export default withTranslations(ProfileRating)



