import React from 'react'
import { t } from 'i18next'
import { Form, Field } from 'redux-form'
import { Editor } from 'Shared/components/FormElements'
import { lengthLimit } from 'Shared/constants/Editor'

function AboutSectionForm (props) {
  return (
    <Form onSubmit={props.handleSubmit}>
      <Field
        component={Editor}
        label={t('common.description')}
        name='pitch'
        limit={lengthLimit.pitch}
        placeholder={t('profile.additional.sections.aboutYou.description')}
      />
    </Form>
  )
}

export default AboutSectionForm



