import Modernizr from 'Shared/utils/modernizr'
import { any, values } from 'ramda'
import { cloudinaryImageFormats } from 'Shared/constants/Image'

export const resize = (canvas, cfg) => {
  const defaultResizeConfig = {
    width: 500,
    height: 500
  }

  const config = {
    ...defaultResizeConfig,
    ...cfg
  }

  const resizedCanvas = document.createElement('canvas')
  const resizedCtx = resizedCanvas.getContext('2d')

  resizedCanvas.width = config.width.toString()
  resizedCanvas.height = config.height.toString()

  resizedCtx.beginPath()
  resizedCtx.rect(0, 0, config.width, config.height)
  resizedCtx.fillStyle = 'white'
  resizedCtx.fill()

  resizedCtx.drawImage(canvas, 0, 0, config.width, config.height)

  return resizedCanvas
}

export const canvasToDataURL = (canvas, compression = 0.8) => {
  return canvas.toDataURL('image/jpeg', compression)
}

export const getCloudinaryImage = (path) => {
  const { jpeg2000, jpegxr, webp } = Modernizr
  const allowsWebp = typeof webp === 'object' ? !(any(value => value === false, values(webp))) : webp

  switch (true) {
    case jpeg2000:
      return `https://res.cloudinary.com/oliv/image/upload/v1669707575/${cloudinaryImageFormats.jpeg2000}/${path}`
    case jpegxr:
      return `https://res.cloudinary.com/oliv/image/upload/v1669707575/${cloudinaryImageFormats.jpegxr}/${path}`
    case allowsWebp:
      return `https://res.cloudinary.com/oliv/image/upload/v1669707575/${cloudinaryImageFormats.webp}/${path}`
    default:
      return `https://res.cloudinary.com/oliv/image/upload/v1669707575/${path}`
  }
}



