import { connect } from 'react-redux'
import ProfilePreviewEducationUniversityElement from 'Candidate/components/ProfilePreviewSections/ProfilePreviewEducation/components/ProfilePreviewEducationUniversityElement'
import {
  getUniversityNameById,
  getEducationDescription,
  getEducationGraduateDate,
  getEducationGradingInfo,
  getEducationNormalizedGrade,
  getEducationDetails,
  hasOneUniversityEducation,
  getUniversityLogoById
} from 'Shared/selectors/education'
import { deleteEducationDetailsById } from 'Shared/ducks/education'
import { t } from 'i18next'

const mapStateToProps = (state, ownProps) => ({
  logoUrl: getUniversityLogoById(state, ownProps.id),
  title: getUniversityNameById(state, ownProps.id),
  description: getEducationDescription(state, ownProps.id),
  graduationDate: `${t('common.graduationDate')}: ` + getEducationGraduateDate(state, ownProps.id),
  gradingInfo: getEducationGradingInfo(state, ownProps.id),
  normalizedGrade: `${t('profile.education.normalizedGrade.title')}: ` + getEducationNormalizedGrade(state, ownProps.id),
  educationDetails: getEducationDetails(state, ownProps.id),
  hasOneUniversityEducation: hasOneUniversityEducation(state)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  deleteEducationDetailsById: () => dispatch(deleteEducationDetailsById(ownProps.id))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewEducationUniversityElement)



