import { connect } from 'react-redux'
import ProfileRating from 'Candidate/components/ProfileRating'
import RatingService from 'Shared/services/RatingService'
import { isCandidateProfileAutoApproved } from 'Shared/selectors/user'
import { recommendationImprovements } from 'Shared/constants/Rating'

const mapStateToProps = state => {
  const ratingService = new RatingService(state.user.details.assessmentRating, state.user.details.assessmentPoints)

  return ({
    rating: ratingService.overallAssessment,
    improvements: ratingService.improvements,
    recommendations: recommendationImprovements,
    isAutoApproved: isCandidateProfileAutoApproved(state)
  })
}

export default connect(mapStateToProps)(ProfileRating)



