import { reduxForm, formValueSelector } from 'redux-form'
import { connect } from 'react-redux'
import { linkTypes } from 'Shared/constants/Links'
import AddSocialLinksForm from 'Candidate/forms/EditProfileSections/WebsitesSection/AddSocialLinksForm'
import { getCandidateLinkType } from 'Shared/selectors/links'
import validate from './AddSocialLinksFormValidator'
import {
  deleteCandidateLink,
  updateCandidateSocialLinks
} from 'Shared/ducks/links'

export const formName = 'AddSocialLinksForm'

let Form = reduxForm({
  form: formName,
  validate,
  enableReinitialize: true,
  onSubmit: (values, dispatch) => dispatch(updateCandidateSocialLinks(values))
})(AddSocialLinksForm)

const mapStateToProps = state => {
  const candidateId = state.user.details.id
  const linkedin = getCandidateLinkType(candidateId, linkTypes.linkedin)(state)
  const instagram = getCandidateLinkType(candidateId, linkTypes.instagram)(state)
  const twitter = getCandidateLinkType(candidateId, linkTypes.twitter)(state)

  return ({
    linkId: field => formValueSelector(formName)(state, field),
    initialValues: {
      linkedin: linkedin.url,
      linkedinId: linkedin.id,
      instagram: instagram.url,
      instagramId: instagram.id,
      twitter: twitter.url,
      twitterId: twitter.id
    }
  })
}

const mapDispatchToProps = dispatch => ({
  deleteLink: linkId => dispatch(deleteCandidateLink(linkId))
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



