import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import { fetchInterests, updateCandidateInterests } from 'Shared/ducks/interests'
import FormUtilsService from 'Shared/services/FormUtilsService'
import { transformToArray } from 'Shared/helpers/redux'
import InterestsForm from 'Candidate/forms/Profile/InterestsForm'

export const formName = 'CompleteProfile-Interests'

const formUtilsService = new FormUtilsService()

let Form = reduxForm({
  form: formName,
  destroyOnUnmount: true,
  enableReinitialize: true
})(InterestsForm)

const mapStateToProps = state => ({
  interests: formUtilsService.mapOptionsByLanguageTranslations(transformToArray(state.interests.entries)),
  initialValues: {
    interests: formUtilsService.mapOptionsByLanguageTranslations(transformToArray(state.interests.candidate))
  }
})

const mapDispatchToProps = dispatch => ({
  fetchInterests: () => dispatch(fetchInterests()),
  submitForm: (values) => dispatch(updateCandidateInterests(values.interests))
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



