import React, { useState } from 'react'
import Label from 'Shared/components/FormElements/Label'
import { t } from 'i18next'
import cx from 'classnames'
import { ifElse, always, equals, not } from 'ramda'
import AvailabilityModalConnector from 'Candidate/connectors/AvailabilityModalConnector'
import { BigJustIconButton } from 'Shared/components/Button'
import './ProfilePreviewAvailability.scss'

function ProfilePreviewAvailability (props) {
  const [editModalVisibility, setEditModalVisibility] = useState(false)

  const openEditModal = () => setEditModalVisibility(true)
  const hideEditModal = () => setEditModalVisibility(false)

  const label = ifElse(
    equals(true),
    always(t('common.availability')),
    always(t('common.availabilityExpired'))
  )(props.isAvailable)

  const content = ifElse(
    equals(true),
    always(props.userAvailability),
    always(t('common.updateAvailability'))
  )(props.isAvailable)

  const availabilityContainerClass = cx({
    'profile-preview-availability': true,
    'profile-preview-availability--expired': not(props.isAvailable)
  })

  return (
    <div className={availabilityContainerClass}>
      <div className='profile-preview-availability__content-container'>
        <i className='icon-im-calendar profile-preview-availability__icon' />
        <div className='profile-preview-availability__content'>
          <Label>{label}</Label>
          <div className='profile-preview-availability__text'>{content}</div>
        </div>
      </div>
      <BigJustIconButton className='profile-preview-availability__edit-button' onClick={openEditModal}><i className='icon-im-edit' /></BigJustIconButton>
      <AvailabilityModalConnector
        show={editModalVisibility}
        onClose={hideEditModal}
      />
    </div>
  )
}

export default ProfilePreviewAvailability



