import React, { useState, useEffect } from 'react'
import { t } from 'i18next'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton/index'
import Collapse, { Panel } from 'rc-collapse'
import cx from 'classnames'
import LanguagesFormFieldsConnector from 'Candidate/connectors/Forms/EditProfileSections/LanguagesSection/LanguagesFormFieldsConnector'
import 'Shared/assets/scss/vendor/rc-collapse-form.scss'

function LanguagesFormFieldArray (props) {
  const [accordionActiveKey, setAccordionActiveKey] = useState(null)

  const addNewField = () => {
    props.fields.push({
      candidateId: props.userId,
      id: null,
      languageCode: null,
      speaking: null,
      writing: null,
      reading: null
    })
  }

  useEffect(() => {
    addNewField()
  }, [])

  useEffect(() => {
    if (props.fields.length > 0) {
      setAccordionActiveKey(props.fields.length.toString())
    }
  }, [props.fields.length])

  const removeField = index => () => {
    const { fields, onDelete } = props
    const itemId = fields.get(index).id
    return itemId ? onDelete(itemId) : fields.remove(index)
  }

  const LanguageFormField = (language, index) => {
    const PanelClass = cx({
      'form-fields__collapse-panel-error': props.isFieldArrayInvalid(language)
    })

    return (
      <Panel className={PanelClass} key={index + 1} header={props.getLanguageName(language)}>
        <LanguagesFormFieldsConnector
          removeField={removeField(index)}
          formName={props.formName}
          language={language}
          index={index}
        />
      </Panel>
    )
  }

  const getFields = () => props.fields.map(LanguageFormField)

  return (
    <div>
      <Collapse
        accordion
        className='form-fields__collapse'
        activeKey={accordionActiveKey}
        onChange={setAccordionActiveKey}
      >
        {getFields()}
      </Collapse>
      <AddNewSectionButton
        text={t('candidate.profile.language.addLanguage')}
        onClick={addNewField}
      />
    </div>
  )
}

export default LanguagesFormFieldArray



