import React from 'react'
import ProfilePreviewAboutWeb from './ProfilePreviewAbout'
import ProfilePreviewAboutMobile from './ProfilePreviewAbout.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewAbout = props => props.isMobile ? <ProfilePreviewAboutMobile {...props} /> : <ProfilePreviewAboutWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewAbout)



