import startCase from 'lodash/startCase'
import { getEditorContentLength } from 'Shared/helpers/editor'
import { lengthLimit } from 'Shared/constants/Editor'

export default values => {
  const errors = {}

  if (!values.pitch) {
    errors.pitch = {
      key: 'errors.descriptionIsARequiredField',
      options: {}
    }
  }

  if (values['pitch'] && getEditorContentLength(values['pitch']) < 30) {
    errors['pitch'] = {
      key: 'errors.descriptionTooShort',
      options: {
        field: startCase('pitch'),
        chars: startCase('30')
      }
    }
  }

  if (values['pitch'] && getEditorContentLength(values['pitch']) > lengthLimit.pitch) {
    errors['pitch'] = {
      key: 'errors.charactersLength',
      options: {
        limit: lengthLimit.pitch
      }
    }
  }

  return errors
}



