import React from 'react'
import ProfilePreviewEducationUniversityElementConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewEducationUniversityElementConnector'
import * as R from 'ramda'
import './ProfilePreviewEducationUniversities.scss'

const renderUniversityElement = education => <ProfilePreviewEducationUniversityElementConnector id={education.id} key={education.id} />

const UniversitiesList = (props) => R.map(
  renderUniversityElement
)(props.educationList)

function ProfilePreviewEducationUniversities (props) {
  return (
    <div className='columns'>
      <UniversitiesList educationList={props.educationList} />
    </div>
  )
}

export default ProfilePreviewEducationUniversities



