import React, { useState, useEffect } from 'react'
import { t } from 'i18next'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton/index'
import Collapse, { Panel } from 'rc-collapse'
import cx from 'classnames'
import CertificatesFormFieldsConnector from 'Candidate/connectors/Forms/EditProfileSections/CertificatesSection/CertificatesFormFieldsConnector'
import 'Shared/assets/scss/vendor/rc-collapse-form.scss'

function CertificatesFormUniversitiesFieldArray (props) {
  const [accordionActiveKey, setAccordionActiveKey] = useState(null)

  const addNewField = () => {
    props.fields.push({
      id: null,
      name: null,
      issuer: null,
      issuedAt: null
    })
  }

  useEffect(() => {
    addNewField()
  }, [])

  useEffect(() => {
    if (props.fields.length > 0) {
      setAccordionActiveKey(props.fields.length.toString())
    }
  }, [props.fields.length])

  const removeField = index => () => {
    const { fields, onDelete } = props
    const itemId = fields.get(index).id
    return itemId ? onDelete(itemId) : fields.remove(index)
  }

  const CertificateFormUniversitiesField = (certificate, index) => {
    const PanelClass = cx({
      'form-fields__collapse-panel-error': props.isFieldArrayInvalid(certificate)
    })

    return (
      <Panel className={PanelClass} key={index + 1} header={props.getCertificateName(certificate)}>
        <CertificatesFormFieldsConnector
          removeField={removeField(index)}
          formName={props.formName}
          certificate={certificate}
        />
      </Panel>
    )
  }

  const getFields = () => props.fields.map(CertificateFormUniversitiesField)

  return (
    <div>
      <Collapse
        accordion
        className='form-fields__collapse'
        activeKey={accordionActiveKey}
        onChange={setAccordionActiveKey}
      >
        {getFields()}
      </Collapse>
      <AddNewSectionButton
        text={t('profile.education.sections.additionalCertifications.addNew')}
        onClick={addNewField}
      />
    </div>
  )
}

export default CertificatesFormUniversitiesFieldArray



