import React from 'react'
import ProfilePreviewWebsitesElementConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewWebsitesElementConnector'
import * as R from 'ramda'

const renderWebsiteElement = website => <ProfilePreviewWebsitesElementConnector id={website.id} key={website.id} />

const WebsitesList = (props) => R.map(
  renderWebsiteElement
)(props.websites)

function ProfilePreviewWebsitesList (props) {
  return (
    <div>
      <WebsitesList websites={props.websites} />
    </div>
  )
}

export default ProfilePreviewWebsitesList



