import { connect } from 'react-redux'
import { formValueSelector } from 'redux-form'
import { isFieldInvalid } from 'Shared/selectors/reduxForm'
import UniversitiesFormFieldArray from 'Candidate/forms/EditProfileSections/EducationSection/UniversitiesFormFieldArray'
import { getUniversityNameOrLabel } from 'Shared/selectors/universities'

const mapStateToProps = (state, ownProps) => ({
  getUniversityName: id => getUniversityNameOrLabel(state, id),
  getUniversityId: universityField => formValueSelector(ownProps.formName)(state, `${universityField}.universityId`),
  isFieldArrayInvalid: universityField => isFieldInvalid(ownProps.formName, universityField)(state)
})

export default connect(mapStateToProps)(UniversitiesFormFieldArray)



