import React from 'react'
import { Form, FieldArray } from 'redux-form'
import LanguagesFormFieldArrayConnector from 'Candidate/connectors/Forms/EditProfileSections/LanguagesSection/LanguagesFormFieldArrayConnector'

function AddLanguagesForm (props) {
  return (
    <Form onSubmit={props.handleSubmit}>
      <div className='form__group'>
        <FieldArray name='languages' formName={props.form} component={LanguagesFormFieldArrayConnector} />
      </div>
    </Form>
  )
}

export default AddLanguagesForm



