import React, { useState, useEffect } from 'react'
import { t } from 'i18next'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton/index'
import Collapse, { Panel } from 'rc-collapse'
import cx from 'classnames'
import ExperienceFormFieldsConnector from 'Candidate/connectors/Forms/EditProfileSections/ExperienceSection/ExperienceFormFieldsConnector'
import 'Shared/assets/scss/vendor/rc-collapse-form.scss'
import { experienceTypes } from 'Shared/constants/Experience'

function CertificatesFormUniversitiesFieldArray (props) {
  const [accordionActiveKey, setAccordionActiveKey] = useState(null)

  const addNewField = () => {
    props.fields.push({
      candidateId: props.userId,
      type: experienceTypes.employment,
      organisationName: null,
      description: null,
      currentWork: null,
      finishedAt: null,
      startedAt: null,
      position: null
    })
  }

  useEffect(() => {
    addNewField()
  }, [])

  useEffect(() => {
    if (props.fields.length > 0) {
      setAccordionActiveKey(props.fields.length.toString())
    }
  }, [props.fields.length])

  const removeField = index => () => {
    const { fields, onDelete } = props
    const itemId = fields.get(index).id
    return itemId ? onDelete(itemId) : fields.remove(index)
  }

  const ExperienceFormField = (experience, index) => {
    const PanelClass = cx({
      'form-fields__collapse-panel-error': props.isFieldArrayInvalid(experience)
    })

    return (
      <Panel className={PanelClass} key={index + 1} header={props.getOrganisationName(experience)}>
        <ExperienceFormFieldsConnector
          removeField={removeField(index)}
          formName={props.formName}
          experience={experience}
        />
      </Panel>
    )
  }

  const getFields = () => props.fields.map(ExperienceFormField)

  return (
    <div>
      <Collapse
        accordion
        className='form-fields__collapse'
        activeKey={accordionActiveKey}
        onChange={setAccordionActiveKey}
      >
        {getFields()}
      </Collapse>
      <AddNewSectionButton
        text={t('profile.experience.sections.workExperience.addNew')}
        onClick={addNewField}
      />
    </div>
  )
}

export default CertificatesFormUniversitiesFieldArray



