import React, { useState, useEffect } from 'react'
import Label from 'Shared/components/FormElements/Label'
import { SmallJustIconButton } from 'Shared/components/Button'
import DeleteModal from 'Shared/components/DeleteModal'
import EditSingleWebsiteFormConnector from 'Candidate/connectors/Forms/EditProfileSections/WebsitesSection/EditSingleWebsiteFormConnector'
import './ProfilePreviewWebsitesElement.scss'
import { t } from 'i18next'
import NotificationService from 'Shared/services/NotificationService'

function ProfilePreviewWebsitesElement (props) {
  const [editModeVisibility, setEditModeVisibility] = useState(false)
  const [deleteModalVisibility, setDeleteModalVisibility] = useState(false)

  const openEditMode = () => setEditModeVisibility(true)
  const hideEditMode = () => setEditModeVisibility(false)

  const openDeleteModal = () => setDeleteModalVisibility(true)
  const hideDeleteModal = () => setDeleteModalVisibility(false)

  const handleDelete = () => {
    props.deleteWebsite()
    hideDeleteModal()
  }

  useEffect(() => {
    if (props.hasUpdateSucceeded) {
      NotificationService.success(t('alerts.updateWebsitesSuccess'))
    }
  })

  return editModeVisibility ? (
    <div className='profile-preview-websites-mobile__element-edit-mode'>
      <EditSingleWebsiteFormConnector afterSubmit={hideEditMode} website={props.website} onDelete={openDeleteModal} />
      <DeleteModal
        title={t('remove.websites.title')}
        content={t('remove.websites.description')}
        handleDelete={handleDelete}
        isModalVisible={deleteModalVisibility}
        hideDeleteModal={hideDeleteModal}
      />
    </div>
  ) : (
    <div className='profile-preview-websites__element'>
      <div className='profile-preview-websites__element-details'>
        <Label>{t(`profile.additional.sections.websites.websiteTypes.${props.website.type}`)}</Label>
        <a href={props.website.url} target='_blank' className='profile-preview-websites__element-title'>{props.website.url}</a>
      </div>
      <div className='profile-preview-websites__element-buttons'>
        <SmallJustIconButton onClick={openEditMode}><i className='icon-im-edit' /></SmallJustIconButton>
      </div>
    </div>
  )
}

export default ProfilePreviewWebsitesElement



