import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import ProfilePreviewSkills from 'Candidate/components/ProfilePreviewSections/ProfilePreviewSkills'
import { getUserSkills } from 'Shared/selectors/skills'
import { formName } from 'Candidate/connectors/Forms/Profile/Experience/SkillsFormConnector'

const mapStateToProps = state => ({
  skills: getUserSkills(state),
  editSkillsSucceeded: hasSubmitSucceeded(formName)(state)
})

const mapDispatchToProps = dispatch => ({
  submitEditSkills: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewSkills)



