import React, { useEffect, useState } from 'react'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import ProfilePreviewEducationUniversitiesConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewEducationUniversitiesConnector'
import ProfilePreviewEducationHighSchoolsConnector from 'Candidate/connectors/ProfilePreviewSections/ProfilePreviewEducationHighSchoolsConnector'
import { t } from 'i18next'
import './ProfilePreviewEducation.scss'
import Modal from 'Shared/components/Modal'
import AddNewSectionButton from 'Shared/components/AddNewSectionButton'
import AddEducationFormConnector from 'Candidate/connectors/Forms/EditProfileSections/EducationSection/AddEducationFormConnector'
import * as R from 'ramda'
import NotificationService from 'Shared/services/NotificationService'

function ProfilePreviewEducation (props) {
  const [addModalVisibility, setAddModalVisibility] = useState(false)

  const showAddModal = () => {
    setAddModalVisibility(true)
  }
  const hideAddModal = () => setAddModalVisibility(false)

  useEffect(() => {
    props.fetchEducationResources()
  }, [])

  useEffect(() => {
    if (props.userId && R.isEmpty(props.educationHighSchoolList)) {
      props.fetchCandidateHighSchoolEducation(props.userId)
    }
  }, props.userId)

  useEffect(() => {
    if (props.hasAddEducationSucceeded) {
      hideAddModal()
      NotificationService.success(t('alerts.updateEducationSuccess'))
    }
  })

  const handleSubmitModal = async () => {
    await props.submitAddEducation()
  }

  return (
    <ProfilePreviewSection
      header={t('profile.education.title')}
    >
      { props.isHighSchoolLevelEducation
        ? <ProfilePreviewEducationHighSchoolsConnector />
        : <ProfilePreviewEducationUniversitiesConnector />
      }
      <div className='profile-preview-education'>
        <AddNewSectionButton
          text={t('candidate.profile.education.addEducation')}
          onClick={showAddModal}
        />
        <Modal
          title={t('modals.addEducation.title')}
          show={addModalVisibility}
          handleClose={hideAddModal}
          handleCancel={hideAddModal}
          handleSubmit={handleSubmitModal}
          cancelText={t('common.cancel')}
          submitText={t('common.submit')}
          showContact={false}
          closeOnSubmit={false}
          showCancel
        >
          <AddEducationFormConnector />
        </Modal>
      </div>
    </ProfilePreviewSection>
  )
}

export default ProfilePreviewEducation



