import React from 'react'
import ProfilePreviewSkillsMobile from './ProfilePreviewSkills.mobile'
import ProfilePreviewSkillsWeb from './ProfilePreviewSkills'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewSkills = props => props.isMobile ? <ProfilePreviewSkillsMobile {...props} /> : <ProfilePreviewSkillsWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewSkills)



