import React from 'react'
import ProfilePreviewCertificatesElementMobile from './ProfilePreviewCertificatesElement.mobile'
import ProfilePreviewCertificatesElementWeb from './ProfilePreviewCertificatesElement'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const ProfilePreviewCertificatesElement = props => props.isMobile ? <ProfilePreviewCertificatesElementMobile {...props} /> : <ProfilePreviewCertificatesElementWeb {...props} />

export default withDeviceWidthDetector(ProfilePreviewCertificatesElement)



