import { connect } from 'react-redux'
import ProfilePreviewAdditionalDocuments from 'Candidate/components/ProfilePreviewSections/ProfilePreviewAdditionalDocuments'
import { getCandidateNonCvFiles, getAllCandidateFiles } from 'Shared/selectors/files'
import { uploadFileToS3, generateS3Signature } from 'Shared/ducks/files'
import { UPLOAD_DOCUMENT_TYPES } from 'Shared/constants/Files'

const mapStateToProps = state => ({
  documents: getCandidateNonCvFiles(state),
  allFiles: getAllCandidateFiles()(state),
  isFileLoading: state.files.isLoading
})

const mapDispatchToProps = dispatch => ({
  generateSignature: () => dispatch(generateS3Signature()),
  uploadFile: (file, policy, signature) => dispatch(uploadFileToS3(file, policy, signature, UPLOAD_DOCUMENT_TYPES.PROJECT))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePreviewAdditionalDocuments)



