import { connect } from 'react-redux'
import ExperienceFormFields from 'Candidate/forms/EditProfileSections/ExperienceSection/ExperienceFormFields'
import { change, formValueSelector } from 'redux-form'

const mapStateToProps = (state, ownProps) => ({
  currentWork: formValueSelector(ownProps.formName)(state, `${ownProps.experience}.currentWork`)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  onCurrentWorkChange: () => dispatch(change(ownProps.formName, `${ownProps.experience}.finishedAt`, null))
})

export default connect(mapStateToProps, mapDispatchToProps)(ExperienceFormFields)



